
import {
  defineComponent, ref, onMounted, onBeforeUnmount, computed,
} from '@vue/composition-api';
import DragSelect from '@/components/DragSelect.vue';
import { commands } from '@/utils/catalog';
import batchBinder, { BatchBinderStop } from '_/batch-binder';
import draggingData from '@/utils/dragging-data';
import clipboard from '@/utils/clipboard';
import CatalogModel, {
  CatalogInfoMaterial, CatalogMaterial, CatalogFile, CatalogModalTagMap,
} from '@/models/Catalog';
import { stop } from '_/jsx-utils';
import useClosest from '_/composables/use-closest';
import { getHost } from '@/utils/exec';
import isImage from '_/is-image';
import ConvertToAnta from '@/views/material/info-anta/components/ConvertToAnta.vue';
import { i18n } from '@/i18n';
import Intersect from 'vue-intersect';

import { delay } from '_/delay';
import { validFiles } from '../../../../utils/validators/valid-file';
import PanelItem from './Item.vue';
import Catalog from './index.vue';

// import delay from '../../../../utils/delay';
const imageType2ImageKey = {
  drape: 'thumbnail' as 'thumbnail',
  flat: 'flat' as 'flat',
  texture: 'texture' as 'texture',
  default: 'texture' as 'texture' | 'thumbnail',
};
function isMaterial(item: CatalogModel): item is CatalogModel<CatalogInfoMaterial> {
  return item.fileType === 1 && item.relateType === 1;
}

const useActiveElement = () => {
  const refActiveElement = ref<Element | null>(null);
  const switchElement = async () => {
    await delay(0);
    const newElement = document.activeElement;
    const oldElement = refActiveElement.value;
    if (newElement !== oldElement) {
      refActiveElement.value = newElement;
    }
  };
  onMounted(async () => {
    document.addEventListener('focus', switchElement, true);
    document.addEventListener('blur', switchElement, true);
    await switchElement();
  });
  onBeforeUnmount(() => {
    document.removeEventListener('focus', switchElement, true);
    document.removeEventListener('blur', switchElement, true);
  });
  return refActiveElement;
};

const PanelList = defineComponent({
  name: 'PanelList',
  components: {
    PanelItem,
    DragSelect,
    ConvertToAnta,
  },
  props: {
    isSearch: Boolean,
    backgroundColor: {
      type: String,
    },
    itemSize: {
      type: Object,
      required: true,
      default() {
        return {
          folder: {
            defaultWidth: 220,
            scale: 1,
          },
          file: {
            defaultWidth: 220,
            scale: 1,
          },
          binaryFile: {
            defaultWidth: 220,
            scale: 1,
          },
          material: {
            defaultWidth: 220,
            scale: 1,
          },
        };
      },
    },
    viewType: {
      type: String,
      required: true,
    },
    shownInfo: {
      type: Boolean,
      required: true,
    },
    tagMap: {
      type: Object as () => CatalogModalTagMap,
    },
  },
  setup(props, {
    root: {
      $openContextMenu, $ImageViewer, $errorHandler, $injectDialog,
    },
  }) {
    let multi = false;

    const catalog = useClosest<typeof Catalog, true>('Catalog');
    const fileList = useClosest('FileList');

    const panelItem = ref<PanelItem[]>([]);
    const showFlagGlass = async ({ item, imageType }:
      { item: CatalogModel, imageType: keyof typeof imageType2ImageKey }) => {
      type VMCatalogMaterial = Omit<PanelItem, 'item'> & { item: CatalogMaterial };
      type VMCatalogFile = Omit<PanelItem, 'item'> & { item: CatalogFile };
      let vmItems: PanelItem[] = panelItem.value;
      let items: CatalogModel[] = panelItem.value.map(({ item: _item }) => _item);
      let srcList: string[] = [];
      if (item.relateType === 1) {
        const vmMaterialCatalogs = panelItem.value.filter(({ item: e }) => {
          if (imageType === 'default') {
            return isMaterial(e) && e.fileInfo && (!!e.fileInfo.texture || !!e.fileInfo.thumbnail);
          }
          return isMaterial(e) && e.fileInfo && !!e.fileInfo[imageType2ImageKey[imageType]];
        }) as VMCatalogMaterial[];
        const materialCatalogs = vmMaterialCatalogs.map(({ item: e }) => e);
        vmItems = vmMaterialCatalogs;
        items = materialCatalogs;
        srcList = materialCatalogs.map(({ fileInfo }) => {
          if (imageType === 'default') {
            return fileInfo.texture || fileInfo.thumbnail!;
          }
          return fileInfo[imageType2ImageKey[imageType]]!;
        }).map(src => ({
          src,
          rulerSetting: {
            rulerHeight: 100,
            dpi: 520.2,
          },
        }));
      } else if (item.relateType === 2) {
        const vmImageCatalogs = (panelItem.value.filter(({ item: e }) => {
          return e.relateType === 2;
        }) as VMCatalogFile[]).filter(({ item: e }) => {
          return !!e.fileInfo.downloadPath && isImage(e.fileInfo.downloadPath);
        });
        const imageCatalogs = vmImageCatalogs.map(({ item: e }) => e);
        vmItems = vmImageCatalogs;
        items = imageCatalogs;
        srcList = imageCatalogs.map(({ fileInfo: { downloadPath } }) => downloadPath!);
      }
      if (srcList.length) {
        const targetElems = vmItems.map(({ $el }) => $el.querySelector('.img-container')!);
        const index = items.indexOf(item);
        await $ImageViewer.show(srcList, {
          index,
          targetElems,
        });
      }
    };

    const handleOpenBackgroundContextMenu = async (e: Event) => {
      const vCatalog = catalog.value;
      const activeFolder = vCatalog.activeFolder!;
      const refresh = () => vCatalog.refresh();
      // 非搜索结果页，显示右键菜单
      if (!vCatalog.catalogPaging?.isSearch && activeFolder) {
        console.log(activeFolder);
        await $openContextMenu(e, {
          data: activeFolder,
          type: 'catalog',
        }, {
          addShortcut: false,
          toggleHide: false,
          copy: false,
          cut: false,
          rename: false,
          remove: false,
          share: false,
          moveTo: false,
          uploadMaterial: refresh,
          recommendMaterial: false,
          recommendMaterialDelete: false,
          submitReview: false,
          // uploadModel: refresh,
          // uploadMaterial: refresh,
          uploadMaterialBaseOn: refresh,
          uploadImage: refresh,
          uploadPDF: refresh,
          upload: refresh,
          createFolder: refresh,
          paste: activeFolder.entryAppType === 16 ? false : refresh,
          refresh,
        });
      }
    };
    const handleOpenContextMenu = async (e: Event, fileObj: CatalogModel) => {
      const vCatalog = catalog.value;
      const { selectedItems } = vCatalog;
      const refresh = () => vCatalog.refresh();
      const remove = () => vCatalog.remove(selectedItems);
      const isSelected = selectedItems.includes(fileObj);
      if (multi) {
        vCatalog.toggleSelection({
          targets: [fileObj],
          toggle: true,
        });
      } else if (!isSelected) {
        vCatalog.cancelSelection();
        vCatalog.toggleSelection({
          targets: [fileObj],
        });
      }
      if (selectedItems.length) {
        await $openContextMenu(e, {
          data: selectedItems,
          type: 'catalog',
        }, {
          createFolder: false,
          uploadMaterial: false,
          uploadModel: false,
          paste: false,
          uploadMaterialBaseOn: refresh,
          upload: false,
          uploadImage: false,
          uploadPDF: false,
          uploadMaterial4Ddat: refresh,
          // replaceMaterial4Ddat: false,
          // rename: async ({ datum, name }) => {
          //   datum.name = name;
          //   await refresh();
          // },
          rename: refresh,
          convertToWsMaterial: async ({ name, relateId }) => {
            // return console.info(target);
            await $injectDialog({
              is: ConvertToAnta,
              props: {
                materialName: name,
                id: relateId,
              },
            });
            await refresh();
          },
          remove,
          moveTo: refresh,
          refresh: false,
          submitReview: refresh,
          shareToIm: (ids) => {
            (fileList.value as unknown as any).shareToIm(ids);
          },
          recommendMaterial: refresh,
          recommendMaterialDelete: refresh,
        });
      }
    };
    const handleDragSelect = (data: PanelItem[], __: any, ___: any, data1: PanelItem[]) => {
      const vCatalog = catalog.value;
      if (multi) {
        vCatalog.toggleSelection({ targets: data1.map(each => each.item) });
      } else {
        vCatalog.cancelSelection();
        vCatalog.toggleSelection({ targets: data.map(each => each.item), toggle: true });
      }
    };
    const handleClickSelect = (target: CatalogModel) => {
      const vCatalog = catalog.value;
      if (multi) {
        vCatalog.toggleSelection({ targets: [target] });
      } else {
        vCatalog.cancelSelection();
        vCatalog.toggleSelection({ targets: [target], toggle: true });
      }
    };
    const handleDrag = (target: CatalogModel, e: DragEvent) => {
      const vCatalog = catalog.value;
      if (!vCatalog.isSelected(target)) {
        handleClickSelect(target);
      }
      draggingData.set({ type: 'catalog', data: vCatalog.selectedItems, command: 'cut' });
      // 新增dataTransfer的方式存储数据,先不动原先draggingData的逻辑,怕遗改功能,后续修改. 比如isDragging的判断跟draggingData挂勾
      e.dataTransfer?.setData('ideationDragData', JSON.stringify({
        type: 'catalog',
        data: vCatalog.selectedItems,
        command: 'cut',
        workspaceId: vCatalog.workspaceId,
        activeFolder: vCatalog.activeFolder,
        entryAppType: vCatalog.entryAppType,
      }));
    };
    const handleDrop = async (target: CatalogModel, e: DragEvent) => {
      const vCatalog = catalog.value;
      // const rawData = e.dataTransfer?.getData('ideationDragData');
      // if (!rawData) { return; }
      // const _draggingData = JSON.parse(rawData) as { type: string, command: string, data: CatalogModel[] };
      // if (_draggingData.data.find(item => item.id === target.id)) {
      //   return;
      // }
      // await commands.paste.run({
      //   data: target,
      // }, _draggingData);
      // vCatalog.refresh();

      if (!draggingData.contains(target)) {
        await commands.paste.run({
          data: target,
        }, draggingData);
        vCatalog.refresh();
      }
    };
    const handleRefresh = async () => {
      const vCatalog = catalog.value;
      const refresh = vCatalog.refresh.bind(vCatalog);
      await refresh();
    };
    const handleRename = async (item: CatalogModel) => {
      await commands.rename.validate({ data: item });
      const { name } = await commands.rename.fn({ data: item });
      item.name = name;
      handleRefresh();
    };
    async function handleDropUpload(event: DragEvent) {
      if (event.dataTransfer) {
        const files = Array.from(event.dataTransfer.files);
        const vCatalog = catalog.value;
        const { activeFolder } = vCatalog;
        if (files.length && activeFolder) {
          validFiles(files);
          await commands.upload.run({ data: activeFolder }, files);
          vCatalog.refresh();
        }
      }
    }
    // const panelList = ref<InstanceType<typeof PanelList>>(null);
    let binder: BatchBinderStop | undefined;
    let _nativeEventBinder: any;

    const activeElement = useActiveElement();
    const isFocus = computed(() => activeElement.value === document.body);

    onMounted(async () => {
      const vCatalog = catalog.value;
      const nativeHost = await getHost();
      if (nativeHost && !_nativeEventBinder) {
        _nativeEventBinder = nativeHost.on({
          downloadProcess: ({ relateId, ...mutations }: { relateId: String }) => {
            vCatalog.catalogs.forEach(item => {
              if (item.relateId === relateId) {
                Object.assign(item.hostData, mutations);
              }
            });
          },
          downloadFail: ({ relateId, ...mutations }: { relateId: String }) => {
            vCatalog.catalogs.forEach(item => {
              if (item.relateId === relateId) {
                Object.assign(item.hostData, mutations);
              }
            });
          },
          downloadFinish: ({ relateId, ...mutations }: { relateId: String }) => {
            vCatalog.catalogs.forEach(item => {
              if (item.relateId === relateId) {
                Object.assign(item.hostData, mutations);
              }
            });
          },
        });
      }

      const refresh = vCatalog.refresh.bind(vCatalog);
      let downEvent: KeyboardEvent | undefined;

      if (!binder) {
        binder = batchBinder([
          {
            target: document.body,
            eventType: 'keydown',
            handler: async e => {
              if (document.body !== document.activeElement) return;
              if (downEvent
                && downEvent.ctrlKey === e.ctrlKey
                && downEvent.metaKey === e.metaKey
                && downEvent.keyCode === e.keyCode) {
                return;
              }
              document.body.addEventListener('keyup', () => {
                downEvent = undefined;
              });
              downEvent = e;
              // if (e.repeat) return;
              const { activeFolder, selectedItems } = vCatalog;
              try {
                const isCtrl = e.ctrlKey || e.metaKey;
                // 删除
                if ((isCtrl && e.keyCode === 68) || e.keyCode === 46) {
                  if (selectedItems.length) {
                    e.preventDefault();
                    // console.log('delete!?');
                    await commands.remove.run({ data: selectedItems });
                    await refresh();
                  }
                }
                if (!isCtrl) {
                  return;
                }
                if (e.keyCode === 65) {
                  // 全选
                  e.preventDefault();
                  vCatalog.selectAll();
                } else if (e.keyCode === 86) {
                  // 粘贴
                  if (clipboard && clipboard.type === 'catalog' && clipboard.data) {
                    e.preventDefault();
                    await commands.paste.run({ data: activeFolder! });
                    await refresh();
                  }
                } else if (e.keyCode === 88) {
                  // 剪切
                  if (selectedItems.length) {
                    e.preventDefault();
                    await commands.cut.run({ data: selectedItems });
                  }
                } else if (e.keyCode === 67) {
                  // 复制
                  if (selectedItems.length) {
                    e.preventDefault();
                    await commands.copy.run({ data: selectedItems });
                  }
                }
              } catch (error) {
                $errorHandler(error);
              }
            },
          },
          {
            target: document.body,
            eventType: 'keydown',
            handler: async e => {
              if (e.ctrlKey || e.metaKey) {
                multi = true;
              }
            },
          },
          {
            target: document.body,
            eventType: 'keyup',
            handler: async e => {
              if (
                e.ctrlKey || e.metaKey
                || e.keyCode === 17 || e.keyCode === 91 || e.keyCode === 93) {
                multi = false;
              }
            },
          },
        ]);
      }
    });
    onBeforeUnmount(async () => {
      // TODO: 不能去掉
      await getHost();
      if (binder) {
        binder();
        binder = undefined;
      }
      if (_nativeEventBinder) {
        _nativeEventBinder();
        _nativeEventBinder = undefined;
      }
    });
    return {
      noop: () => { },
      isFocus,
      panelItem,
      catalog,
      handleRename,
      showFlagGlass,
      handleDrag,
      handleDrop,
      handleRefresh,
      handleOpenBackgroundContextMenu,
      handleOpenContextMenu,
      handleDragSelect,
      handleClickSelect,
      handleDropUpload,
    };
  },
  render() {
    const self = this;
    const {
      catalog,
      itemSize,
      viewType,
      shownInfo,
      handleRename,
      handleClickSelect,
      handleDragSelect,
      handleDrag,
      handleDrop,
      handleRefresh,
      handleOpenContextMenu,
      handleOpenBackgroundContextMenu,
      handleDropUpload,
      $scopedSlots,
      $act,
      isFocus,

    } = self;
    const {
      catalogs, canLoadMore, loadMore, loadingPage, tagMap,
    } = catalog;
    const showFlagGlass = (...args: any[]) => {
      $act.call(this, self.showFlagGlass, args);
    };
    let content = null;
    if (catalogs) {
      if (catalogs.length) {
        content = <div class={['panel-list-wrapper scrollable-y', { 'is-focus': isFocus }]}>
          <DragSelect

            // nativeOnDrop={stop.prevent(() => {
            //   debugger;
            // })}
            // nativeOnDragOver={stop.prevent(() => {
            //   debugger;
            // })}

            onClick={() => catalog.cancelSelection()}
            selectorClass="panel-item"
            onSelect={handleDragSelect}
            mode="contact"
            class="panel-list"
            ref="dragSelector">
            {
              catalogs.map((item, index) => {
                const prev = catalogs[index - 1];
                return [
                  (prev && prev.fileType !== item.fileType) ? <div style="height: 30px; width: 100%"></div> : null,
                  <PanelItem
                    class="panel-item"
                    ref="panelItem"
                    refInFor={true}
                    item={item}
                    key={item.id}
                    itemSize={itemSize}
                    viewType={viewType}
                    shownInfo={shownInfo}
                    draggingData={draggingData}
                    isSelected={catalog.isSelected(item)}
                    isCutting={catalog.isCutting(item)}
                    isDragging={catalog.isDragging(item)}
                    isCopying={catalog.isCopying(item)}
                    on={{
                      enterFolder: () => self.$emit('enterFolder', item),
                      rename: handleRename,
                      refresh: handleRefresh,
                      contextmenu: handleOpenContextMenu,
                      click: handleClickSelect,
                      drag: handleDrag,
                      preview: showFlagGlass,
                      drop: (target: CatalogModel, e: DragEvent) => self.$act(handleDrop, [target, e]),
                    }}
                    tagMap={tagMap}
                  />,
                ];
              })

            }
            {
              // <div vLoading={true}
              //  element-loading-background={backgroundColor} style="height: 100px; width: 100%"/>
            }
          </DragSelect>
          {
            canLoadMore ? <Intersect onEnter={loadMore}>
              <div class="load-more">
                {
                  loadingPage && loadingPage > 1 ? [
                    <i class="el-icon-loading" />,
                    i18n.t('material-mall.loading'),
                  ] : i18n.t('material-mall.scroll_to_load')
                }
              </div>
            </Intersect> : (catalogs.length > 0 ? <el-divider>{i18n.t('material-mall.no_more')}</el-divider> : null)

          }
        </div>;
      } else if ($scopedSlots.empty) {
        content = $scopedSlots.empty(null);
      }
    }
    return <div class="panel-list"
      ref="panelList"
      on={{
        dragover: stop.prevent(),
        drop: stop.prevent(event => self.$act(handleDropUpload, [event])),
        contextmenu: stop.prevent(event => self.$act(handleOpenBackgroundContextMenu, [event])),
      }}
    // style={{ fontSize: `${itemSize * 100}%` }}
    >
      {content}
    </div>;
  },
});

export default PanelList;
