
import axios from '@/api/axios';
import { defineComponent, ref } from '@vue/composition-api';
import Form from './Form.vue';

export default defineComponent({
  name: 'ConvertToAnta',
  dialogify: {
    props: {
      top: '5vh',
      width: '900px',
      compact: true,
      // closeOnClickModal: false,
      // // compact: true,
    },
    // closeWith: 'material',
  },
  props: {
    materialName: {
      type: String,
      required: true,
    },
    id: {
      type: String,
      required: true,
    },
  },

  setup({ materialName, id }, { emit }) {
    const material = ref<{[key: string]: any}>({
      materialName,
      id,
      extraProperty: {
        season: null,
        material_specification: '',
        available_area: '',
        supplier_price_array: [{
          reference_price_value: '',
          reference_price_unit: 1,
          supplier_name: '',
          cost_value: '',
          cost_unit: 1,
          supplier_status: 1,
          delete_status: 0,
        }],
      },
    });

    const antaForm = ref(null);

    async function save() {
      const data = await antaForm.value?.getFormData();
      await axios({
        method: 'post',
        url: '/anta-material/material/management/transitionMaterialInfo',
        data: {
          ...data,
          id,
        },
      });

      emit('confirm');
    }

    return {
      material,
      antaForm,
      save,
    };
  },

  render() {
    const { material, save } = this;

    return <div>
      <div class="scrollable-y" style="height: 750px;padding: 20px;">
        <Form ref="antaForm" material={material} status="convert"></Form>
      </div>
      <div class="bottom-button" >
        <el-button type="primary" onclick={save}>确定</el-button>
      </div>
    </div>;
  },
});

