/* eslint-disable indent */
<template>
  <div class="container operation-record">
    <header>
      <strong>
        {{ $t('file-list.operation_record')}}
      </strong>
    </header>
    <!-- 操作记录不为空时显示列表 -->
    <div class="list scrollable-y"
      v-if="records && records.length !== 0"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="disabled"
      infinite-scroll-immediate="false">
      <MessageItem :item="item" v-for="item in records" :key="item.id" />
    </div>

    <!-- 操作记录为空时显示空图片 -->
    <error v-else :error="{
      code: 404,
      message: $t('file-list.no_operation_record'),
    }" :img="require('@/assets/img/error/no-record.png')" />
    <div class="footer" v-html="$t('file-list.total_activity_records_count', {
      total,
    })">
    </div>
  </div>
</template>
<script>
import axios from '@/api/axios';
import MessageItem from './OperationRecordItem';

async function load({ limit, cursor, workspaceId }) {
  const { data } = await axios({
    method: 'get',
    url: '/msgcenter/audit/list',
    params: { cursor, limit },
    headers: {
      groupAppId: workspaceId,
    },
  });
  return data;
}
export default {
  name: 'OperationRecord',
  components: {
    MessageItem,
  },
  props: {
    workspaceId: {
      type: String,
    },
    limit: {
      type: Number,
      default: 20,
    },
  },
  data() {
    return {
      loading: false,
      total: undefined,
      cursor: 1,
      current: 1,
      size: undefined,
      /** @type { any[] } */
      records: undefined,
      totalPage: undefined,
      item: {
        groupAppId: '1183950867120828418',
        id: '1291703463763873794',
        operationType: 68,
        params: '',
        time: '2020-08-07 19:51:19',
        user: {
          account: 'harvey.woo@outlook.com',
          // eslint-disable-next-line max-len
          avatar: 'http://dev.4dshoetech.local/datapipe/repository/userImg/1183939321070227457-7659f198cde5ba9d6600ed4e788fe11e.png?token=1268730335355396098:web:cb27ae242bd9dded40aec0c9fe95b9ca',
          id: '1183939321070227457',
          name: '啛',
        },
        userId: '1183939321070227457',
      },
    };
  },
  computed: {
    /** @return { boolean } */
    noMore() {
      return this.totalPage !== undefined && this.current >= this.totalPage;
    },
    /** @return { string } */
    requestParams() {
      // 转成字符串避免重复修改
      return JSON.stringify({
        limit: this.limit,
        cursor: this.cursor,
        workspaceId: this.workspaceId,
      });
    },
    /** @return { boolean } */
    disabled() {
      return this.loading || this.noMore;
    },
  },
  methods: {
    async reload(params) {
      this.loading = true;
      Object.assign(this, await load(params));
      this.loading = false;
    },
    async loadMore() {
      this.loading = true;
      this.current++;
      const {
        total, size, current, records,
      } = await load({
        workspaceId: this.workspaceId,
        cursor: this.current ?? this.cursor,
        limit: this.limit,
      });
      if (!this.records) {
        this.records = records;
      } else {
        this.records.push(...records);
      }
      this.total = total;
      this.totalPage = Math.ceil(this.total / this.limit);
      this.size = size;
      this.current = current;
      this.loading = false;
    },
  },
  watch: {
    cursor(current) {
      this.current = current;
    },
    requestParams: {
      handler(requestParams) {
        this.reload(JSON.parse(requestParams));
      },
      immediate: true,
    },
    workspaceId() {
      this.cursor = 0;
    },
  },
};
</script>

