<template>
  <div>
    <div v-html="$t('file-list.operation-record.template63', {
      operatorName: item.params[0].name || item.params[0].operatorName,
      userName: item.params[1].name || item.params[1].userName,
      roleName: roleIdToRoleName(item.params[2].id) })">
      <!-- <span class="highlight">
        {{ item.params[0].name || item.params[0].operatorName }}
      </span>
      set
      <span class="highlight">
        {{ item.params[1].name || item.params[1].userName }}
      </span>
      to
      <span class="highlight">
        {{ item.params[2].id | roleIdToRoleName }}
      </span>
      role -->
    </div>
    <div class="handle">
    </div>
  </div>
</template>
<script>
import i18n from 'i18next';

export default {
  props: {
    item: {},
  },
  methods: {
    // eslint-disable-next-line
    roleIdToRoleName(typeId) {
      // eslint-disable-next-line
      switch (Number(typeId)) {
        case 4:
          return i18n.t('workspace.owner');
        case 5:
          return i18n.t('workspace.admin');
        case 6:
          return i18n.t('workspace.member');
        case 7:
          return i18n.t('workspace.guest');
        case 8:
          return i18n.t('workspace.invited');
      }
    },
  },
};
</script>
