
import Vue, { VueConstructor } from 'vue';
import {
  defineComponent, ref, computed, watch, nextTick,
} from '@vue/composition-api';
import { ElCascader } from 'element-ui/types/cascader';
// @ts-ignore
import VueTagsInput from '@johmun/vue-tags-input';
import { i18n } from '@/i18n';
import axios from '@/utils/axios';
import { getContextWorkspace } from '@/loaders/context';
import { materialColorPicker } from '@/views/material/form-related';
import keys from '_/keys';
import { envs } from '@/utils/envs';
import { getCurrentCompany } from '@/loaders/company';
import PriceForm from './PriceForm.vue';


const rootSymbol = Symbol('root');
export type Tree = {
  label: string,
  value: unknown,
  code?: string,
  children?: Tree[],
  tagName?: string,
  leaf?: boolean,
  slot?: VueConstructor,
  isSelected?: (value: any) => boolean,
  getChildren?: (this: Tree, options: Tree[], level: number) => Promise<Tree[]> | Tree[]
  [rootSymbol]?: Tree,
};
export type OptionsTree = Tree;
type CascaderOptionFromServer = {
  itemCode: string,
  itemValue: string,
  label: string,
};

async function getPaths(
  // eslint-disable-next-line no-use-before-define
  values: OptionsTree['value'][],
  optionsTree: OptionsTree[],
  getChildren: Function,
) {
  const result = [];
  const _optionsTree = optionsTree;
  for (let i = 0; i < values.length; i++) {
    const value = values[i];
    const findIterator = (option: OptionsTree) => {
      const isSelected = option.isSelected || ((_value: any) => option.value === _value);
      return isSelected(value);
    };
    const option = optionsTree.find(findIterator);
    if (option) {
      if (!option.leaf) {
        optionsTree = await getChildren(option, _optionsTree, i + 1);
      }
      result.push(option);
      if (option.leaf) {
        break;
      }
    }
  }
  return result;
}
const SearchAndSelect = defineComponent({
  name: 'SearchAndSelect',
  props: {
    options: {
      type: Array as () => OptionsTree[],
      // required: true,
    },
    value: {
      type: Object as () => {
        filters: {
          [x: string]: Tree['value'][],
        },
        keyword: string,
      },
      default: () => ({
        filters: {},
        keyword: '',
      }),
    },
    getChildren: {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      default: () => async (node: Tree | undefined, options: OptionsTree[], level: number) => {
        if (!node) {
          if (options) {
            return options;
          }
          const isAnta = (getContextWorkspace()?.businessType === 1 ?? 0);
          let children: Tree[];
          if (isAnta) {
            const productTypes = '品牌';

            children = [{
              code: 'materialClassification',
              value: 'materialClassification',
              label: '材料分类',
              async getChildren(_, _level) {
                const _node = this;
                const params = {
                  requestType: '3',
                  ...(_level > 1 ? { parentId: _node.value } : {}),
                };
                return (await axios.post<
                  {
                    itemCode: string,
                    itemValue: string,
                    label: string,
                    subCount: number | null,
                  }[]
                >('bff-center/search/dict/conditions/detail', params, {
                  headers: {
                    language: i18n.language,
                  },
                })).data.map(item => {
                  return {
                    value: item.itemValue,
                    code: item.itemCode,
                    label: item.label,
                    leaf: !item.subCount,
                  };
                });
              },
            }, {
              code: 'productType',
              value: 'productType',
              label: productTypes,
              async getChildren() {
                return (await axios.get(`anta-material/dynamics/property/value/title?title=${encodeURIComponent(productTypes)}`)).data
                  .map((item: any) => {
                    return {
                      code: item.id, value: item.id, label: item.label, leaf: true,
                    };
                  });
              },
            }, {
              code: 'year',
              value: 'year',
              label: '年份',
              children: (() => {
                const currentYear = 2020;
                const getYearObj = (year: string) => ({
                  code: year, value: year, label: year, leaf: true,
                });
                const afterYears = [...Array(51)].map((_, i) => getYearObj(`${currentYear + i}`));
                const beforeYears = [...Array(20)].map((_, i) => getYearObj(`${currentYear - i - 1}`));
                return [...afterYears, ...beforeYears];
              })(),
            }, {
              code: 'season',
              value: 'season',
              label: '季节',
              children: Array.from(new Array(4)).map((_item, i) => {
                const q = `Q${i + 1}`;
                return {
                  code: q, value: q, label: q, leaf: true,
                };
              }),
            }, {
              code: 'price',
              value: { startPrice: undefined, endPrice: undefined, unit: 1 },
              isSelected(value) {
                return typeof value === 'object' && ('startPrice' in value || 'endPrice' in value) && 'unit' in value;
              },
              label: '价格区间',
              slot: PriceForm,
            }, {
              code: 'fileInfoStatus',
              value: 'fileInfoStatus',
              label: '有无4ddat文件',
              children: [
                {
                  label: '有4ddat文件', value: true, leaf: true, code: 'fileInfoStatus',
                },
                {
                  label: '无4ddat文件', value: false, leaf: true, code: 'fileInfoStatus',
                },
              ],
            }];
            const company = getCurrentCompany();

            if (company && company.type !== 3) {
              children.push({
                code: 'evergreen',
                value: 'evergreen',
                label: '常青材料',
                children: [
                  {
                    label: 'TRUE', value: 1, leaf: true, tagName: '常青', code: 'evergreen',
                  },
                  {
                    label: 'FALSE', value: 2, leaf: true, tagName: '非常青', code: 'evergreen',
                  },
                ],
              });
            }
          } else {
            children = [
              {
                label: i18n.t('file-list.material_type'),
                code: 'materialType',
                value: 'materialType',
                async getChildren() {
                  return (await axios.get<{ itemValue: string, itemCode: string, label: string }[]>('bff-center/showCommon/getDictionaryTypeItem', {
                    headers: {
                      language: i18n.language,
                    },
                    params: {
                      typeCode: 'materialType',
                    },
                  })).data.map(item => {
                    return {
                      value: item.itemValue,
                      code: item.itemCode,
                      label: item.label,
                      leaf: true,
                    };
                  });
                },
              },
              {
                label: i18n.t('file-list.color_family'),
                code: 'colorFamily',
                value: 'colorFamily',
                children: materialColorPicker.map(item => {
                  return {
                    value: item.name,
                    code: 'colorFamily',
                    label: i18n.language === 'cn' ? item.cnName : item.name,
                    leaf: true,
                  };
                }),
              },

            ];
          }
          return children;
        }

        return node.children ?? [];
      },
    },
    placeholder: {
      default: i18n.t('workspace.search'),
    },
  },
  render() {
    const self = this;
    const customSlotNodes = self.customSlotNodes ?? [];
    const customSlotNode = customSlotNodes[customSlotNodes.length - 1];
    return (
      <div class='search-and-select-wrapper flex' style={{
        position: 'relative',
      }}>


        <div style={{ flex: 1 }} class='flex'>
          {self.showCascader}
          {self.showCascader
            ? <el-popover
              ref="popover"
              vModel={this.popoverShown}
              trigger="click"
              popper-class='search-and-select-cascader-wrapper'
              placement="bottom-start"
            >
              <el-button
                style="height: 32px; font-size: 13px;"
                slot="reference"
                size='medium'
                class='flex-center drop-down-trigger'
              >{Object.keys(self.filters).length ? i18n.t('file-list.filter') : i18n.t('file-list.all')}
                <i style='margin-left: 8px; font-size: 14px; color: #C0C4CC'
                  class={['el-select__caret el-icon-arrow-down', { 'is-reverse': this.popoverShown }]} />
              </el-button>
              <div class="flex">
                <el-cascader-panel
                  ref="cascader"
                  border={false}
                  on={{
                    'expand-change': async (tag: (string | number)[]) => {
                      const filter = await getPaths(tag, self.options_, this.innerGetChildren);
                      const node = filter[filter.length - 1];
                      if (node.slot) {
                        this.customSlotNodes = filter;
                      } else {
                        this.customSlotNodes = undefined;
                      }
                      await nextTick();
                      // 重新计算popover位置
                      self.$refs.popover.updatePopper();
                    },
                    change: (values: (string | number)[]) => {
                      // self.cascaderValue = Date.now();
                      self.addFilter(values);
                    },
                  }}
                  props={{
                    options: self.options_,
                    props: {
                      lazy: true,
                      async lazyLoad({ data, level }, resolve) {
                        resolve(await self.innerGetChildren(data, self.options_, level));
                      },
                      children: 'children_',
                    },
                  }}
                >
                </el-cascader-panel>
                {
                  customSlotNode ? <div class="custom-panel">
                    <customSlotNode.slot node={customSlotNode} nodes={customSlotNodes} value={customSlotNode.value} onChange={(value) => {
                      customSlotNode.value = value;
                      self.addFilter(customSlotNodes.map(item => item.value));
                      self.$refs.cascader.activePath = [];
                      this.customSlotNodes = null;
                    }} />
                  </div> : null
                }
              </div>

            </el-popover> : null
          }
          <VueTagsInput
            class={{ 'show-icon': !self.showCascader, 'has-search-image': self.$slots['append-icon'] }}
            scopedSlots={{
              'between-elements': () => (self.showCascader ? null : <i class='iconfont icon-search' />),
            }}
            vModel={self.keyword}
            tags={[
              ...Object.values(self.filters)
                .map(filter => {
                  const last = filter[filter.length - 1];
                  if (last) {
                    return { text: last.tagName ?? last.label, filter };
                  }
                  return undefined;
                }).filter(item => !!item),
            ]}
            placeholder={self.placeholder}
            nativeOnKeyup={(e) => {
              if (e.keyCode === 13) {
                self.updateKeyword();
              }
            }}
            on={{
              blur: () => {
                self.updateKeyword();
              },
              'before-adding-tag': () => {
                self.updateKeyword();
              },
              'before-deleting-tag': ({ tag }) => {
                if (tag) {
                  const { filter } = tag;
                  self.removeFilter(filter[0].code);
                }
              },
            }}
          />
          {self.$slots['append-icon']
            ? <div class="search-icon-wrap">{self.$slots['append-icon']}</div> : null}
        </div>
      </div>
    );
  },
  setup(props, { emit }) {
    const cooperation = computed(() => { return envs.VUE_APP_COOPERATION; });
    const cascader = ref<ElCascader>();
    const keyword = ref(props.value.keyword);
    const filters = ref<{ [k: string]: OptionsTree[] }>({});
    const options = ref([] as OptionsTree[]);
    const customSlotNodes = ref<Tree[]>();
    const dropdownShown = ref(false);

    const innerGetChildren = async (node: Tree | undefined, _options: Tree[], level: number) => {
      const getChildren = node?.getChildren
        ?? function getChildren(this: Tree | undefined, __options: Tree[], _level: number) {
          return props.getChildren(this, _options, _level);
        };
      const children = await getChildren.call(node as Tree, _options, level);
      return children!.map(item => {
        if (!item.leaf) {
          item.getChildren = item.getChildren ?? getChildren;
        }
        if (item.slot) {
          item.leaf = false;
          item.children = [];
        }
        item.code = item.code ?? node?.code;
        return item;
      });
    };

    function update(__options, value) {
      keyword.value = value.keyword;
      filters.value = {};
      keys(value.filters).forEach(async (key) => {
        Vue.set(filters.value, key, await getPaths(value.filters[key], __options, innerGetChildren));
      });
    }

    let w: ReturnType<typeof watch>;
    const generateWatcher = () => {
      if (w) w();
      w = watch(() => props.value, (value, old) => {
        if (JSON.stringify(value) !== JSON.stringify(old)) {
          update(options.value, value);
        }
      }, {
        immediate: false,
      });
    };

    async function updateKeyword() {
      if (props.value.keyword !== keyword.value) {
        emit('change', {
          filters: props.value.filters,
          keyword: keyword.value,
        });
      }
    }

    async function addFilter(values: (string | number)[]) {
      const filter = await getPaths(values, options.value, innerGetChildren);
      const _filters = { ...props.value.filters };
      _filters[filter[0].code] = values;
      emit('change', {
        keyword: props.value.keyword,
        filters: _filters,
      });
    }

    async function removeFilter(filterKey) {
      const _filters = { ...props.value.filters };
      delete _filters[filterKey];
      emit('change', {
        keyword: props.value.keyword,
        filters: _filters,
      });
    }


    function clearSelectorsAndKeyword() {
      emit('change', {
        keyword: '',
        filters: {},
      });
    }


    watch(() => props.options, async (_options) => {
      const __options = await innerGetChildren(undefined, _options, 0);
      options.value = __options;
      customSlotNodes.value = undefined;
      update(options.value, props.value);
    }, {
      immediate: true,
    });

    const showCascader = computed(() => {
      return options.value && !!options.value.length;
    });

    generateWatcher();

    return {
      innerGetChildren,
      dropdownShown,
      updateKeyword,
      removeFilter,
      addFilter,
      customSlotNodes,
      options_: options,
      // cascaderValue: ref(Date.now()),
      popoverShown: ref(false),
      cooperation,
      showCascader,
      cascader,
      filters,
      keyword,
      clearSelectorsAndKeyword,
    };
  },
});
export default SearchAndSelect;
