<template>
  <div>
    <div v-loading="true" class="loading" v-if="memberList === $loadingState" style="height: 400px"></div>
    <el-table v-else :data="memberList" class="custom-table" height="400" style="width: 100%">
      <el-table-column prop="E-mail" :label="$t('workspace.email_or_id')" :width="mailW" :show-overflow-tooltip="true">
        <template slot-scope="{ row: member }">
          <BaseUser :user="member" size="small">
            {{ member.account || member.mail }}
          </BaseUser>
        </template>
      </el-table-column>
      <el-table-column :label="$t('workspace.username')" prop="name" :width="nameW" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column :width="permissionW" :show-overflow-tooltip="true">
        <template slot="header">
          {{ $t("workspace.permission") }}
          <a href="javascript:" class="iconfont icon-help" @click="showPermissionDetail" />
        </template>
        <template slot-scope="{ row: member }">
          <span v-if="member.roleId == '8'" class="text-width width-20">
            {{ $t("workspace.workspace_pass") }}
          </span>
          <span v-else-if="member.roleId != '4' && spaceRoleId == '4'">
            <el-select
              v-model="member.roleId"
              size="small"
              @change="
                $act(
                  changeUserPrivilege,
                  [groupAppId, member.roleId, member.userId],
                  $t('workspace.priviledge_change_info')
                )
              "
            >
              <el-option
                v-for="charactor of ownerRole"
                :key="charactor.roleId"
                :label="roleId2Role[+charactor.roleId]"
                :value="charactor.roleId"
              ></el-option>
            </el-select>
          </span>
          <span
            v-else-if="(member.roleId == '6' || member.roleId == '7') && (spaceRoleId == '5' || spaceRoleId == '4')"
          >
            <el-select
              v-model="member.roleId"
              size="small"
              @change="$act(changeUserPrivilege, [groupAppId, member.roleId, member.userId])"
            >
              <el-option
                v-for="charactor of adminRole"
                :key="charactor.roleId"
                :label="roleId2Role[+charactor.roleId]"
                :value="charactor.roleId"
              ></el-option>
            </el-select>
          </span>
          <span v-else class="text-width width-20">
            {{ roleId2Role[+member.roleId] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column v-if="roleColumnVisible" width="40">
        <template slot-scope="{ row: member }">
          <i
            class="iconfont icon-file-delete"
            v-if="
              (member.roleId == '5' && spaceRoleId == '4') ||
              ((member.roleId == '6' || member.roleId == '7' || member.roleId == '8') &&
                (spaceRoleId == '5' || spaceRoleId == '4')) ||
              (member.roleId == '7' && spaceRoleId == '6')
            "
            @click="$act(handleDeleteUser, [member])"
            style="cursor: pointer"
          />
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import i18n from 'i18next';
import BaseUser from '@/components/base/User';
import { WorkspaceMemberOrm, WorkspaceRoleOrm, WorkspaceUserRoleOrm } from '@/models';
import { changeMemberPrivilege, delMember } from '@/loaders/workspace-member';
import PermissionDetail from './PermissionDetail';
import {
  userAbout,
} from './mixin';

export default {
  mixins: [userAbout],
  props: {
    groupAppId: [String, Number],
    memberList: [Object, Array],
    mailW: {
      type: [String, Number],
      default: 280,
    },
    nameW: {
      type: [String, Number],
      default: 170,
    },
    permissionW: {
      type: [String, Number],
      default: 150,
    },
    roleColumnVisible: {
      type: Boolean,
      default: true,
    },
  },
  components: {
    BaseUser,
  },
  computed: {
    selfRoleId() {
      return WorkspaceUserRoleOrm.find(this.groupAppId).roleId;
    },
    roles() {
      return WorkspaceRoleOrm.all();
    },
    ownerRole() {
      // eslint-disable-next-line
      const arr = this.roles.filter(item => item.roleId != 4 && item.roleId != 8);
      return arr;
    },
    adminRole() {
      // eslint-disable-next-line
      const list = this.roles.filter(item => {
        // eslint-disable-next-line
        if (item.roleId == 6 || item.roleId == 7) {
          return item;
        }
      });
      return list;
    },
  },
  methods: {
    changeUserPrivilege(groupAppId, memberRoleId, memberId) {
      // 注意顺序有变
      changeMemberPrivilege(
        groupAppId, memberId, memberRoleId,
      );
    },
    async showPermissionDetail() {
      await this.$Dialog.dialogify({
        is: PermissionDetail,
      }, {
        props: {
          title: i18n.t('workspace.workspace_permission'),
          width: '720px',
        },
      });
    },
    async handleDeleteUser(member) {
      if (await this.$confirmBool('Confirm to remove this account from workspace ?', 'Info', {
        confirmButtonText: 'Confirm',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
      })) {
        try {
          await delMember(this.groupAppId, member.userId);
          this.$emit('deleteMember');
          this.$message.success('Delete success!');
        } catch (e) {
          this.$errHandler(e);
        }
        // await this.getMembers();
      }
    },
    getUserTypeFromTypeId(typeId) {
      switch (Number(typeId)) {
        case 4:
          return 'Owner';
        case 5:
          return 'Admin';
        case 6:
          return 'Member';
        case 7:
          return 'Guest';
        case 8:
          return 'Pass';
        default:
          return 'Undefined User';
      }
    },
  },
  filters: {
    en: value => {
      if (value) {
        // eslint-disable-next-line
        switch (value) {
          case '空间创建者':
            return 'Owner';
          case '下载者':
            return 'Admin';
          case '编辑者':
            return 'Member';
          case '查看者':
            return 'Guest';
          case '参与者':
            return 'Invited';
        }
      }
      return value;
    },
  },
};
</script>

