<template>
  <section class="invitation-container">
    <div class="invitation-component-selector">
      <!-- 分为两部分，权限（角色）选择器和单人多人选择器 -->
      <div class="permission-and-charactor-selector">
        <!-- 绝对定位到左下角的单人多人切换 -->
        <div class="toggle-target color-primary">
          <div v-if="!multiMode" @click="multiMode = true">
            <i class="iconfont icon-bulk-invitation-o"></i>
            <span>
              {{ modeInvite ? $t("workspace.invite_multiple_partners") : $t("workspace.create_multiple_partners") }}
            </span>
          </div>
          <div v-else @click="multiMode = false">
            <i class="iconfont icon-single-invitation-o"></i>
            <span>
              {{ modeInvite ? $t("workspace.invite_a_partner") : $t("workspace.create_a_partner") }}
            </span>
          </div>
        </div>
      </div>

      <!-- 动态决定用哪个组件 -->
      <transition name="translate-x" mode="out-in">
        <template v-if="currentComponent === componentList[0]">
          <MemberInvitationPermission
            v-loading="isLoading"
            ref="permissionInvitor"
            :multiMode="multiMode"
            :groupAppId="groupAppId"
            :spaceRoleId="spaceRoleId"
            @showPermissionDetail="showPermissionDetail"
            @changeComponent="(t) => (currentComponent = t)"
          />
        </template>
        <template v-else-if="currentComponent === componentList[1]">
          <MemberInvitationWorkspacePass
            v-loading="isLoading"
            ref="workspacePassInvitor"
            :multiMode="multiMode"
            :groupAppId="groupAppId"
            @showPermissionDetail="showPermissionDetail"
            @changeComponent="(t) => (currentComponent = t)"
          />
        </template>
      </transition>

      <footer class="button-group flex-justify-flex-end">
        <el-button @click="$emit('cancel')">
          {{ $t("public.dialog.cancel") }}
        </el-button>
        <el-button @click="$act(handleConfirm)" type="primary" :disabled="!okAvailable || $isActing(handleConfirm)">
          {{ $t("public.dialog.confirm") }}
        </el-button>
      </footer>
    </div>
  </section>
</template>

<script>
import { MessageBox } from 'element-ui';
import i18n from 'i18next';
import tokenizeEmails from '@/utils/tokenizeEmails';
import { refreshByWorkspaceId } from '@/loaders/workspace-member';
import PermissionDetail from './PermissionDetail';
import MemberInvitationPermission from './MemberInvitationPermission';
import MemberInvitationWorkspacePass from './MemberInvitationWorkspacePass';


export default {
  name: 'InvitationWrapper',
  dialogify: {
    props: {
      closeOnClickModal: false,
      width: '560px',
    },
  },
  props: {
    spaceRoleId: [String, Number],
    groupAppId: String,
    modeInvite: {
      default: true,
      type: Boolean,
    },
  },
  components: {
    MemberInvitationPermission,
    MemberInvitationWorkspacePass,
  },
  data() {
    return {
      componentList: ['MemberInvitationPermission', 'MemberInvitationWorkspacePass'],
      currentComponent: '',
      multiMode: false,

      okAvailable: false,
      isLoading: false,
    };
  },
  beforeMount() {
    if (this.modeInvite) {
      // eslint-disable-next-line prefer-destructuring
      this.currentComponent = this.componentList[0];
    } else {
      // eslint-disable-next-line prefer-destructuring
      this.currentComponent = this.componentList[1];
    }
  },
  computed: {
    permissionInvitor() {
      return this.$refs.permissionInvitor;
    },
    workspacePassInvitor() {
      return this.$refs.workspacePassInvitor;
    },
  },
  methods: {
    async handleConfirm() {
      this.isLoading = true;
      try {
        // 赋予权限
        if (this.currentComponent === this.componentList[0]) {
          // 单个赋予
          if (!this.multiMode) {
            // 当没填邮箱，但可以调用该函数，说明是复制了链接，所以操作为直接关闭弹框
            if (!this.permissionInvitor.emailIsEmpty) {
              this.$emit('cancel');
              return;
            }
            await this.permissionInvitor.handleSingleInvitation();
          } else {
            // 多个赋予
            const unnormalizedEmails = this.permissionInvitor.role.mails;
            const emails = tokenizeEmails(unnormalizedEmails);
            await this.permissionInvitor.handleMultipleInvitation(emails);
          }
          this.$message.success(i18n.t('workspace.invite_success_tip'));
        } else if (this.currentComponent === this.componentList[1]) { // 创建虚拟账号
          // 单个创建
          if (!this.multiMode) {
            await this.workspacePassInvitor.handleSingleInvitation();
            // 创建多个
          } else {
            const { formShareableLink } = this.workspacePassInvitor;
            const unnormalizedEmails = formShareableLink.form.mails;
            const emails = tokenizeEmails(unnormalizedEmails);
            await this.workspacePassInvitor.handleMultipleInvitation(emails);
          }
          await refreshByWorkspaceId(this.groupAppId);
          this.$message.success(i18n.t('workspace.workspace_pass_create_success_tip'));
        }
        this.$emit('confirm');
      } catch (e) {
        if (e.message === 'params invalidate: invited user already in your company') {
          MessageBox.alert(i18n.t('workspace.exist_in_company'), i18n.t('public.dialog.notice'));
        } else {
          this.$errHandler(e);
        }
      } finally {
        this.isLoading = false;
      }
    },
    async showPermissionDetail() {
      await this.$Dialog.dialogify({
        is: PermissionDetail,
      }, {
        props: {
          title: i18n.t('workspace.workspace_permission'),
          width: '720px',
        },
      });
    },
  },
};
</script>

