<template>
  <section class="permission-invitation-container">

    <h3 class="flex-center" style="margin-bottom: 38px; font-size: 20px; color: #2D2E30;">
      {{ $t('workspace.invite_people')}}
    </h3>

    <!-- 标题 -->
    <h4 class="invitation-header" style="width: 280px;">
      {{ $t('workspace.select_permission') }}
      <i @click="$emit('showPermissionDetail')" class="iconfont icon-help"></i>
    </h4>

    <!-- 权限选择器 -->
    <el-radio-group class="permission-selector flex-space-around" v-model="role.roleId">
      <el-radio :label="7" v-if="spaceRoleId < 7">
        {{ $t('workspace.guest') }}
      </el-radio>
      <el-radio :label="6" v-if="spaceRoleId < 6">
        {{ $t('workspace.member') }}
      </el-radio>
      <el-radio :label="5" v-if="spaceRoleId < 5">
        {{ $t('workspace.admin') }}
      </el-radio>
    </el-radio-group>

    <!-- 创建并绑定虚拟账号 -->
    <!-- <div style="margin-top: 20px;">
      Or&nbsp;<span class="color-primary"
        @click="$emit('changeComponent', 'MemberInvitationWorkspacePass')">
        Create Workspace Pass</span>
    </div> -->

    <el-divider />

    <!-- 邮箱和备注 -->
    <h4 class="invitation-header" v-if="!multiMode">
      {{ $t('workspace.choose_invitation_mode') }}
    </h4>
    <section class="email-invitation-container" style="margin-top: 20px;">
      <div v-if="!multiMode">
        {{ $t('workspace.email_invitation') }}
      </div>
      <h4 v-else class="invitation-header">
        {{ $t('workspace.email_invitation') }}
      </h4>
      <div class="email-box" style="margin-top:15px;">
        <div style="width:20%;">
          {{ $t('workspace.email') }}:
        </div>
        <el-input v-if="!multiMode" v-model="role.email" @focus="showRemarks=true"
          :placeholder="$t('workspace.email_input_placeholder')"
          label="mainbox"></el-input>
        <el-input v-else type="textarea" v-model="role.mails" @focus="showRemarks=true" rows="5"
          class="multi-textarea"
          :placeholder="multipleInvitationPlaceholder"></el-input>
      </div>
      <div v-show="showRemarks" class="email-box" style="margin-top:10px;">
        <div style="width:20%;">
          {{$t('workspace.remarks')}}:
        </div>
        <el-input type="textarea" v-model="role.remarks"
          :placeholder="$t('workspace.remarks_tip')"></el-input>
      </div>
    </section>

    <!-- 复制生成的链接 -->
    <section class="Link-invitation-container" v-if="!multiMode">
      <div style="margin-bottom: 15px;">
        {{ $t('workspace.link_invitation') }}
      </div>
      <button class="link-copy-wrapper flex"
        :data-clipboard-text="link">
        <strong style="margin-left: 10px;">
          <i class="iconfont icon-link link-icon"></i>
        </strong>
        <span>
          {{ $t('workspace.copy_invitation_link') }}
        </span>
      </button>
    </section>
  </section>
</template>

<script>
import i18n from 'i18next';
import Clipboard from 'clipboard';
import { generateInvitationLink, sendEmailByType, InviteMultipleMembers } from '@/api/email';
import tokenizeEmails from '@/utils/tokenizeEmails';

export default {
  props: {
    multiMode: Boolean,
    groupAppId: String,
    spaceRoleId: [Number, String],
  },
  data() {
    this.multipleInvitationPlaceholder = i18n.t('workspace.multiple_invitation_placeholder');
    this.tokenizeEmails = tokenizeEmails;
    return {
      role: {
        roleId: 7,
        email: '',
        mails: '',
        remarks: '',
      },
      showRemarks: false,
      link: '',
      clipboard: {},
    };
  },
  mounted() {
    this.clipboard = new Clipboard('.link-copy-wrapper');
    this.clipboard.on('success', () => {
      this.$closest('InvitationWrapper').okAvailable = true;
      this.$message.success(i18n.t('workspace.copy_success_tip'));
    });
    this.clipboard.on('error', () => {
      this.$message.warning(i18n.t('workspace.copy_fail_tip'));
    });
    this.generateLink();
    this.$closest('InvitationWrapper').okAvailable = false;
  },
  computed: {
    emailIsEmpty() {
      return this.role.email;
    },
    singleEmailValid() {
      return /\w+@\w+.\w+/.test(this.role.email);
    },
    multipleEmailsValid() {
      try {
        tokenizeEmails(this.role.mails);
        return true;
      } catch (e) {
        return false;
      }
    },
  },
  methods: {
    async generateLink() {
      const { groupAppId } = this;
      const params = {
        type: 2,
        groupAppId,
        roleId: this.role.roleId,
      };
      const { data } = await generateInvitationLink(params);
      this.link = data;
    },
    async handleSingleInvitation() {
      const params = {
        ...this.role,
        groupAppId: this.groupAppId,
      };
      await sendEmailByType({
        email: params.email,
        params,
        type: 3,
      });
    },
    async handleMultipleInvitation(emails) {
      // eslint-disable-next-line
      let { roleId: role, remarks } = this.role;
      await InviteMultipleMembers({
        newAccount: false,
        emails,
        role,
        remarks,
        groupAppId: this.groupAppId,
      });
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'role.roleId': function () {
      this.generateLink();
    },
    multiMode() {
      this.showRemarks = false;
      this.$closest('InvitationWrapper').okAvailable = false;
    },
    singleEmailValid(newv) {
      this.$closest('InvitationWrapper').okAvailable = newv;
    },
    multipleEmailsValid(newv) {
      this.$closest('InvitationWrapper').okAvailable = newv;
    },
  },
  destroyed() {
    this.clipboard.destroy();
  },
};
</script>

<style lang="scss" scoped>
.permission-invitation-container {
  .email-box {
    display: flex;
    align-items: center;
  }
  .link-copy-wrapper {
    height: 46px;
    line-height: 46px;
    width: 100%;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #dbe0e6;
    outline: none;
    border-radius: 4px;
  }
  .Link-invitation-container {
    margin: 25px 0 0;
  }
  .link-icon {
    @include resize-icon(1.2);
    margin-right: 0.3em;
  }
  .multi-textarea {
    /deep/ textarea {
      word-break: normal;
    }
  }
}
.icon-help {
  color: #ccc;
  font-weight: 100;
}
</style>
