<template>
  <transition-group name="workspace-group" tag="ul" class="workspace-members"
    v-loading="membersLoading">
    <li v-for="member of
    (useVisibleList) ? visibleList : groupMembers" :key="member.$id"
      class="workspace-members__member flex-column flex-center">
      <!-- 成员卡片 -->
      <MemberCard :user="member"
        :groupAppId="groupAppId">
        <!-- 成员卡片的触发器是包括头像和姓名的整体 -->
        <div style="cursor: pointer;" class="flex-center flex-column">
          <div style="position: relative">
            <base-image :src="member.headImg || defaultAvatar" class="avator" />
            <div v-if="roleId2Role[+member.roleId] === OWNER">
              <IconWrapper
                icon-name='iconfont iconbiaoqian-owner' :font-size='15'
                style="position: absolute;right:0;bottom:0;color:#F7A900;" />
            </div>
            <div v-else-if="roleId2Role[+member.roleId] === ADMIN">
              <IconWrapper
                icon-name='iconfont iconbiaoqian-admin' :font-size='15'
                style="position: absolute;right:0;bottom:0;color:#2878FF;" />
            </div>
          </div>
          <OmittedDisplayer width='57' class="member-name">
            {{ member.name || member.account || ' ' }}
          </OmittedDisplayer>
        </div>
      </MemberCard>
    </li>
    <!-- 省略标识，点击后展开为所有成员列表 -->
    <li v-if="useVisibleList" @click="lastTimeUseVisibleList = false;
    useVisibleList = false;" key='placeholder'
      class="workspace-members__member flex-column flex-vertical-center">
      <div class="avator placeholder-avator flex-center">
        +{{ omittedLength }}
      </div>
      <div class="member-name"> </div>
    </li>
  </transition-group>
</template>

<script>
import IconWrapper from '@/components/icon/IconWrapper';
import OmittedDisplayer from '@/components/OmittedDisplayer';
import defaultAvatar from '@/assets/img/header/default_avatar.png';
import { userAbout } from './mixin';
import MemberCard from './MemberCard';

export default {
  mixins: [userAbout],
  components: {
    OmittedDisplayer,
    MemberCard,
    IconWrapper,
  },
  props: {
    groupMembers: [Array, null],
    searchMode: Boolean,
    membersLoading: Boolean,
    groupAppId: String,
  },
  computed: {
    visibleList() { // 省略后的成员列表，长度为17个
      return this.omitUsable && this.groupMembers.slice(0, 17);
    },
    omitUsable() { // 省略的成员列表是否可用，由长度确定
      return this.groupMembers.length > 18;
    },
    omittedLength() {
      return this.groupMembers.length - this.visibleList.length;
    },
  },
  data() {
    return {
      // 是否启用省略后的成员列表，由是否是成员搜索模式和omitUsable确定,由于是搜索是异步的，所以不用computed
      useVisibleList: false,
      lastTimeUseVisibleList: true,
      defaultAvatar,
    };
  },
  watch: {
    groupMembers(newList, oldList) {
      if (!Array.isArray(newList)) return;
      /** 当不是搜索模式且新列表长度大于17，且包含以下两条件中的一个时触发使用省略列表：
       * 第一次进入时（此时原列表为空）
       * 之前使用了省略列表
       */
      if (!this.searchMode && this.omitUsable
        && (!oldList || this.lastTimeUseVisibleList)) {
        this.useVisibleList = true;
      } else {
        if (this.useVisibleList) this.lastTimeUseVisibleList = true;
        this.useVisibleList = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.workspace-members {
  display: flex;
  margin: 0 12px 12px;
  flex-wrap: wrap;
  .workspace-members__member {
    padding: 12px 6px;
    .placeholder-avator {
      padding: 0 6px;
      background-color: $color-primary;
      color: #fff;
      cursor: pointer;
    }
    .avator {
      width: 45px;
      height: 45px;
      border-radius: 50%;
    }
    .member-name {
      display: block;
      padding-top: 7px;
      text-align: center;
      width: 57px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;

      color: #666;
      font-size: 12px;
    }
  }
}
</style>
