import { WorkspaceOrm } from '@/models';
import { Workspace } from '@/models/Workspace';
import {
  ApiGroupappList as FetchWorkspacesInfo, // 获取个人workpace信息
  ApiDismissWorkpace as DeleteWorkspace, // 解散worksapce
  ApiQuitWorkpace as QuitWorkspace, // 退出worksapce
  ApiRenameWorkspace as RenameWorkspace, // 重命名worksapce
  ApiAddWorkspace as CreateWorkspace, // 新建worksapce
} from '@/api/permission';
import {
  fetchWorkspaces,
} from '@/api/catalogue';
import axios from '@/utils/axios';

async function getWorkspace(groupAppId: string) {
  return (await axios.get<Workspace>('authcenter/manage/groupapp/detail', {
    headers: { groupAppId },
  })).data;
}

async function updateWorkspace(groupAppId: string) {
  const workspace = await getWorkspace(groupAppId);
  await WorkspaceOrm.insertOrUpdate({ data: workspace });
  return workspace;
}

export async function refreshWorkspace() {
  // this.deleteAll();
  const workspaces = await FetchWorkspacesInfo('2');
  await WorkspaceOrm.insertOrUpdate(workspaces);
}

export async function refreshWorkspaceWithoutMembers() {
  // this.deleteAll();
  const workspaces = await fetchWorkspaces(2);
  await WorkspaceOrm.insertOrUpdate(workspaces);
}

export async function addWorkspace(name: string) {
  const { data: groupAppId } = await CreateWorkspace({ name });
  return (await WorkspaceOrm.insertOrUpdate({
    data: [{
      groupAppId,
      name,
      roleId: '4', // 创建者roleId为4
      type: 1,
    }],
  })).workspaces[0];
}

export async function delWorkspace(groupAppId: string) {
  await DeleteWorkspace(groupAppId);
  WorkspaceOrm.delete(groupAppId);
}

export async function renameWorkspace(groupAppId: string, name: string) {
  await RenameWorkspace(groupAppId, name);
  WorkspaceOrm.update({
    where: w => w.groupAppId === groupAppId,
    data: { name },
  });
}

export async function quitWorkspace(groupAppId: string) {
  await QuitWorkspace(groupAppId);
  WorkspaceOrm.delete(groupAppId);
}

export { getWorkspace, updateWorkspace };
