
import {
  computed, defineComponent, ref, watch,
} from '@vue/composition-api';
import Catalog, {
  BusinessType0SearchParams, BusinessType1SearchParams, CatalogFolder, SearchParams,
} from '@/models/Catalog';
import keys from '@4dst-saas/public-utils/dist/keys';
import { i18n } from '@/i18n';
import { getContextWorkspace } from '@/loaders/context';
import ImagePicker from './ImagePicker.vue';
import SearchAndSelect from './Search&Select.vue';

const searchTypeFiles = [0, 1, 2, 3, 4];
const searchTypeMaterial = [1];
const searchTypeFilesStr = JSON.stringify(searchTypeFiles);
const searchTypeMaterialStr = JSON.stringify(searchTypeMaterial);
const searchTypeFolderStr = JSON.stringify([null]);
type Price = { startPrice: number, endPrice: number, unit: number };
type SearchAndSelectValue = {
  keyword: string
  filters: {
    fileInfoStatus?: ['fileInfoStatus', boolean]

    materialType?: ['materialType', string]
    colorFamily?: ['colorFamily', string]

    materialClassification?: ['materialClassification', ...string[]]
    productType?: ['productType', string]
    year?: ['year', string]
    season?: ['season', string]
    price?: [Price]
    evergreen?: ['evergreen', (1 | 2)]
  }
};
export default defineComponent({
  props: {
    activeFolder: {
      type: Object as () => CatalogFolder,
      required: true,
    },
    searchParams: {
      type: Object as () => SearchParams & { relateType: Catalog['relateType'][] },
    },
  },
  setup(props, { emit }) {
    const showImageSearchDialog = ref(false);
    const searchType = ref<string>('');
    const searchAndSelectValue = ref<SearchAndSelectValue>();
    const showMaterialSearchOption = computed(() => {
      return [12, 16, 13].includes(props.activeFolder.entryAppType!);
    });
    const showMaterialSearch = computed(() => {
      return searchType.value === searchTypeMaterialStr && showMaterialSearchOption.value;
    });
    const uploadImage = (file: File) => {
      if (file) {
        emit('search', { ...props.searchParams, image: file, relateType: searchType.value });
      } else {
        emit('search', undefined);
      }
    };
    const folderSearchKeyword = ref('');
    watch(() => {
      const { searchParams } = props;
      if (!searchParams) {
        return {
          searchAndSelectValue: {
            keyword: '',
            filters: {},
          },
          forderSearchKeyword: '',
          searchTypes: 'undefined',
        };
      }
      const _searchParams = searchParams as BusinessType0SearchParams & BusinessType1SearchParams;
      const _searchAndSelectValue: SearchAndSelectValue = {
        keyword: props.searchParams?.keyword ?? '',
        filters: Object.fromEntries(([
          'materialType', 'colorFamily', 'fileInfoStatus',
          'productType', 'materialClassification', 'year',
          'season', 'price', 'evergreen',
        ] as const).map(k => {
          const value = _searchParams[k];
          return [
            k,
            // eslint-disable-next-line no-nested-ternary
            value === undefined ? undefined : (
              (k === 'price' ? [] : [k]).concat(value as any)
            ),
          ];
        }).filter(([, value]) => value !== undefined)),
      };
      return {
        searchAndSelectValue: _searchAndSelectValue,
        forderSearchKeyword: searchParams.keyword ?? '',
        searchTypes: typeof searchParams.relateType === 'undefined' ? 'undefined'
          : JSON.stringify(searchParams.relateType),
      };
    }, ({
      forderSearchKeyword: _folderSearchKeyword,
      searchTypes: _searchTypes,
      searchAndSelectValue: _searchAndSelectValue,
    }) => {
      console.log(_searchTypes);
      folderSearchKeyword.value = _folderSearchKeyword;
      searchType.value = _searchTypes;
      searchAndSelectValue.value = _searchAndSelectValue;
    }, {
      immediate: true,
    });
    const searchFolder = () => {
      emit('search', {
        relateType: props.searchParams?.relateType,
        keyword: folderSearchKeyword.value,
      });
    };
    const handleSearchParamsChange = (v: SearchAndSelectValue) => {
      const { filters } = v;
      const otherSearchParams = keys(filters).reduce((memo, key) => {
        const value = filters[key];
        if (value && value.length) {
          if (key === 'materialClassification') {
            memo[key] = value.slice(1).join('_');
          } else {
            memo[key] = value[value.length - 1];
          }
        }
        return memo;
      }, {} as Omit<SearchParams, 'keyword' | 'relateType'>);
      emit('search', {
        keyword: v.keyword,
        relateType: props.searchParams?.relateType,
        ...otherSearchParams,
      });
    };

    const changeSearchType = (types: string) => {
      emit('search', {
        keyword: '',
        relateType: types === 'undefined' ? undefined : JSON.parse(types),
      });
    };
    return {
      changeSearchType,
      folderSearchKeyword,
      searchFolder,
      handleSearchParamsChange,
      showImageSearchDialog,
      showMaterialSearch,
      showMaterialSearchOption,
      uploadImage,
      searchType,
      searchAndSelectValue,
    };
  },
  render() {
    const {
      showMaterialSearch, showMaterialSearchOption, uploadImage,
      handleSearchParamsChange, searchFolder, searchAndSelectValue, changeSearchType,
    } = this;
    const options = showMaterialSearch ? undefined : [];
    const { searchParams } = this;
    const workspace = getContextWorkspace();
    return <div class="navigation-search host">
      <el-select class="search-type" size="small" value={this.searchType}
        onChange={changeSearchType}>
        <el-option label={i18n.t('file-list.all')} value={'undefined'} />
        <el-option label={i18n.t('file-list.folder')} value={`${searchTypeFolderStr}`} />
        {showMaterialSearchOption
          ? <el-option label={i18n.t('file-list.material')} value={`${searchTypeMaterialStr}`} /> : null}
      </el-select>
      {
        showMaterialSearch ? <SearchAndSelect
          class="search-and-select"
          options={options}
          props={
            {
              ...(showMaterialSearch ? {} : { options: [] }),
              ...(searchAndSelectValue ? {
                value: searchAndSelectValue,
              } : {}),
            }
          }
          onChange={handleSearchParamsChange}
        >
          {
            workspace?.businessType === 1
              ? <el-popover slot="append-icon">
                <ImagePicker
                  image={searchParams?.image}
                  on={{
                    'select-image': uploadImage,
                  }}
                />
                <i
                  slot="reference"
                  class={['i-camera', { active: !!this.searchParams?.image }]}
                />
              </el-popover> : null
          }

        </SearchAndSelect> : <div class="el-input el-input--small folder-keyword">
            <input type="text" autocomplete="off"
              vModel={this.folderSearchKeyword}
              onBlur={() => searchFolder()}
              placeholder={this.searchType === 'undefined'
                ? i18n.t('file-list.search_placeholder') : i18n.t('file-list.folder_search_placeholder')} class="el-input__inner"
              onKeyup={(e) => { if (e.keyCode === 13) { searchFolder(); } }}
            />
          </div>
      }
    </div>;
  },
});
