



























































































import draggingData from '@/utils/dragging-data';
import {
  defineComponent, watch, ref, computed, onMounted,
} from '@vue/composition-api';
import isQt from '@/utils/is-qt';
import { WorkspaceOrm } from '@/models';
import openWindow from '@/utils/open-window';
import exec from '@/utils/exec';
import { defaultLang } from '@/i18n';
import { CatalogOptions, SearchParams } from '@/models/Catalog';
import axios from '@/utils/axios';
import ShortcutFile from './components/ShortcutFile.vue';
import Catalog from './components/catalog/index.vue';
import ChatPanel from './components/im/ChatPanel.vue';
import WorkspaceSidebar from './components/workspace/WorkspaceSidebar.vue';
import WorkspaceTopbar from './components/workspace/WorkspaceTopbar.vue';


export default defineComponent({
  name: 'FileList',
  props: {
    entryAppType: Number,
    id: String,
    workspaceBusinessType: Number,
    workspaceName: String,
    workspaceId: String,
    workspaceType: Number,
    searchParams: Object,
    groupId: String,
  },
  components: {
    WorkspaceSidebar,
    WorkspaceTopbar,
    // ShortcutFile,
    Catalog,
    // WorkspaceList,
    ChatPanel,
  },
  setup(props, { root }) {
    const { $router } = root;
    const shareChatId = ref<number | null>();
    const chating = ref(false);

    const catalogParams = props;
    watch(() => {
      return {
        groupId: props.groupdId,
        // 筛选
      };
    }, async ({ groupId }) => {
      chating.value = !!groupId;
      shareChatId.value = groupId ?? null;
    }, {
      immediate: true,
    });

    // 分享到Im，外部调用
    const shareToIm = (shareChatList) => {
      chating.value = true;
      shareChatId.value = shareChatList[0].id;
    };
    const initIm = () => {
      chating.value = true;
      shareChatId.value = null;
    };

    const workspaceList = computed(() => {
      return WorkspaceOrm.query().orderBy('sort').get();
    });
    const isWorkspace = computed(() => {
      return props.entryAppType === 13;
    });

    const _draggingData = computed(() => {
      return draggingData.get();
    });

    const collapseSideBar = ref(props.entryAppType === 13);
    watch(() => ({ entryAppType: props.entryAppType, draggingData: _draggingData.value }), ({ entryAppType, draggingData: dv }) => {
      collapseSideBar.value = entryAppType === 13 || !!dv;
    });

    const meetingId = ref('');

    onMounted(async () => {
      root.$on('open-im', (id) => {
        // console.info(id);
        shareToIm([{ id }]);
      });
      meetingId.value = (await axios({
        url: '/meeting/meeting/id',
      })).data;
    });

    const showImPoppver = ref(false);


    const openUpMetting = async () => {
      if (isQt()) {
        const url = `${window.location.origin}/qt/up-meeting?meetingId=${meetingId.value}&language=${defaultLang}`;
        exec('enterShowRoom', url);
      } else {
        openWindow({ path: '/up-meeting', query: { meetingId: meetingId.value } });
      }
    };

    const vmCatalog = ref<InstanceType<typeof Catalog>>();

    const search = async (_searchParams?: SearchParams, activeModule?: CatalogOptions) => {
      // KHD-1086
      if (!activeModule) return undefined;
      try {
        if (_searchParams && _searchParams.image) {
          return vmCatalog.value?.refresh({ searchParams: _searchParams });
        }
        await $router.replace({
          query: {
            id: props.id,
            relateId: props.workspaceId,
            appType: `${props.entryAppType}`,
            workspaceId: props.workspaceId,
            workspaceBusinessType: `${props.workspaceBusinessType}`,
            ...(_searchParams ? { searchParams: JSON.stringify(_searchParams) } : {}),
          },
        });
        return undefined;
      } catch (e) {
        // 路由相同时，刷新数据，用于以图搜图取消图片或者使用其他搜索的情况
        if (e.name === 'NavigationDuplicated') {
          return vmCatalog.value?.refresh({ searchParams: _searchParams, timestamp: Date.now() });
        }
        throw e;
      }
    };

    return {
      catalog: vmCatalog,
      meetingId,
      openUpMetting,
      search,
      async enterFolder(catalog) {
        try {
          await $router.push({
            query: {
              ...$router.currentRoute.query,
              searchParams: null,
              id: catalog.id,
              workspaceId: props.workspaceId,
              workspaceBusinessType: catalog.workspaceBusinessType,
              appType: catalog.entryAppType,
              relateId: catalog.workspaceId,
            },
          });
        } catch (e) {
          console.log(e);
          // 路由相同时，刷新数据，用于以图搜图取消图片或者使用其他搜索的情况
          if (e.name === 'NavigationDuplicated') {
            vmCatalog.value?.refresh({ searchParams: null, timestamp: Date.now() });
          }
          throw e;
        }
      },
      isWorkspace,
      workspaceList,
      draggingData: _draggingData,
      collapseSideBar,
      catalogParams,
      showImPoppver, // 虚拟帐号首次进入的群聊提示
      chating, // 正在聊天
      shareChatId, // 分享的第一个群聊
      shareToIm,
      initIm,
      isQt,
      // WorkspaceOrm,
    };
  },
});
