
import i18n from 'i18next';
import {
  defineComponent, computed,
} from '@vue/composition-api';
import { commands } from '@/utils/catalog';
import useClosest from '_/composables/use-closest';
import { CatalogOptions } from '@/models/Catalog';
import Catalog from './index.vue';

const NavigationTools = defineComponent({
  props: {
    itemIncrement: {
      type: Number,
    },
    viewType: {
      type: String,
    },
    shownInfo: {
      type: Boolean,
    },
    sortParams: {
      type: Object as () => NonNullable<CatalogOptions['sortParams']>,
      required: true,
    },
    searchParams: {
      type: Object as () => NonNullable<CatalogOptions['searchParams']>,
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const fileList = useClosest<typeof Catalog, true>('Catalog');
    type KeyOfCommands = keyof (typeof commands);
    const commandKeys: KeyOfCommands[] = ['createFolder', 'uploadMaterial', 'uploadModel', 'upload'];
    const activeFolder = computed(() => {
      return fileList.value.activeFolder;
    });
    const can = computed(() => {
      return commandKeys.reduce((memo, k) => {
        memo[k] = commands[k].check({ data: activeFolder.value! });
        return memo;
      }, {} as { [key in KeyOfCommands]: boolean });
    });

    const handleUploadRawFile = async () => {
      const vFileList = fileList.value;
      await commands.upload.fn({ data: vFileList.activeFolder! });
      await vFileList.refresh();
    };
    // const handleUploadImage = async () => {
    //   const vFileList = fileList.value;
    //   await commands.uploadImage.fn({ data: vFileList.activeFolder! });
    //   await vFileList.refresh();
    // };
    // const handleUploadPDF = async () => {
    //   const vFileList = fileList.value;
    //   await commands.uploadPDF.fn({ data: vFileList.activeFolder! });
    //   await vFileList.refresh();
    // };
    const handleUploadMaterial = async () => {
      const vFileList = fileList.value;
      await commands.uploadMaterial.fn({ data: vFileList.activeFolder! });
      await vFileList.refresh();
      // /fragment/upload/material
    };
    const handleUploadModel = async () => {
      const vFileList = fileList.value;
      await commands.uploadModel.fn({ data: vFileList.activeFolder! });
      await vFileList.refresh();
      // /fragment/upload/model
    };
    const handleAddFolder = async () => {
      const vFileList = fileList.value;
      await commands.createFolder.fn({ data: vFileList.activeFolder! });
      await vFileList.refresh();
    };
    const setShownInfo = (v: boolean) => {
      emit('update:shownInfo', v);
    };
    const setViewType = (v: 'drape' | 'flat' | 'default') => {
      emit('update:viewType', v);
    };
    const setItemIncrement = (v: number) => {
      emit('update:itemIncrement', v);
    };
    const setSort = (sortParams: { orderBy?: string, desc?: boolean }) => {
      emit('update:sortParams', {
        ...props.sortParams,
        ...sortParams,
      });
    };
    const setSortOrderBy = (v: string) => {
      setSort({
        orderBy: v,
      });
    };
    const setSortDesc = (v: boolean) => {
      setSort({
        desc: v,
      });
    };
    const showSearchOptions = computed(() => {
      const vActiveFolder = activeFolder.value;
      return ([12, 16, 13] as (void | number)[]).includes(vActiveFolder?.entryAppType);
    });


    const handleSearchParamsChange = (v: {
      keyword: string,
      filters: {
        materialTypes: string[],
        colorFamilys: string[],
        productTypes: string[],
        brands: string[],
        materialClassifications: string[],
        years: string[],
        seasons: string[],
        prices?: { startPrice: number, endPrice: number, unit: number }[]
      }
    }) => {
      const vFileList = fileList.value;
      vFileList.search(v);
    };


    return {
      fileList,
      can,
      showSearchOptions,
      activeFolder,
      handleUploadRawFile,
      // handleUploadImage,
      // handleUploadPDF,
      handleUploadMaterial,
      handleUploadModel,
      handleAddFolder,
      setShownInfo,
      setViewType,
      setItemIncrement,
      setSortOrderBy,
      setSortDesc,
      setSort,
      handleSearchParamsChange,
    };
  },
  render() {
    const {
      sortParams,
      can, itemIncrement,
      viewType,
      shownInfo,
      setItemIncrement,
      handleAddFolder,
      setViewType,
      setShownInfo,
      setSortDesc,
      setSort,
      handleUploadRawFile,
      // handleUploadImage,
      // handleUploadPDF,
      handleUploadModel,
      handleUploadMaterial,
      isSearch,
    } = this;
    return <div class="modes-container">
      {
        // 设置每块文件的大小
      }
      <div class="mode-item mode-item-constant">
        <el-slider
          nativeOnDblclick={() => setItemIncrement(0)}
          class="size-picker"
          style="margin: 0 8px"
          show-tooltip={false}
          value={-itemIncrement}
          min={-2}
          max={2}
          step={1}
          onInput={(v) => setItemIncrement(-v)} />
      </div>

      {
        // 新建文件夹
      }
      {
        can.createFolder
          ? <el-tooltip
            content={i18n.t('file-list.new_folder')}
            effect="dark"
            placement="top"
            class={{ 'is-disabled': isSearch }}
          >
            <div class="mode-item" onClick={() => this.$act(handleAddFolder)}>
              <i class="iconfont icon-folder-add-o"></i>
            </div>
          </el-tooltip>
          : null
      }

      {
        // 排序
      }
      {
        <el-dropdown placement="bottom">
          <el-tooltip content={i18n.t('file-list.sort')} placement="top" class={{ 'is-disabled': isSearch }}>
            <div class="mode-item">
              <i class="iconfont icon-sort-2"></i>
            </div>
          </el-tooltip>
          <el-dropdown-menu slot="dropdown" style="width:165px">
            <el-dropdown-item
              // divided
              nativeOnClick={() => setSort({
                orderBy: 'gmt_modify_time',
              })}>
              <div class="dropdown-item-content">
                <span>{i18n.t('file-list.sort_options.order_by_modify_time')}</span>
                {
                  sortParams.orderBy === 'gmt_modify_time'
                    ? <i class="iconfont icon-tick" />
                    : null
                }
              </div>
            </el-dropdown-item>
            <el-dropdown-item
              nativeOnClick={() => setSort({
                orderBy: 'gmt_create_time',
              })}>
              <div class="dropdown-item-content">
                <span>{i18n.t('file-list.sort_options.order_by_create_time')}</span>
                {
                  sortParams.orderBy === 'gmt_create_time'
                    ? <i class="iconfont icon-tick" />
                    : null
                }
              </div>
            </el-dropdown-item>
            <el-dropdown-item
              nativeOnClick={() => setSort({
                orderBy: 'name',
              })}>
              <div class="dropdown-item-content">
                <span>{i18n.t('file-list.sort_options.order_by_name')}</span>
                {
                  sortParams.orderBy === 'name'
                    ? <i class="iconfont icon-tick" />
                    : null
                }
              </div>
            </el-dropdown-item>
            <el-dropdown-item
              divided
              nativeOnClick={() => setSort({
                desc: false,
              })}>
              <div class="dropdown-item-content">
                <span>{i18n.t('file-list.sort_options.asc')}</span>
                {
                  !sortParams.desc
                    ? <i class="iconfont icon-tick" />
                    : null
                }
              </div>
            </el-dropdown-item>
            <el-dropdown-item
              nativeOnClick={() => setSort({
                desc: true,
              })}>
              <div class="dropdown-item-content">
                <span>{i18n.t('file-list.sort_options.desc')}</span>
                {
                  sortParams.desc
                    ? <i class="iconfont icon-tick" />
                    : null
                }
              </div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      }


      {
        // 设置（面料下的）平铺、垂悬，以及文件的详情或简约
      }

      <el-dropdown placement="bottom">
        <el-tooltip content={i18n.t('file-list.view_mode')} placement="top">
          <div class="mode-item">
            <i class="iconfont icon-four-rect"></i>
          </div>
        </el-tooltip>
        {
          // 垂悬或平铺（面料限定）
        }
        <el-dropdown-menu slot="dropdown" style="width:136px">
          <el-dropdown-item
            nativeOnClick={() => setViewType('default')}>
            <div class="dropdown-item-content">
              <span>{i18n.t('file-list.viewMode.default')}</span>
              {
                viewType === 'default'
                  ? <i class="iconfont icon-tick" />
                  : null
              }
            </div>
          </el-dropdown-item>
          <el-dropdown-item
            nativeOnClick={() => setViewType('drape')}>
            <div class="dropdown-item-content">
              <span>{i18n.t('file-list.viewMode.drape')}</span>
              {
                viewType === 'drape'
                  ? <i class="iconfont icon-tick" />
                  : null
              }
            </div>
          </el-dropdown-item>
          <el-dropdown-item
            nativeOnClick={() => setViewType('flat')}>
            <div class="dropdown-item-content">
              <span>{i18n.t('file-list.viewMode.flat')}</span>
              {
                viewType === 'flat'
                  ? <i class="iconfont icon-tick" />
                  : null
              }
            </div>
          </el-dropdown-item>
          {
            // 详情或简约
          }
          <el-dropdown-item divided
            nativeOnClick={() => setShownInfo(false)}>
            <div class="dropdown-item-content">
              <span>{i18n.t('file-list.viewMode.simple')}</span>
              {
                !shownInfo
                  ? <i class="iconfont icon-tick" />
                  : null
              }
            </div>

          </el-dropdown-item>
          <el-dropdown-item
            nativeOnClick={() => setShownInfo(true)}>
            <div class="dropdown-item-content">
              <span>{i18n.t('file-list.viewMode.detail')}</span>
              {
                shownInfo
                  ? <i class="iconfont icon-tick" />
                  : null
              }
            </div>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>


      {
        // 上传相关
      }
      {
        <el-dropdown placement="bottom" vShow={can.upload || can.uploadMaterial || can.uploadModel || can.uploadImage || can.uploadPDF}>
          <el-tooltip content={i18n.t('file-list.upload')}
            placement="top"
            class={{ 'is-disabled': isSearch }}
          >
            <div class="mode-item">
              <i class="iconfont icon-upload"></i>
            </div>
          </el-tooltip>
          <el-dropdown-menu slot="dropdown" style="width:176px">
            {
              can.uploadModel
                ? <el-dropdown-item
                  nativeOnClick={() => this.$act(handleUploadModel)}>
                  <div class="dropdown-item-content">
                    <i class="iconfont icon-model" />
                    <span>{i18n.t('file-list.commands.upload_design')}</span>
                  </div>
                </el-dropdown-item>
                : null
            }
            {
              can.uploadMaterial
                ? <el-dropdown-item
                  nativeOnClick={() => this.$act(handleUploadMaterial)}>
                  <div class="dropdown-item-content">
                    <i class="iconfont icon-material-o" />
                    <span>{i18n.t('file-list.commands.upload_material')}</span>
                  </div>
                </el-dropdown-item>
                : null
            }
            {
              can.upload
                ? <el-dropdown-item
                  nativeOnClick={() => this.$act(handleUploadRawFile)}>
                  <div class="dropdown-item-content">
                    <i class="iconfont icon-upload" />
                    <span>{i18n.t('file-list.commands.upload')}</span>
                  </div>
                </el-dropdown-item>
                : null
            }
            {
              // can.uploadImage
              //   ? <el-dropdown-item
              //     disabled={self.$isActing(handleUploadImage)}
              //     nativeOnClick={() => self.$act(handleUploadImage)}>
              //     <div class="dropdown-item-content">
              //       <i class="iconfont icontupian"/>
              //       <span>{ i18n.t('file-list.commands.upload_image') }</span>
              //     </div>
              //   </el-dropdown-item>
              //   : null
            }
            {
              // can.uploadPDF
              //   ? <el-dropdown-item
              //     disabled={self.$isActing(handleUploadPDF)}
              //     nativeOnClick={() => self.$act(handleUploadPDF)}>
              //     <div class="dropdown-item-content">
              //       <i class="iconfont icontupianbeifen3"/>
              //       <span>{ i18n.t('file-list.commands.upload_pdf') }</span>
              //     </div>
              //   </el-dropdown-item>
              //   : null
            }

          </el-dropdown-menu>
        </el-dropdown>
      }

      {
        // 展示厅
      }
      {
        this.meetingId
          ? <el-tooltip content={i18n.t('virtual-meetup.home.show_room')}
            placement="top"
          >
            <div class="mode-item" style={{ color: '#606266' }} onClick={() => this.$act(openUpMetting)}>
              <i class="iconfont iconzhanshiting"></i>
            </div>
          </el-tooltip>
          : null
      }
      {this.$scopedSlots.default ? this.$scopedSlots.default() : null}
    </div>;
  },
});


export default NavigationTools;
