<template>
  <div class="icon-search-box" :class="{ 'is-input': isInputMode }" @click.stop>
    <i
      v-if="!isInputMode"
      class="iconfont icon-search button-search"
      @click="switchInputMode" />
    <el-input
      v-if="isInputMode"
      v-model="keyword"
      :placeholder="$t('workspace.search')"
      prefix-icon="iconfont icon-search"
      clearable
      ref="input"
      :disabled="isVirtualAccount"
      @input.native="debounceInput"
      @keyup.native.esc="switchSearchMode"
      @clear="resetKeyword">
    </el-input>

    <!-- 搜索结果显示框 -->
    <!-- <transition name="fadeInDown" appear> -->
    <div v-if="isInputMode && keyword" class="search-result-box">
      <section class="result-section">
        <h2 class="search-result-title"
          v-html="$t('workspace.buttonSearch.space_result', { keyword })">
          <!-- <i18n path="workspace.buttonSearch.space_result">
            <span place="keyword" class="keyword long text-one-line">{{ keyword }}</span>
          </i18n> -->
          <!-- Search results for "
          <span class="keyword long text-one-line">{{ keyword }}</span>" -->
        </h2>
        <ul v-if="searchResult.workspaces.length" class="search-result-list scrollable-y">
          <!-- <transition-group name="fadeInDown" mode="out-in" appear> -->
          <li
            v-for="workspace in searchResult.workspaces"
            :key="workspace.groupAppId"
            class="search-result-item">
            <WorkspaceItem :workspace="workspace" @trigger-click="isInputMode = false" />
          </li>
          <!-- </transition-group> -->
        </ul>
        <p class="empty-result-tip" v-else>{{ $t('workspace.buttonSearch.space_not_found') }}</p>
      </section>

      <section>
        <h2 class="search-result-title"
          v-html="$t('workspace.buttonSearch.member_result', { keyword })">
          <!-- "<span class="keyword short text-one-line">{{ keyword }}</span>"
           found in these workspaces -->
        </h2>
        <ul
          v-if="searchResult.members.length"
          class="search-result-list"
          :class="isShowAll ? 'scroll-content scrollable-y' : 'overflow-content'">
          <!-- <transition-group name="fadeInDown" mode="out-in" appear> -->
          <li
            v-for="member in searchResult.members"
            :key="member.groupAppId"
            class="search-result-item">
            <WorkspaceItem :workspace="member" @trigger-click="isInputMode = false" />
          </li>
          <!-- </transition-group> -->
        </ul>
        <p v-else class="empty-result-tip"
          v-html="$t('workspace.buttonSearch.member_not_found', { keyword })">
          <!-- <i18n path="workspace.buttonSearch.member_not_found">
            <span place="keyword" class="keyword short text-one-line">{{ keyword }}</span>
          </i18n> -->
        </p>
        <el-button
          v-if="searchResult.members.length > 2 && !isShowAll"
          class="result-more"
          type="text"
          @click="showMoreResult">
          {{ $t('workspace.buttonSearch.view_all') }}<i class="iconfont icon-angle-down" />
        </el-button>
      </section>
    </div>
    <!-- </transition> -->
  </div>
</template>

<script>
import { debounce } from 'throttle-debounce';

import { apiSearchWorkspaces } from '@/api/workspace';
import WorkspaceItem from '../fragments/WorkspaceItem';
import { getContextUser } from '../../../../loaders/context';


export default {
  name: 'ButtonSearchWorkspace',
  components: {
    WorkspaceItem,
  },
  data() {
    return {
      searchResult: {
        members: [],
        workspaces: [],
      },
      keyword: '',
      isInputMode: false,
      isShowAll: false,
    };
  },
  watch: {
    isInputMode(newVal) {
      // 根据是否搜索模式，捕获该组件外的点击事件；
      // 在搜索模式下，点击外部元素可退出搜索模式
      if (newVal) {
        document.getElementById('app').addEventListener('click', this.switchSearchMode);
      } else {
        document.getElementById('app').removeEventListener('click', this.switchSearchMode);
      }

      this.keyword = '';
      this.$emit('change-mode', newVal);
    },
  },
  computed: {
    isVirtualAccount() {
      return getContextUser()?.roleId === '3';
    },
  },
  methods: {
    switchSearchMode() {
      this.isInputMode = false;
    },
    async switchInputMode() {
      this.isInputMode = true;
      // 等待DOM渲染后才去触发focus事件
      await this.$nextTick();
      this.$refs.input.focus();
    },
    debounceInput: debounce(300, async function search() {
      if (this.keyword === '') {
        this.resetKeyword();
        return;
      }

      try {
        const { memberSeach, workspaceSeach } = (await apiSearchWorkspaces(this.keyword)).data;
        this.searchResult.members = memberSeach;
        this.searchResult.workspaces = workspaceSeach;
      } catch (err) {
        this.$errHandler(err);
      }
    }),
    // 显示全部的搜索结果
    showMoreResult() {
      this.isShowAll = true;
    },
    // 清空搜索关键字
    resetKeyword() {
      this.keyword = '';
      this.searchResult = {
        members: [],
        workspaces: [],
      };
    },
  },
};
</script>

