  position: absolute;
  z-index: 8;
  width: 264px;
  padding: 0 0 5px 0;
  margin: 10px 0 0 -5px;
  background: #fff;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-shadow: 0px 4px 8px 0px rgba(49, 51, 57, 0.15);

  .result-section {
    border-bottom: 1px solid #b9bdc2;
  }

  .search-result-list {
    max-height: 260px;
    transition: all linear 300ms;
    &.overflow-content {
      max-height: 104px;
      overflow: hidden;
    }
    &.scroll-content {
      max-height: 260px;
      overflow: auto;
    }
  }

  .search-result-title {
    display: flex;
    margin: 0 12px 0 12px;
    font-size: 14px;
    font-weight: 400;
    line-height: 3;
    color: #333;
    /deep/ .keyword {
      color: $color-primary;
      &.long {
        max-width: 112px;
      }
      &.short {
        max-width: 52px;
      }
    }
  }

  .empty-result-tip {
    display: flex;
    margin-left: 12px;
    font-weight: 300;
    line-height: 52px;
    color: #999;
    /deep/ .keyword {
      color: $color-primary;
      &.long {
        max-width: 112px;
      }
      &.short {
        max-width: 52px;
      }
    }
  }

  .result-more {
    width: 100%;
    font-size: 12px;
