<template>
  <section class="workspace-group-settings-container">
    <!-- 标题 -->
    <SettingsItem border="bottom">
      <h2 style="font-size: 20px">
        {{ $t("workspace.more") }}
      </h2>
      <span class="flex-center iconfont-wrapper" @click="$emit('cancel', null)">
        <i class="el-icon-close el-icon"></i>
      </span>
    </SettingsItem>

    <main style="width: 100%" class="scrollable-y">
      <transition name="workspace-rename" mode="out-in">
        <!-- 协同空间名称 -->
        <SettingsItem v-if="!renameMode" border="bottom">
          <span>
            {{ workspaceName }}
          </span>
          <span v-if="$can(groupAppId, 'OP0043')" class="flex-center iconfont-wrapper" @click="renameWorkspace">
            <i class="iconfont icon-rename" />
          </span>
        </SettingsItem>
      </transition>

      <!-- 协同空间成员展示和查看 -->
      <section class="workspace-members">
        <transition name="search-transition" mode="out-in">
          <!-- 非搜索模式下显示搜索按钮 -->
          <SettingsItem v-if="!searchMode" style="padding-bottom: 8px">
            <span> {{ $t("workspace.members") }}({{ groupMembers ? groupMembers.length : "" }}) </span>
            <div class="flex-center">
              <el-dropdown>
                <span class="flex-center iconfont-wrapper">
                  <i class="el-icon el-icon-circle-plus-outline"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    :disabled="!$can('workspace', 'OP0127')"
                    @click.native="$can('workspace', 'OP0127') && handleClickAddMembers()"
                    class="flex-start flex-vertical-center"
                  >
                    <!-- <i class="iconfont iconws-add-addpeople icon-group"></i> -->
                    <span class="flex-center" style="width: 25px; height: 25px">
                      <i class="iconfont iconws-add-addpeople icon-group"></i>
                    </span>
                    <span>
                      {{ $t("workspace.add_members") }}
                    </span>
                  </el-dropdown-item>
                  <el-dropdown-item
                    :disabled="!$can(groupAppId, 'OP0029')"
                    @click.native="$can('workspace', 'OP0029') && handleClickInvitePeoples()"
                    class="flex-start flex-vertical-center"
                  >
                    <span class="flex-center" style="width: 25px; height: 25px">
                      <i class="iconfont iconws-add-InviteAccount icon-group"></i>
                    </span>
                    <span>
                      {{ $t("workspace.invite_people") }}
                    </span>
                  </el-dropdown-item>
                  <el-dropdown-item
                    v-if="currentWorkspace.businessType !== 1 && user.businessType !== 1"
                    :disabled="!$can(groupAppId, 'OP0131')"
                    @click.native="$can('workspace', 'OP0131') && handleClickCreateWorkspacePass()"
                    class="flex-start flex-vertical-center"
                  >
                    <span class="flex-center" style="width: 25px; height: 25px">
                      <i class="iconfont iconws-add-CreateWorkspacePass icon-group"></i>
                    </span>
                    <span>
                      {{ $t("workspace.create_workspace_pass") }}
                    </span>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
              <span
                class="flex-center iconfont-wrapper"
                @click="
                  searchMode = true;
                  $nextTick(() => $refs.memberSearch.focus());
                "
              >
                <i class="el-icon-search el-icon"></i>
              </span>
            </div>
          </SettingsItem>

          <!-- 搜索模式下显示搜索栏和取消字样 -->
          <SettingsItem v-else class="search-bar-wrapper flex-space-between" style="padding-bottom: 8px">
            <el-input ref="memberSearch" class="search-bar" size="small" v-model="memberKeyword" />
            <span
              class="search-cancel"
              @click="
                searchMode = false;
                memberKeyword = '';
              "
            >
              {{ $t("public.dialog.cancel") }}
            </span>
          </SettingsItem>

          <!-- 搜索结果展示 -->
        </transition>
        <WorkspaceGroupMembers
          :groupMembers="groupMembers"
          :searchMode="memberSearchMode"
          :membersLoading="membersLoading"
          :groupAppId="groupAppId"
        />
      </section>

      <!-- 协同空间成员管理 -->
      <!-- <SettingsItem border='top' class="clickable-item"
        @click="$act(handleClickWorkspaceManagement, [groupAppId, workspaceName])">
        <span>Workspace management</span>
        <span class="flex-center iconfont-wrapper">
          <i class="el-icon-arrow-right"></i>
        </span>
      </SettingsItem> -->

      <!-- 开启/关闭水印 -->
      <SettingsItem border="top" v-if="$can(groupAppId, 'OP0162')">
        <span>
          {{ watermarkFlag ? $t("workspace.watermark_on") : $t("workspace.watermark_off") }}
          <el-tooltip :content="$t('workspace.watermark_tip')">
            <i class="i-help_nol" />
          </el-tooltip>
        </span>
        <el-switch @change="handTriggerWatermark" :value="watermarkFlag"> </el-switch>
      </SettingsItem>

      <!-- 解散协同空间 -->
      <SettingsItem
        border="top"
        v-if="$can(groupAppId, 'OP0040')"
        class="clickable-item"
        @click="$act(handleDeleteWorkspace, [], $t('workspace.delete_ws_success_tip'))"
      >
        <span>
          {{ $t("workspace.delete_workspace") }}
        </span>
        <span class="flex-center iconfont-wrapper">
          <i class="el-icon-arrow-right"></i>
        </span>
      </SettingsItem>

      <!-- 离开协同空间 -->
      <SettingsItem
        border="top settings-item-border-bottom"
        v-if="$can(groupAppId, 'OP0058')"
        class="clickable-item"
        @click="$act(handleLeftWorkspace, [], $t('workspace.left_workspace_success_tip'))"
      >
        <span>
          {{ $t("workspace.left_workspace") }}
        </span>
        <span class="flex-center iconfont-wrapper">
          <i class="el-icon-arrow-right"></i>
        </span>
      </SettingsItem>
    </main>
  </section>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import axios from '@/api/axios';

import { debounce } from 'throttle-debounce';
import i18n from 'i18next';
import nameInputValidator from '@/utils/validators/checkers/valid-name';
import { prompt } from '@/components/index';
import {
  fetchGroupMembers,
} from '@/api/account';
import {
  getWorkspaceConfig,
  updateWorkspaceWatermart,
} from '@/api/permission';
import { WorkspaceUserRoleOrm, WorkspaceOrm, WorkspaceMemberOrm } from '@/models';
import { delWorkspace, renameWorkspace, quitWorkspace } from '@/loaders/workspace';
import { addMembers, refreshByWorkspaceId, getMemberInfoByUserId } from '@/loaders/workspace-member';
import { updateWorkspaceRolesAndPermissions, getContextUser } from '../../../../loaders/context';
import { addUsedWorkspace } from '../../../../loaders/user';
import SettingsItem from './SettingsItem';

import WorkspaceGroupMembers from './WorkspaceGroupMembers';
import MemberManagement from './MemberManagement';
import MemberInvitationWrapper from './MemberInvitationWrapper';
// import MemberSelector from './MemberSelector';
import MemberAdd from './MemberAdd';

export default defineComponent({
  name: 'WorkspaceGroupSettings',
  props: ['currentWorkspace'],
  components: {
    SettingsItem,
    WorkspaceGroupMembers,
  },
  computed: {
    firstWorkspace() {
      return WorkspaceOrm.query().get()[0];
    },
    groupAppId() {
      return this.currentWorkspace.groupAppId;
    },
    /** @return { Object } */
    user() {
      return getContextUser();
    },
    /** @return { string | undefined } */
    workspaceName() {
      return this.currentWorkspace?.name;
    },
    /** @return { boolean } */
    memberSearchMode() {
      return !!this.memberKeyword.trim();
    },
    /** @return { string } */
    selfRoleId() {
      return WorkspaceUserRoleOrm.find(this.groupAppId).roleId;
    },
  },
  data() {
    // eslint-disable-next-line no-console
    this.console = console.dir.bind(window);
    return {
      renameMode: false,
      workspaceNewName: '',
      searchMode: false,
      memberKeyword: '',

      groupMembers: null,
      membersLoading: false,
      watermarkFlag: true,
    };
  },
  methods: {
    async enterFirstWorkspace() {
      await this.enterWorkspaceByReplaceURL(this.firstWorkspace.groupAppId);
    },
    // 重命名workspace
    async renameWorkspace() {
      // const formData = await this.$Dialog.dialogify({
      //   is: DialogWorkspaceCreate,
      //   props: {
      //     name: this.workspaceName,
      //   },
      // }, {
      //   props: {
      //     title: i18n.t('workspace.workspace_name'),
      //     width: '400px',
      //     titleAlign: 'left',
      //     closeOnClickModal: false,
      //   },
      // });
      let newName = this.workspaceName;
      newName = ((await prompt('', {
        title: i18n.t('public.dialog.rename'),
        inputPlaceholder: i18n.t('public.file.input_file_name'),
        confirmButtonText: i18n.t('public.dialog.confirm'),
        cancelButtonText: i18n.t('public.dialog.cancel'),
        inputValue: newName,
        closeOnClickModal: false,
        inputValidator: (input) => nameInputValidator(input, { min: 1, max: 100 }),
      }))).value;
      this.currentWorkspace.name = newName;
      renameWorkspace(this.groupAppId, newName);
    },
    async handleClickAddMembers() {
      const result = await this.$Dialog.dialogify({
        is: MemberAdd,
        props: {
          groupAppId: this.groupAppId,
        },
      }, {
        key: this.groupAppId,
        props: {
          titleAlign: 'left',
          width: '700px',
          title: i18n.t('workspace.add_members'),
        },
      });
      if (typeof result === 'string') {
        this.handleClickInvitePeoples();
        return;
      }

      try {
        await addMembers(
          this.groupAppId, result.map((member) => member.id),
        );
        this.$message.success(i18n.t('workspace.add_members_success_tip'));
        this.refreshMembers();
      } catch (err) {
        this.$errHandler(err);
      }
    },
    async handleClickInvitePeoples() {
      await this.$Dialog.dialogify({
        is: MemberInvitationWrapper,
        props: {
          key: this.groupAppId,
          groupAppId: this.groupAppId,
          spaceRoleId: this.selfRoleId,
          modeInvite: true,
        },
      });
      this.refreshMembers();
    },
    async handleClickCreateWorkspacePass() {
      await this.$Dialog.dialogify({
        is: MemberInvitationWrapper,
        props: {
          key: this.groupAppId,
          groupAppId: this.groupAppId,
          spaceRoleId: this.selfRoleId,
          modeInvite: false,
        },
      });
      this.refreshMembers();
    },
    searchMembersWrapper() {
      this.searchMembers(this.memberKeyword);
    },
    async searchMembers(memberKeyword) {
      this.membersLoading = true;
      const members = (await fetchGroupMembers({
        keyword: memberKeyword,
        groupAppId: this.groupAppId,
      })).data;
      if (members.length) {
        this.groupMembers = members.map(member => {
          return getMemberInfoByUserId(
            this.groupAppId,
            member.id,
          );
        }).reduce((a, b) => [...a, ...b]);
      } else {
        this.groupMembers = [];
      }
      this.membersLoading = false;
    },
    async refreshMembers() {
      this.membersLoading = true;
      const members = await refreshByWorkspaceId(this.groupAppId);
      if (members.length) {
        this.groupMembers = members;
      } else {
        this.groupMembers = [];
      }
      this.membersLoading = false;
    },
    handleRenameWorkspace(groupAppId, workspaceNewName) {
      if (workspaceNewName.length >= 64) {
        this.$message.error(i18n.t('workspace.input_exceed_limit_tip'));
        return;
      }
      renameWorkspace(groupAppId, workspaceNewName);
    },
    async handleClickWorkspaceManagement(groupAppId, title) {
      await this.$Dialog.dialogify({
        is: MemberManagement,
        props: {
          groupAppId,
          title,
        },
        on: {
          // 当成员管理有增删改操作时，要通知这里更新内容
          refreshMembers: () => { this.refreshMembers(); },
        },
      }, {
        props: {
          title,
          titleAlign: 'left',
          width: '700px',
        },
      });
    },
    async handleDeleteWorkspace() {
      const h = this.$createElement;
      const vnode = h('p', {
        domProps: {
          innerHTML: i18n.t('workspace.delete_workspace_confirm', {
            workspaceName: this.workspaceName,
          }),
        },
      });
      const res = await this.$alert(vnode,
        i18n.t('workspace.delete_workspace_title'),
        {
          confirmButtonText: i18n.t('workspace.delete_workspace'),
          // confirmButtonClass: 'el-button--default',
          type: 'warning',
        });
      if (res) {
        addUsedWorkspace(-1);
        delWorkspace(this.groupAppId);
        this.enterFirstWorkspace();
        this.$emit('cancel');
      }
    },
    async handleLeftWorkspace() {
      const res = await this.$alert(i18n.t('workspace.left_workspace_confirm'),
        i18n.t('workspace.prompt'),
        {
          confirmButtonText: i18n.t('public.dialog.confirm'),
          type: 'warning',
          showCancelButton: true,
          cancelButtonText: i18n.t('public.dialog.cancel'),
          iconClass: 'iconfont icon-help-circle',
          confirmButtonClass: 'el-button--danger',
          cancelButtonClass: '',
        });
      if (res) {
        quitWorkspace(this.groupAppId);
        this.enterFirstWorkspace();
        this.$emit('cancel');
      }
    },
    // 修改水印开启状态
    async handTriggerWatermark() {
      await updateWorkspaceWatermart(this.groupAppId, +!this.watermarkFlag);
      this.watermarkFlag = !this.watermarkFlag;
    },
    // 获取ws配置
    async getWorkspaceConfig() {
      this.watermarkFlag = !!(await getWorkspaceConfig(this.groupAppId)).data.openWatermark;
    },
  },
  async created() {
    await this.refreshMembers();
    // await refreshByWorkspaceId(this.groupAppId);
    this.debounceSearchMembers = debounce(300, this.searchMembers);
    await updateWorkspaceRolesAndPermissions(this.groupAppId);
    // this.searchMembersWrapper();
    console.info(this.groupAppId);
    await this.getWorkspaceConfig();
  },
  watch: {
    memberKeyword(newVal) {
      this.debounceSearchMembers(newVal);
    },
  },
});
</script>


