<template>
  <div :draggable="isDraggable"
    v-if="!isHidden"
    :data-is-droppable="isDroppable"
    :class="['tree', 'host', {
      'is-root': isRoot,
      'is-dragging': isDragging,
      'is-opened': isOpened,
      'is-droppable': isDroppable,
    }, typeof insertIndex === 'number' ? `is-dragover` : '']"
    @dragstart.self="handleDragStart"
    @dragover.prevent="handleDragOver"
    @dragend.self="handleDragEnd">
    <div class="indent" v-if="!isRoot && data[valueKey]">
      <i class="loading el-icon-loading" v-if="pendingChildren[data[valueKey]]" />
      <i
        v-else-if="isExpandable"
        class="arrow el-icon-arrow-right"
        @click.stop="toggle(data)"></i>
      <i class="indent-line"></i>
    </div>
    <div class="content">
      <div
        class="item-wrapper"
        v-if="!isRoot && data[valueKey]"
        v-on="isDroppable ? {
          dragenter: handleDragEnter,
          dragleave: handleDragLeave,
          drop: handleDrop
        } : {}"
        @contextmenu.stop="handleContextmenu($event)">
        <!--
          @slot 默认的slot, 传递了一系列的scopedData，请自行查看
        -->
        <slot v-bind="scopedData">
          <div class="item">
            {{data[labelKey]}}
          </div>
        </slot>
      </div>
      <el-collapse-transition
        v-if="loadedChildren[data[valueKey]]">
        <div class="sublist-wrapper" v-show="innerOpened">
          <transition-group name="list" tag="div" class="sublist">
            <div class="child"
              :key="item[valueKey] || i"
              v-for="(item, i) in loadedChildren[data[valueKey]]">
              <tree
                :opened="openedItems"
                :load="load"
                :hidden="hidden"
                :draggable="draggable"
                :droppable="droppable"
                :expandable="expandable"
                :label-key="labelKey"
                :dragging-data="draggingData"
                :value-key="valueKey"
                :loadedChildren="loadedChildren"
                :pendingChildren="pendingChildren"
                @update:draggingData="updateDraggingData"
                @drop="(v) => $emit('drop', v)"
                @contextmenu="(e, v) => $emit('contextmenu', e, v)"
                :indexs="[...indexs, i]"
                :ancestors="nodes"
                :data="item">
                <template v-for="slot in Object.keys($scopedSlots)" v-slot:[slot]="scope">
                  <slot :name="slot" v-bind="scope" />
                </template>
              </tree>
              <div
                class="hr before"
                :class="{'active': insertIndex === i }"
                v-on="{
                  dragenter: (e) => handleDragEnter(e, i),
                  dragleave: handleDragLeave,
                  drop: (e) => handleDrop(e, i)
                }"><span /></div>
              <div class="hr after"
                :class="{'active': insertIndex === i + 1 }"
                v-on="{
                  dragenter: (e) => handleDragEnter(e, i + 1),
                  dragleave: handleDragLeave,
                  drop: (e) => handleDrop(e, i + 1)
                }"><span /></div>
            </div>

          </transition-group>

          <div class="other"
            v-if="$scopedSlots.other && isShowOther"
            @contextmenu.stop="handleOtherContextmenu"
            @dragstart.prevent draggable="true">
            <div class="host">
              <div class="indent">
                <div class="indent-line" />
              </div>
              <div class="content">
                <!-- @slot 其他slot，用于显示除了树之外的内容 -->
                <slot name="other" v-bind="scopedData" />
              </div>
            </div>
          </div>
          <!-- @slot 空的slot，如果没有子项，显示的slot -->
          <slot name="empty" v-else-if="!loadedVisibleChildrenLength" />
        </div>
      </el-collapse-transition>

    </div>
  </div>
</template>
<script>

import delay from '_/delay';

/**
 * 这个组件用于显示一个树状结构，包含拖动，右键等功能
 *
 * @template N
 */

export default {
  name: 'Tree',
  props: {
    /**
     * 树的根节点数据，一个对象
     * @type {object}
     */
    data: {
      type: Object,
    },
    /**
     * 树是否可以释放，布尔值或者传递一个返回布尔值的function，参数为释放的节点
     * @type {number | string}
     */
    droppable: {
      type: [Boolean, Function],
      default: false,
    },
    /**
     * 树是否可以拖动，布尔值或者传递一个返回布尔值的function，参数为拖动的节点
     */
    draggable: {
      type: [Boolean, Function],
      default: false,
    },
    /**
     * 一个加载子节点的function
     * @type { <T>(item: T) => Promise<T[]> | T}
     */
    load: {
      type: Function,
      default: async function load(item) {
        await delay(500);
        return item.children;
      },
    },
    /**
     * 树是否隐藏，传递布尔值，或者一个返回布尔值的function，参数为需要判断隐藏的节点
     */
    hidden: {
      type: [Boolean, Function],
      default: () => function hidden(item) {
        return item.hidden;
      },
    },
    /**
     * 树是否显示其他，传递布尔值，或者一个返回布尔值的function，参数为需要判断是否显示其他的节点
     */
    showOther: {
      type: [Boolean, Function],
      default: () => function showOther() {
        return false;
      },
    },
    /**
     * 树是否可展开，传递布尔值，或者一个返回布尔值的function，参数为需要判断是否可展开的节点
     */
    expandable: {
      type: [Boolean, Function],
      default: () => function checkChildrenExistence(item) {
        return item.children && item.children.length;
      },
    },
    /**
     * 正在拖动的项目的数据，需要一个对象，并且有一个data的字段为item，用于外部修改内部的拖动内容
     */
    draggingData: {
      default: null,
    },
    /**
     * 树是否可以展开，传递布尔值或者一个打开的items的数组
     */
    opened: {
      default: () => [],
    },
    /**
     * 树状用于显示内容的key，默认为label，如果传递了default slot，则需要自定义显示的内容
     */
    labelKey: {
      default: 'label',
    },
    /**
     * 用于缓存一些内部数据用的参数，需要传递节点的id的key，默认为"id"
     */
    valueKey: {
      default: 'id',
    },
    /**
     * @ignore
     */
    // 这两个内部使用
    indexs: {
      type: Array,
      default: () => [],
    },
    /**
     * @ignore
     */
    ancestors: {
      type: Array,
      default: () => [],
    },

    loadedChildren: {
      type: Object,
      default: () => ({}),
    },

    pendingChildren: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    // console.log(this.data, this.indexs.length);
    // console.log(222);
    return {
      openedItems: this.getOpenedItems(this.opened),
      insertIndex: null,
      innerOpened: undefined,
    };
  },
  computed: {
    /**
     * A song
     * @typedef {Object} ScopedData
     * @property {string} isOpened - The title
     * @property {string} artist - The artist
     * @property {number} year - The year
     */
    scopedData() {
      return {
        isOpened: this.isOpened,
        isExpandable: this.isExpandable,
        isDraggable: this.isDraggable,
        isDroppable: this.isDroppable,
        data: this.data,
        indexs: this.indexs,
        nodes: this.nodes,
        ancestors: this.ancestors,
        parent: this.parent,
        index: this.index,
      };
    },
    parent() {
      return this.ancestors[this.ancestors.length - 1];
    },
    nodes() {
      return [...this.ancestors, this.data];
    },
    index() {
      if (this.indexs.length) {
        return this.indexs[this.indexs.length - 1];
      }
      return undefined;
    },
    isRoot() {
      return !this.indexs.length;
    },
    isDragging() {
      if (!this.draggingData) return false;
      return this.draggingData.data === this.data;
    },
    isShowOther() {
      return typeof this.showOther === 'function' ? this.showOther(this.data) : !!this.showOther;
    },
    isHidden() {
      return typeof this.hidden === 'function' ? this.hidden(this.data) : !!this.hidden;
    },
    isExpandable() {
      return typeof this.expandable === 'function' ? this.expandable(this.data) : !!this.expandable;
    },
    isDraggable() {
      return typeof this.draggable === 'function' ? this.draggable(this.data) : !!this.draggable;
    },
    isDroppable() {
      const s = { from: this.draggingData, to: { data: this.data, getChildren: this.load } };
      return typeof this.droppable === 'function' ? this.droppable(s) : !!this.droppable;
    },
    isOpened() {
      return this.isRoot || this.openedItems.includes(this.data);
    },
    loadedVisibleChildrenLength() {
      const children = this.loadedChildren[this.data[this.valueKey]];
      if (!children) return 0;
      if (typeof this.hidden !== 'function') {
        return this.hidden ? 0 : children.length;
      }
      return children.length - children.filter(this.hidden).length;
    },
  },
  methods: {
    async loadChildren(item) {
      const id = item[this.valueKey];
      if (!this.loadedChildren[id]) {
        let loadPromise = this.pendingChildren[id];
        if (!loadPromise) {
          loadPromise = this.load(item);
          this.$set(this.pendingChildren, id, loadPromise);
        }
        const children = await loadPromise;
        try {
          this.$set(this.loadedChildren, id, children);
        } catch (e) {
          console.error(e);
        } finally {
          this.$set(this.pendingChildren, id, null);
        }
        await this.$nextTick();
      }
      return this.loadedChildren[id];
    },
    getOpenedItems(opened) {
      if (Array.isArray(opened)) {
        return opened;
      }
      if (opened) {
        return [this.data];
      }
      return [];
    },
    toggle(item, opened) {
      if (opened && !this.isExpandable) {
        return;
      }
      const index = this.openedItems.indexOf(item);
      if (opened === undefined) {
        opened = index === -1;
      }
      if (Array.isArray(this.opened)) {
        if (opened) {
          this.openedItems.push(item);
        } else {
          this.openedItems.splice(index, 1);
        }
      } else {
        /**
         * 更新opened的事件，如果opend本来是数组，则不会触发
         * @event update:opened
         * @param {Boolean} opened 是否打开
         */
        this.$emit('update:opened', opened);
      }
    },
    handleOtherContextmenu(ev) {
      if (this.$listeners.othercontextmenu) {
        ev.preventDefault();
        /**
         * 其他项的右键事件
         * @event othercontextmenu
         * @param {ScopedData} scopedData 一个包含大部分节点数据的结构
         */
        this.$emit('othercontextmenu', ev, this.scopedData);
      }
    },
    handleContextmenu(ev) {
      if (this.$listeners.contextmenu) {
        ev.preventDefault();
        /**
         * 右键事件
         * @event contextmenu
         * @param {ScopedData} scopedData 一个包含大部分节点数据的结构
         */
        this.$emit('contextmenu', ev, this.scopedData);
      }
    },
    handleDragOver(ev) {
      ev.dataTransfer.dropEffect = 'copy';
    },
    handleDragLeave(e) {
      if (this._enterElement === e.target) {
        this.clearOpenTimeout();
        this.insertIndex = null;
      }
    },
    setOpenTimeout() {
      this._openTimeout = setTimeout(() => {
        if (!this.isOpened && this.isExpandable) {
          this.toggle(this.data, true);
        }
      }, 1000);
    },
    clearOpenTimeout() {
      if (this._openTimeout) {
        clearTimeout(this._openTimeout);
      }
    },
    handleDragEnter(e, index = 0) {
      e.dataTransfer.dropEffect = 'copy';
      this._enterElement = e.target;
      this.insertIndex = index;
      this.clearOpenTimeout();
      this.setOpenTimeout();
    },
    async handleDragEnd() {
      await this.$nextTick();
      this.clearOpenTimeout();
      this.insertIndex = null;
      this.updateDraggingData(null);
    },
    async handleDrop(e, insertIndex) {
      e.preventDefault();
      e.stopPropagation();
      let moveIndex = insertIndex;

      const from = this.draggingData;

      const { index, parent } = from;

      const target = this.data;

      const children = await this.loadChildren(target);
      const before = children[moveIndex - 1];
      const after = children[moveIndex];

      if (parent === target) {
        if (moveIndex > index) {
          moveIndex -= 1;
          if (moveIndex < 0) moveIndex = 0;
        }
        if (index === moveIndex) return;
      }

      /**
       * 释放事件
       * @event drop
       * @param { ({ from: object, to: object }) } dropData 释放需要的数据，from为拖动的节点，to为释放到的节点
       */
      this.$emit('drop', {
        from,
        to: {
          ...this.scopedData, before, after, index: insertIndex, moveIndex,
        },
      });
      this.handleDragEnd();
    },
    updateDraggingData(v) {
      /**
       * 更新拖动的事件
       * @event update:draggingData
       * @param { {data: object} | null } scopedData 拖动的数据
       */
      this.$emit('update:draggingData', v);
    },
    handleDragStart(e) {
      if (this.draggingData) {
        return;
      }
      // e.dataTransfer.effectAllowed = 'move';
      e.dataTransfer.dropEffect = 'copy';
      this.updateDraggingData({
        parent: this.parent,
        data: this.data,
        index: this.index,
        indexs: this.indexs.slice(),
        getChildren: this.load,
      });
    },
  },
  watch: {
    opened(opened) {
      this.openedItems = this.getOpenedItems(opened);
    },
    isOpened: {
      immediate: true,
      async handler(opened) {
        if (opened) {
          const item = this.data;
          await this.loadChildren(item);
          this.innerOpened = true;
        } else {
          this.innerOpened = false;
        }
      },
    },
  },
};
</script>
<style lang="scss" scoped>
$primaryColor: #4c8eff;
$hoverColor: #d7e4fb;
$borderColor: #eee;
$columnHeight: 32px;
$sortLineHeight: 18px;

