  position: relative;
  flex: 1;
  &:before {
    content: "";
    position: absolute;
    left: 17px;
    right: auto;
    bottom: 0;
    top: 0;
    border-style: dashed;
    border-width: 1px;
    border-color: darken($borderColor, 5%);
    border-left-color: transparent;
  }
  .is-opened
    > .content
    > .sublist-wrapper
    > .sublist
    > .child
    > .host
    > .indent
    & {
    display: block;
  }
}
.other {
  margin-top: -$sortLineHeight/2;
}
.item {
  cursor: pointer;
  padding-bottom: 7px;
  padding-top: 7px;
  padding-left: 5px;
  padding-right: 5px;
  overflow: hidden;
  position: relative;
  border-radius: 2px 2px 0 0;
  border-bottom: 1px solid;
  border-color: $borderColor;
  &:focus {
    outline: none;
  }
  &:hover {
    background-color: $hoverColor;
    color: $primaryColor;
    border-color: darken($hoverColor, 10%);
  }
}

