//   .content {
//     color: $primaryColor;
//     background-color: $hoverColor;
//   }
//   .item {
//     border-color: darken($hoverColor, 10%);
//   }
//   .indent-line {
//     border-color: darken($hoverColor, 10%);
//   }
//   .item {
//     border-color: darken($hoverColor, 10%);
//   }
// }

.host.is-droppable {
  > .content > .sublist-wrapper > .sublist > .child > .hr {
    display: block;
  }
}

.host.is-dragover {
  > .content > .item-wrapper > .item {
    color: $primaryColor;
  }
  > .content > .sublist-wrapper > .sublist > .child > .host {
    > .indent .indent-line:before {
      border-left-color: darken($primaryColor, 10%);
      border-right-color: darken($primaryColor, 10%);
    }
  }
}

.indent {
  width: 34px;
  display: flex;
  flex-direction: column;
}
