  transition: transform 0.3s;
  font-weight: 300;
  cursor: pointer;
}
.is-opened > .indent > .arrow {
  transform: rotate(90deg);
}

.is-dragging {
  cursor: move;
  > * {
    cursor: move;
    pointer-events: none;
  }
  > .content {
    opacity: 0.5;
  }
}

.content {
  flex: 1;
  position: relative;
}
.child {
  position: relative;
}

.hr {
  display: none;
  &.active {
    span,
    &:before,
    &:after {
      border-color: darken($primaryColor, 10%);
    }
  }
  &.after {
    display: none;
    bottom: -$sortLineHeight/2;
  }
  &.before {
    top: -$sortLineHeight/2;
  }
  .child:first-child > &.before {
    &:before,
    &:after {
      top: $sortLineHeight/2;
    }
  }
  .child:last-child > &.after {
    &:before,
    &:after {
      bottom: $sortLineHeight/2;
      margin-bottom: 0;
    }
  }
  * {
    pointer-events: none;
  }
  position: absolute;
  padding-top: $sortLineHeight/2-1px;
  padding-bottom: $sortLineHeight/2-1px;
  height: $sortLineHeight;
  left: 17px;
  right: 0;
  span {
    display: block;
    border-top: 2px dashed transparent;
  }
  &:before,
  &:after {
    display: inline-block;
    bottom: $sortLineHeight/2/2;
