import {
  computed, Ref, watch, ref,
} from '@vue/composition-api';
import { Key2ValueType } from '@/models/Setting';
import { getSetting, setSetting } from '@/loaders/setting';

function useSetting<K extends keyof Key2ValueType, T>(key: K):
  Ref<NonNullable<Key2ValueType[K]> | undefined>;
function useSetting<
K extends keyof Key2ValueType,
T extends Key2ValueType[K]>(key: K, defaults: T):
  Ref<NonNullable<Key2ValueType[K]> | T>;
function useSetting<
K extends keyof Key2ValueType,
T extends Key2ValueType[K]>(key: K, defaults ?: T) {
  const k = ref<T | Key2ValueType[K] | undefined>(undefined);
  watch(() => {
    return getSetting(key, defaults);
  }, (v) => {
    // @ts-ignore
    k.value = v;
  }, {
    immediate: true,
  });
  return computed<T | Key2ValueType[K] | undefined>({
    // @ts-ignore
    get() {
      return k.value;
    },
    set(v) {
      setSetting(key, v);
    },
  });
}

export { useSetting };

export default useSetting;
