/**
 * 该文件记录与系统权限相关的接口
 */

import axios from './axios';

/* ========================= 系统权限 ============================ */
/* ======================== 获取 system 权限相关 ============================ */
/**
 * 登录用户系统权限列表
 * @returns {Promise}
 */
export const getSystemPermissionList = () => {
  return axios({
    method: 'get',
    url: '/authcenter/operation/auth/system',
  });
};

/* ======================== 获取 workspace 权限相关 ============================ */
/**
 * 单个workspace的权限列表
 * @param {String} groupAppId workspaceId
 * @returns {Promise}
 */
export const getWorkpacePermissionList = (groupAppId) => {
  return axios({
    method: 'get',
    url: '/authcenter/operation/auth/workspace',
    headers: {
      groupAppId,
    },
  });
};

/**
 * 获取 workspace 所属公司的名称； 没有则返回用户的名称
 * @param {String} groupAppId workspaceId
 * @returns {Promise}
 */
export const getWorkpaceCompnayInfo = (groupAppId) => {
  return axios({
    method: 'get',
    url: '/authcenter/manage/groupapp/company',
    headers: {
      groupAppId,
    },
  });
};

/**
 * 获取登录人拥有的workpace个数
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const ApiGroupappList = (id) => {
  return axios({
    method: 'get',
    url: '/authcenter/manage/groupapp/list',
    params: {
      appId: id,
    },
  });
};

/**
 * 权限下拉选择
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const ApiroleList = (id) => {
  return axios({
    method: 'get',
    url: '/authcenter/role/list',
    params: {
      appId: id,
    },
  });
};

/**
 * 修改权限下拉选择
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const ApiUpdateRole = (data, id) => {
  return axios({
    method: 'post',
    url: '/authcenter/role/change',
    headers: {
      groupAppId: id,
    },
    data,
  });
};

/**
 * 移除账号
 * @param {String} id workspaceId
 * @param {String} userId 用户id
 * @returns {Promise}
 */
export const ApiRemoveAccount = (id, userId) => {
  return axios({
    method: 'post',
    url: '/authcenter/ugroup/remove',
    headers: {
      groupAppId: id,
      targetIds: JSON.stringify([userId]),
    },
  });
};

/**
 * 获取单个workspace的列表数据
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const ApiSingleWorkpaceTable = (id) => {
  return axios({
    method: 'get',
    url: '/authcenter/ugroup/users',
    headers: {
      groupAppId: id,
    },
  });
};

/**
 * 获取workspace的配置
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const getWorkspaceConfig = (id) => {
  return axios({
    method: 'get',
    url: '/authcenter/config/workspace/byWorkspaceId',
    params: {
      workspaceId: id,
    },
  });
};

/**
 * 获取workspace的配置（个人）
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const getWorkspaceConfigSelf = (id) => {
  return axios({
    method: 'get',
    url: '/authcenter/config/workspace/needOpenWatermark',
    params: {
      workspaceId: id,
    },
  });
};

/**
 * 更变workspace的水印配置
 * @param {String} id workspaceId
 * @param {Number} openWatermark 1开启，0关闭
 * @returns {Promise}
 */
export const updateWorkspaceWatermart = (id, openWatermark) => {
  return axios({
    method: 'post',
    url: '/authcenter/config/workspace/openWatermark',
    headers: {
      groupAppId: id,
    },
    data: {
      workspaceId: id,
      openWatermark,
    },
  });
};

/**
 * 解散workspace
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const ApiDismissWorkpace = (id) => {
  return axios({
    method: 'post',
    url: '/authcenter/ugroup/clear',
    headers: {
      groupAppId: id,
    },
  });
};

/**
 * 用户workspace统计信息
 * @returns {Promise}
 */
export const ApiGetTotalWorkpaces = () => {
  return axios({
    method: 'get',
    url: '/authcenter/ugroup/self/detail',
  });
};

/**
 *  重命名workspace
 * @param {String} id workspaceId
 * @param {String} name 新名称
 * @returns {Promise}
 */
export const ApiRenameWorkspace = (id, name) => {
  return axios({
    method: 'post',
    url: '/authcenter/manage/groupapp/rename',
    headers: {
      groupAppId: id,
    },
    data: {
      name,
    },
  });
};

/**
 *  新增workspace
 * @param {String} name 名称
 */
export const ApiAddWorkspace = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/manage/groupapp/add',
    data,
  });
};

/**
 * 退出workspace
 * @param {String} id workspaceId
 * @returns {Promise}
 */
export const ApiQuitWorkpace = (id) => {
  return axios({
    method: 'post',
    url: '/authcenter/ugroup/remove/self',
    headers: {
      groupAppId: id,
    },
  });
};

/**
 * 权限角色
 * @param {String}
 * @returns {Promise}
 */
export const getDictionaryByType = () => {
  return axios({
    method: 'get',
    url: '/authcenter/role/list',
    params: {
      appId: 2,
    },
  });
};
