import axios from '@/utils/axios';
import {
  reloadModelRendererUrl,
} from '@/api/catalogue';
import exec from '@/utils/exec';
import isQt from '@/utils/is-qt';
import { openSingleton } from '@/utils/open-window';
/**
 * @typedef { import("@/models/Catalog").CatalogModel } CatalogModel
 */
const operationMixin = {
  props: {
    workspaceId: {
      default() {
        return this.$route.query.relateId;
      },
    },
  },
  methods: {
    /**
     *
     * @param { Pick<CatalogModel, 'relateId' | 'relateType' | 'id' | 'name' | 'workspaceId'> } catalog
     * @param { { showAnnotation?: boolean, commentDialogOpen?: boolean, isHistory?: boolean } } [additionalParams]
     */
    async handleViewDesign(catalog, additionalParams = {}) {
      try {
        const { data: lastestModelId } = await reloadModelRendererUrl({
          catalogId: catalog.id,
          from: catalog.workspaceId,
        });
        const { relateType, name } = (await axios.get('bff-center/cloud-disk/list/getCatalogById', {
          params: {
            catalogId: catalog.id,
          },
        })).data;
        catalog.relateType = catalog.relateType ?? relateType;
        catalog.name = catalog.name ?? name;
        catalog.relateId = catalog.relateId ?? lastestModelId;
        additionalParams.isHistory = additionalParams.isHistory ?? (lastestModelId !== catalog.relateId);

        if (isQt()) {
          await exec('openRenderer', catalog, additionalParams);
        } else {
          await openSingleton(async () => {
            let routeName = '';
            if (additionalParams.isHistory) {
              routeName = 'ModelPreview';
            } else if (catalog.relateType === 4) {
              routeName = 'ClothesItem';
            } else {
              routeName = 'ModelItem';
            }
            return {
              name: routeName,
              params: {
                modelId: catalog.relateId,
              },
              query: {
                workspaceId: catalog.workspaceId,
                catalogId: catalog.id,
                relateType,
                ...additionalParams,
              },
            };
          }, true, 'ModelItem');
        }
      } catch (e) {
        this.$errHandler(e);
      }
    },
  },
};

export default operationMixin;
