import axios from './axios';

/**
 * 搜索workspace
 * @see http://yapi.4dshoetech.local/project/28/interface/api/5381
 * @param {string} appId
 * @param {string} keyword
 * @returns {Promise<{data:Object}>}
 */
export const apiSearchWorkspaces = (keyword) => axios({
  method: 'get',
  url: '/authcenter/manage/groupapp/listSeach',
  params: {
    appId: 2,
    keyword,
  },
});

/**
 * 获取登录账号的workpace列表
 * @param {String} appId workspaceId
 * @returns {Promise<data: Object>}
 */
export const apiGetWorkspaceList = (appId = 2) => {
  return axios({
    method: 'get',
    url: '/authcenter/manage/groupapp/list',
    params: {
      appId,
    },
  });
};
