


import {
  defineComponent, computed,
} from '@vue/composition-api';
import Vue from 'vue';
import i18n from 'i18next';
import CatalogModel from '@/models/Catalog';
import useClosest from '_/composables/use-closest';
import { appType2Icon, appType2Name } from './app-type-to-name';
import Catalog from './index.vue';

type KeyOfAppTypeIcon = keyof typeof appType2Icon;

function getName(
  index: number, rootCatalogName: string | undefined,
  appType: KeyOfAppTypeIcon, folder: CatalogModel,
) {
  return index === 0 && rootCatalogName
    ? rootCatalogName : (appType2Name[appType] ?? folder.name);
}

function handle(this: any, fn: Function | Function[], ...args: any[]) {
  const fns = Array.isArray(fn) ? fn : [fn];
  fns.forEach((f) => {
    f.call(this, ...args);
  });
}

const NavigationItem = Vue.extend({
  functional: true,
  props: {
    index: Number,
    item: Object as () => CatalogModel,
    rootCatalogName: String,
  },
  render(_, { props: { index, item: folder, rootCatalogName }, listeners: { click } }) {
    const appType = folder.appType as keyof typeof appType2Icon;

    return <li class="path-folder">
      {index
        ? <i class="el-icon-arrow-right"></i>
        : <i class={appType2Icon[appType] ?? 'iconfont icon-folder-close'}></i>
      }
      <span onClick={() => handle(click, folder)} class="path-folder-name">{
        getName(index, rootCatalogName, appType, folder)
      }</span>
    </li>;
  },
});

const NavigationBar = defineComponent({
  props: {
    keyword: String,
    appType: Number,
    rootCatalogName: String,
    folderTreeOpened: Boolean,
    isSearch: Boolean,
  },
  setup(props, { emit }) {
    const catalog = useClosest<typeof Catalog>('Catalog');
    const paths = computed(() => catalog.value?.paths);
    const activeFolder = computed(() => (paths.value ? paths.value[paths.value.length - 1] : undefined));
    const isBusinessType1 = computed(() => (activeFolder.value ? activeFolder.value.workspaceBusinessType === 1 : false));
    const fullPath = computed(() => {
      if (!paths.value) return '';
      return paths.value
        .map((e, i) => getName(i, props.rootCatalogName, e.appType as KeyOfAppTypeIcon, e)).join(' > ');
    });
    const goToParent = () => {
      if (!paths.value) return;
      emit('enterFolder', paths.value[paths.value.length - 2]);
    };
    const handleRefresh = () => {
      if (catalog.value) {
        catalog.value.refresh();
      }
    };

    return {
      isBusinessType1,
      catalog,
      paths,
      fullPath,
      goToParent,
      handleRefresh,
    };
  },
  render() {
    const self = this;
    const { isBusinessType1 } = this;
    const folderBtn = isBusinessType1 ? <el-tooltip content={'目录结构'} placement="top">
      <a class={['nav-item', { active: self.folderTreeOpened }]}
        onClick={() => self.$emit('toggleFolderTree')}>
        <i class="iconfont icon-tree"></i>
      </a>
    </el-tooltip> : null;

    // 暂时是会议库显示
    const homeBtn = this.catalog?.entryAppType === 26 ? <el-tooltip content={i18n.t('public.home')} placement="top">
      <a class="nav-item" onClick={() => this.$router.push({ name: 'UpMeeting', query: { meetingId: this.$route.query.meetingId } })}>
        <i class="iconfont iconshouye"></i>
      </a>
    </el-tooltip> : null;

    const backBtn = <el-tooltip content={i18n.t('file-list.back')} placement="top">
      <a class="nav-item" onClick={() => self.$emit('back')}>
        <i class="iconfont icon-right-arrow"></i>
      </a>
    </el-tooltip>;

    const forwardBtn = <el-tooltip content={i18n.t('file-list.forward')} placement="top">
      <a class="nav-item" onClick={() => self.$emit('forward')}>
        <i class="iconfont icon-left-arrow"></i>
      </a>
    </el-tooltip>;

    const parentFolderBtn = <el-tooltip content={i18n.t('file-list.previous')} placement="top">
      <a onClick={() => self.goToParent()}
        class={['nav-item', { disabled: !self.paths || self.paths.length <= 1 }]}>
        <i class="iconfont icon-up-arrow"></i>
      </a>
    </el-tooltip>;

    return <div class="navigation-bar-container">
      <div
        class="router-controller"
        style={{
          width: folderBtn ? '175px' : '125px',
        }}
      >
        {folderBtn}
        {folderBtn ? <div class="sep" /> : null}
        {homeBtn}
        {backBtn}
        {forwardBtn}
        {parentFolderBtn}
      </div>
      <div class="navigation-bar main-path" ref="navBar">
        <el-tooltip content={i18n.t('file-list.refresh')} placement="top">
          <div class="refresh-icon"
            onClick={self.handleRefresh}>
            <i class="iconfont icon-refresh"></i>
          </div>
        </el-tooltip>
        <el-tooltip placement="bottom" effect="dark" content={self.fullPath}>

          <ul class="path-container main-path-container">

            {
              self.paths ? self.paths.map((item, index) => (
                <NavigationItem
                  key={index}
                  item={item}
                  index={index}
                  onClick={(catalog: CatalogModel) => self.$emit('enterFolder', catalog)}
                  rootCatalogName={self.rootCatalogName}
                />
              )) : null
            }
            {self.isSearch
              ? <div class='path-folder'>
                <i class="el-icon-arrow-right"></i>
                <span>{i18n.t('file-list.search_tips')}</span>
              </div> : null}
          </ul>
        </el-tooltip>
      </div>
    </div>;
  },
});

export default NavigationBar;
