<template>
  <el-popover :width="202" v-model="cardVisible">
    <!-- 弹框内容 -->
    <div class="user-info-wrapper">
      <!-- 关闭按钮 -->
      <div style="height: 20px" class="flex-justify-flex-end">
        <i class="el-icon-close" style="font-size: 18px; cursor: pointer" @click="cardVisible = false"></i>
      </div>
      <!-- 用户信息 -->
      <div class="user-info">
        <!-- 用户头像和名称 -->
        <figure class="flex-vertical-center flex-column">
          <div style="position: relative">
            <img class="avator" :src="userImage" />
            <!-- <div style="position: absolute;right:0;bottom:0;font-size: 18px;">
              <i class="iconfont iconbiaoqian-owner"></i>
            </div> -->
            <div v-if="roleId2Role[+user.roleId] === OWNER">
              <IconWrapper
                icon-name="iconfont iconbiaoqian-owner"
                :font-size="18"
                style="position: absolute; right: 0; bottom: 0; color: #f7a900"
              />
            </div>
            <div v-else-if="roleId2Role[+user.roleId] === ADMIN">
              <IconWrapper
                icon-name="iconfont iconbiaoqian-admin"
                :font-size="18"
                style="position: absolute; right: 0; bottom: 0; color: #2878ff"
              />
            </div>
          </div>
          <figcaption class="user-name flex-center">
            {{ user.name || user.account || user.mail }}
          </figcaption>
        </figure>
        <el-divider></el-divider>
      </div>
      <el-button
        class="private-chat"
        v-if="user.userId !== currentUser.id"
        @click="$act(createPrivateChat, [groupAppId, user.userId])"
      >
        {{ $t("workspace.send_message") }}
      </el-button>
      <!-- 用户在协同空间的权限，和对其的操作 -->
      <div class="flex role-controller">
        <span> {{ $t("workspace.permission") }}: </span>
        <!-- 为虚拟账号时，只读不可改 -->
        <span v-if="roleId2Role[+user.roleId] === WORKSPACEPASS" class="controller-or-reader">
          {{ WORKSPACEPASS }}
        </span>
        <!-- 在本协同空间是创建者，可读可改对方角色 -->
        <span
          v-else-if="roleId2Role[+selfRoleId] === OWNER && roleId2Role[+user.roleId] !== OWNER"
          class="controller-or-reader"
        >
          <el-select
            :value="+user.roleId"
            size="small"
            @change="
              (val) =>
                $act(changeUserPrivilege, [groupAppId, user.userId, `${val}`], $t('workspace.priviledge_change_info'))
            "
          >
            <el-option
              v-for="charactor of ownerUsableRole"
              :key="charactor.roleId"
              :label="roleId2Role[+charactor.roleId]"
              :value="+charactor.roleId"
            ></el-option>
          </el-select>
        </span>
        <!-- 在本协同空间是管理员，可读可改管理员（不包含）以下的角色 -->
        <span
          v-else-if="
            roleId2Role[+selfRoleId] === ADMIN &&
            (roleId2Role[+user.roleId] === MEMBER || roleId2Role[+user.roleId] === GUEST)
          "
          class="controller-or-reader"
        >
          <el-select
            :value="+user.roleId"
            size="small"
            @change="
              (val) =>
                $act(changeUserPrivilege, [groupAppId, user.userId, `${val}`], $t('workspace.priviledge_change_info'))
            "
          >
            <el-option
              v-for="charactor of adminUsableRole"
              :key="charactor.roleId"
              :label="roleId2Role[+charactor.roleId]"
              :value="+charactor.roleId"
            ></el-option>
          </el-select>
        </span>
        <!-- 其余情况只读 -->
        <span v-else class="controller-or-reader">
          {{ roleId2Role[+user.roleId] }}
        </span>
      </div>
      <!-- 移出操作 -->
      <div
        v-if="
          (roleId2Role[+selfRoleId] === OWNER && roleId2Role[+user.roleId] !== OWNER) ||
          (roleId2Role[+selfRoleId] === ADMIN &&
            (roleId2Role[+user.roleId] === MEMBER ||
              roleId2Role[+user.roleId] === GUEST ||
              roleId2Role[+user.roleId] === WORKSPACEPASS)) ||
          (roleId2Role[+selfRoleId] === MEMBER &&
            (roleId2Role[+user.roleId] === GUEST || roleId2Role[+user.roleId] === WORKSPACEPASS))
        "
        style="line-height: 32px; padding: 0 5px; cursor: pointer"
      >
        <span @click="$act(handleDeleteUser, [user])">
          {{ $t("workspace.remove") }}
        </span>
      </div>
    </div>

    <!-- 弹框的触发者 -->
    <span slot="reference">
      <slot>
        <button>
          {{ $t("workspace.user") }}
        </button>
      </slot>
    </span>
  </el-popover>
</template>

<script>
import i18n from 'i18next';
import app from '@/app';

// eslint-disable-next-line no-unused-vars
import IconWrapper from '@/components/icon/IconWrapper';
import { createPrivateChat } from '@/api/im';
import { changeMemberPrivilege, delMember } from '@/loaders/workspace-member';
import { getContextUser } from '../../../../loaders/context';
import { userAbout } from './mixin';

export default {
  mixins: [userAbout],
  components: { IconWrapper },
  props: {
    user: {},
    groupAppId: {
      type: String,
      required: true,
    },
  },
  computed: {
    currentUser() {
      return getContextUser();
    },
    userImage() {
      return this.user.headImg || this.user.avator || require('@/assets/img/header/default_avatar.png');
    },
  },
  data() {
    return {
      cardVisible: false,
    };
  },
  methods: {
    // 修改用户权限
    async changeUserPrivilege(groupAppId, memberId, memberRoleId) {
      await changeMemberPrivilege(
        groupAppId, memberId, memberRoleId,
      );
      this.$closest('WorkspaceGroupSettings').searchMembersWrapper();
    },
    // 删除协同空间内的用户
    async handleDeleteUser(member) {
      if (await this.$confirmBool(i18n.t('workspace.delete_confirm_text'),
        i18n.t('workspace.info'),
        {
          confirmButtonText: i18n.t('public.dialog.real_confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          closeOnClickModal: false,
          type: 'warning',
        })) {
        try {
          await delMember(this.groupAppId, member.userId);
          this.$message.success(i18n.t('workspace.delete_success_tip'));
          this.$closest('WorkspaceGroupSettings').searchMembersWrapper();
        } catch (e) {
          this.$errHandler(e);
        }
      }
    },
    // 创建私聊
    async createPrivateChat(groupAppId, userId) {
      // return console.info(this.$root);
      const {
        data: {
          groupId: sessionId,
          groupPrivateState: state,
        },
      } = await createPrivateChat(groupAppId, userId);

      const newSession = await this.$im.getSession({ id: sessionId });
      if (state) {
        // send system msg
        const currentUser = getContextUser();
        await this.$im.send(newSession, { type: 'system', subType: 2, member: currentUser.name });
      }

      this.$closest('WorkspaceGroupSettings').$emit('cancel', null);
      app.$emit('open-im', sessionId);
    },
  },
};
</script>

<style lang="scss" scoped>
.avator {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f5f5f5;
  // margin-right: 10px;
}
.role-controller {
  line-height: 32px;
  padding: 0 5px;
  /deep/ .el-input__inner {
    width: 80px;
    padding: 0 5px;
    border: 0;
  }
}
.private-chat {
  width: 100%;
  border: 1px solid #c1c5ca;
  color: #666;
  margin-bottom: 10px;
  &:hover,
  &:active,
  &:focus {
    color: $color-primary;
    border: 1px solid $color-primary;
    background-color: #fff;
  }
}
/deep/ .el-divider--horizontal {
  margin-top: 15px;
  margin-bottom: 12px;
}
.user-info-wrapper {
  font-size: 12px;
  color: #666;
}
.user-name {
  font-size: 14px;
  padding: 10px 0;
  text-align: center;
  word-break: break-all;
}
.controller-or-reader {
  padding: 0 5px;
}
</style>
