<template>
  <div>
    <TableList
      :memberList="memberList"
      :spaceRoleId="spaceRoleId"
      :groupAppId="groupAppId"
      @deleteMember="$emit('refreshMembers')"
    >
    </TableList>

    <div class="button-group">
      <el-tooltip :content="$t('workspace.share_and_invite_partners')" placement="top">
        <el-button type="text" class="invite-members" @click="handleInvite" v-if="$can(groupAppId, 'OP0029')">
          <i class="iconfont icon-invitation-o"></i>
          {{ $t("workspace.share_and_invite_permission") }}
        </el-button>
      </el-tooltip>

      <!-- <el-tooltip content="test test test" placement="top"> -->
      <el-button
        type="primary"
        class="add-members"
        :disabled="!$can(groupAppId, 'OP0029')"
        @click="$act(handleAddMembers, [], $t('workspace.add_members_success_tip'))"
      >
        {{ $t("workspace.add_members") }}
      </el-button>
      <!-- </el-tooltip> -->
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import { WorkspaceMemberOrm, WorkspaceUserRoleOrm } from '@/models';
import { refreshByWorkspaceId, addMembers } from '@/loaders/workspace-member';
import MemberSelector from './MemberSelector';
// import MemberInvitation from './MemberInvitation';
import MemberInvitationWrapper from './MemberInvitationWrapper';
import TableList from './TableList';

export default {
  dialogify: {
    props: {
      title: 'Workspace',
      titleAlign: 'left',
      width: '700px',
    },
  },
  props: {
    title: String,
    groupAppId: [String, Number],
  },
  components: {
    TableList,
  },
  computed: {
    spaceRoleId() {
      return WorkspaceUserRoleOrm.find(this.groupAppId).roleId;
    },
    memberList() {
      return WorkspaceMemberOrm.query().where('groupAppId', this.groupAppId).get()
        ? WorkspaceMemberOrm.query().where('groupAppId', this.groupAppId).get()
        : this.$loadingState;
    },
  },
  created() {
    this.$watch(() => {
      return this.groupAppId;
    }, (groupAppId) => {
      refreshByWorkspaceId(groupAppId);
    }, {
      immediate: true,
    });
  },
  methods: {
    async handleInvite() {
      // eslint-disable-next-line no-unused-vars
      await this.$Dialog.dialogify({
        is: MemberInvitationWrapper,
        props: {
          groupAppId: this.groupAppId,
          spaceRoleId: this.spaceRoleId,
        },
      });
      this.$emit('refreshMembers');
    },
    async handleAddMembers() {
      const list = await this.$Dialog.dialogify({
        is: MemberSelector,
        props: {
          groupAppId: this.groupAppId,
        },
      }, {
        props: {
          titleAlign: 'left',
          width: '700px',
          title: i18n.t('workspace.add_members'),
        },
      });
      await addMembers(
        this.groupAppId, list.map((member) => member.id),
      );
      this.$emit('refreshMembers');
    },
    // eslint-disable-next-line
    getUserTypeFromTypeId(typeId) {
      // eslint-disable-next-line
      switch (Number(typeId)) {
        case 4:
          return i18n.t('workspace.owner');
        case 5:
          return i18n.t('workspace.admin');
        case 6:
          return i18n.t('workspace.member');
        case 7:
          return i18n.t('workspace.guest');
        case 8:
          return i18n.t('workspace.invited');
        // "企业关联账户";
      }
    },
  },
  filters: {
    en: value => {
      if (value) {
        // eslint-disable-next-line
        switch (value) {
          case '空间创建者':
            return 'Owner';
          case '下载者':
            return 'Admin';
          case '编辑者':
            return 'Member';
          case '查看者':
            return 'Guest';
          case '参与者':
            return 'Invited';
        }
      }
      return value;
    },
  },
};
</script>

