  display: flex;
  margin-top: 30px;
  justify-content: space-between;
  align-items: center;
  .iconfont {
    @include resize-icon(1.3);
    margin-right: 0.3em;
  }
}
.member-management-title {
  text-align: center;
  width: 100%;
  margin-bottom: 26px;
  font-size: 18px;
  font-weight: 700;
}
</style>
