<template>
  <div
    class="workspace-sidebar-wrapper"
    :class="{ invisible: !collapseSideBar }"
  >
    <span
      class="collapse-tab"
      @click="$emit('update:collapseSideBar', !collapseSideBar)"
    >
      <i v-if="collapseSideBar" class="iconfont icon-angle-left"></i>
      <i v-else class="iconfont icon-angle-right"></i>
    </span>
    <aside
      class="workspace-sidebar-container flex-column"
      :class="{ 'qt-workspace-sidebar-container': isQt() }"
    >
      <!-- 根据协同空间名或成员搜索协同空间 -->
      <!-- <div class="workspace-searcher"></div> -->

      <!-- 添加和搜索 -->
      <div class="nav-box">
        <WorkspaceCreator v-if="!searchMode" class="nav-left" />
        <ButtonSearchWorkspace @change-mode="searchMode = $event" />
      </div>
      <!-- 创建时间筛选 -->
      <div class="time-filter" v-if="includesWsType2">
        <el-dropdown>
          <span class="el-dropdown-link">
            {{
              sortParams.orderBy === "gmt_create_time"
                ? "创建时间"
                : "修改时间"
            }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              @click.native="sortParams.orderBy = 'gmt_create_time'"
              >创建时间</el-dropdown-item
            >
            <el-dropdown-item
              @click.native="sortParams.orderBy = 'gmt_modify_time'"
              >修改时间</el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
        <el-date-picker
          class="date-picker"
          v-model="dateRange"
          value-format="timestamp"
          size="mini"
          type="daterange"
          range-separator="-"
          :default-time="['00:00:00', '23:59:59']"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
        >
        </el-date-picker>
        <el-dropdown>
          <div class="el-dropdown-link">
            <i class="iconfont icon-sort-2"></i>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="sortParams.desc = false">
              升序
              <i v-if="sortParams.desc === false" class="iconfont icon-tick" />
            </el-dropdown-item>
            <el-dropdown-item @click.native="sortParams.desc = true">
              降序
              <i v-if="sortParams.desc === true" class="iconfont icon-tick" />
            </el-dropdown-item>
            <el-dropdown-item @click.native="sortParams.desc = null">
              自由排序
              <i v-if="sortParams.desc === null" class="iconfont icon-tick" />
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="tab-bar" v-if="includesWsType2">
        <span
          class="tab-item"
          :class="wsTab === 1 ? 'is-active' : ''"
          @click="wsTab = 1"
          @dragenter="handleDragenterTab($event, 1)"
          >商品WS</span
        >
        <span
          class="tab-item"
          :class="wsTab === 2 ? 'is-active' : ''"
          @click="wsTab = 2"
          @dragenter="handleDragenterTab($event, 2)"
          >供应商WS</span
        >
      </div>

      <!-- 可选择或排序的协同空间列表 -->
      <section class="workspace-list">
        <transition-group
          name="list"
          tag="ul"
          class="scrollable-y"
          style="position: absolute; width: 100%; height: 100%"
          ref="workspaceList"
        >
          <!-- ondragend处理ws拖拽排序，ondrop处理文件和文件夹拖拽到ws -->
          <li
            class="workspace-item flex-space-between"
            :class="{
              current: relateId === workspace.groupAppId,
              paste:
                pointerIndex === i &&
                draggingData &&
                relateId !== workspace.groupAppId,
              unread:
                getUnread(workspace.groupAppId) ||
                getWorkspaceUnread(workspace.groupAppId),
            }"
            v-for="(workspace, i) of getWorkspaceList"
            :key="workspace.groupAppId"
            @click="
              $act(toggleworkspaceCurrentItem, [
                workspace.groupAppId,
                workspace,
              ])
            "
            :draggable="canDragSort"
            @dragstart.self="handleDragstart($event, i)"
            @dragenter="handleDragenter($event, i)"
            @dragleave="pointerIndex = null"
            @drop.prevent="dropFile($event, workspace)"
            @dragover.prevent
            @dragend.prevent="handleDragend($event, i)"
            @contextmenu.prevent="rightClickRenameWorkspace(workspace)"
          >
            <span
              v-if="workspace.roleId === '4'"
              class="iconfont-wrapper flex-center"
            >
              <i class="iconfont icon-workspace-admin"></i>
            </span>
            <span v-else class="iconfont-wrapper flex-center">
              <i class="iconfont icon-workspace-participant"></i>
            </span>
            <OmittedDisplayer class="workspace-name flex-1" width="168">
              {{ workspace.name }}
            </OmittedDisplayer>
            <!-- <span v-html="workspace.name" class="workspace-name"></span> -->
            <!-- <span class="iconfont-wrapper flex-center">
              <i class="iconfont icon-workspace-move-flag"></i>
            </span> -->
          </li>
        </transition-group>
        <!-- <SortableList sortable="true">
          <div v-for="e of list" :key="e"
            @dragstart.self="handleDragstart">
            asdfadsfasdfasdfasdf
          </div>
        </SortableList> -->
      </section>
    </aside>
  </div>
</template>

<script>
import i18n from 'i18next';
import { prompt } from '@/components/index';
// import SortableList from '@/components/SortableList';
import ButtonSearchWorkspace from '@/views/workspace/components/aside/ButtonSearchWorkspace';
import WorkspaceCreator from '@/views/profile/workspaceManager/WorkspaceCreator';
import OmittedDisplayer from '@/components/OmittedDisplayer';
import draggingData from '@/utils/dragging-data';
import nameInputValidator from '@/utils/validators/checkers/valid-name';
import {
  changeSequence,
} from '@/api/catalogue';
import { updateWorkspaceRolesAndPermissions, getContextUser } from '@/loaders/context';
import allow from '@/utils/validators/can';
import { getCatalogId } from '@/loaders/catalog';
import isQt from '@/utils/is-qt';
import { getUnread as getWorkspaceUnread } from '@/loaders/workspace-msg';
import { WorkspaceOrm } from '@/models';
import { refreshWorkspaceWithoutMembers, renameWorkspace } from '@/loaders/workspace';
import { getUnread } from '../../../../loaders/im';
import { commands } from '../../../../utils/catalog';
import wsMixin from './mixin';

export default {
  mixins: [wsMixin],
  props: ['appType', 'relateId', 'collapseSideBar', 'workspaceType'],
  components: {
    // SortableList,
    ButtonSearchWorkspace,
    WorkspaceCreator,
    OmittedDisplayer,
  },
  data() {
    this.dragstartIndex = -1; // 拖动对象的初始index
    this.dragTargetGroupAppId = null; // 被拖动的协同空间的id
    this.lastDragEnterIndex = -1; // 上一个被拖动对象被拖到的index
    this.transitionItemIndexList = []; // 用于防止元素在移动的动画过程中被再次移动
    this.dragRequest = Promise.resolve();
    return {
      list: [],
      pointerIndex: null, // 拖拽文件进来的样式
      creatorVisible: true,
      searchMode: false,
      wsTab: this.workspaceType || 1, // 1: 个人WS; 2: 入库WS
      dateRange: '',
      sortParams: {
        orderBy: 'gmt_create_time', // gmt_create_time | gmt_modify_time
        desc: null,
      },
    };
  },
  computed: {
    /** @return { any } */
    draggingData() {
      // debugger;
      return draggingData.get();
    },
    /** @return { Collection<Workspace> } */
    workspaceList() {
      return WorkspaceOrm.query().orderBy('sort').get();
    },
    // /** @return { boolean } */
    // isAnta() {
    //   return getContextUser()?.businessType === 1;
    // },
    /** @return { boolean } */
    includesWsType2() {
      return this.workspaceList.some(ws => ws.type === 2);
    },
    /** @return { Collection<Workspace> } */
    getWorkspaceList() {
      if (!this.includesWsType2) {
        return this.workspaceList;
      }
      // console.log('sortParams==========', this.sortParams, this.sortParams.desc, this.sortParams.orderBy);
      const newWsList = this.workspaceList.filter(ws => {
        // 根据workspace类型筛选
        if (ws.type !== this.wsTab) {
          return false;
        }
        // 根据创建时间和修改时间筛选
        if (this.dateRange) {
          if (this.sortParams.orderBy === 'gmt_create_time') {
            if (ws.gmtCreateTime < this.dateRange[0] || ws.gmtCreateTime > this.dateRange[1]) {
              return false;
            }
          } else if (this.sortParams.orderBy === 'gmt_modify_time') {
            if (ws.gmtModifyTime < this.dateRange[0] || ws.gmtModifyTime > this.dateRange[1]) {
              return false;
            }
          }
        }
        return true;
      });
      // 按时间升降序
      this.sortParams.desc !== null && newWsList.sort((a, b) => {
        if (this.sortParams.orderBy === 'gmt_modify_time') {
          if (this.sortParams.desc) {
            return a.gmtModifyTime - b.gmtModifyTime;
          }
          return b.gmtModifyTime - a.gmtModifyTime;
        }
        if (this.sortParams.desc) {
          return a.gmtCreateTime - b.gmtCreateTime;
        }
        return b.gmtCreateTime - a.gmtCreateTime;
      });
      return newWsList;
    },
    /** @return { boolean } */
    canDragSort() {
      return this.sortParams.desc === null;
    },
  },
  methods: {
    isQt,
    getUnread,
    getWorkspaceUnread,
    // 控制workspace Item 拖拽开始
    handleDragstart(e, i) {
      // 设置拖拽携带值
      e.dataTransfer.setData('text/plain', 'workspace');
      // 通过类来取消拖动时的hover和active的效果
      this.$refs.workspaceList.$el.classList.add('disable-hover');
      // 给选中元素增加高亮
      Array.from(this.$refs.workspaceList.$el.children)[i].classList.add('current');
      this.lastDragEnterIndex = i;
      this.dragstartIndex = i;
      this.dragTargetGroupAppId = this.getWorkspaceList[i].groupAppId;
    },
    // 控制workspace Item 拖拽结束
    async handleDragend(_, i) {
      // 当不是当前活跃协同空间时，删除高亮
      if (this.getWorkspaceList[i].groupAppId !== this.relateId) {
        Array.from(
          this.$refs.workspaceList.$el.children,
        )[i].classList.remove('current');
      }
      // 取消类，恢复hover和active效果
      this.$refs.workspaceList.$el.classList.remove('disable-hover');
      if (this.dragstartIndex === this.lastDragEnterIndex
        || this.dragstartIndex === i) return;
      // console.log(`startIndex: ${this.dragstartIndex}, endIndex: ${this.lastDragEnterIndex}`);
      const sortData = this.workspaceList.map(item => ({ groupAppId: item.groupAppId, sort: item.sort }));
      this.dragRequest = this.dragRequest.then(async () => {
        await changeSequence(
          sortData,
        );
      });
    },
    // 控制workspace Item 拖拽动画
    async handleDragenter(e, i) {
      if (this.draggingData) {
        this.handleFileDragenter(e, i);
        return;
      }
      if (!this.canDragSort) {
        return;
      }
      if (i === this.lastDragEnterIndex
        || this.transitionItemIndexList.includes(i)
        || this.transitionItemIndexList.includes(this.lastDragEnterIndex)) return;
      const startIndex = Math.min(this.lastDragEnterIndex, i);
      const endIndex = Math.max(this.lastDragEnterIndex, i);
      for (let j = startIndex; j <= endIndex; j++) {
        this.transitionItemIndexList.push(j);
      }
      // 改变前删除高亮
      Array.from(this.$refs.workspaceList.$el.children)[
        this.lastDragEnterIndex
      ].classList.remove('current');
      await this.changeSortDuringDrag(this.lastDragEnterIndex, i, this.getWorkspaceList);
      // 改变后增加高亮
      Array.from(
        this.$refs.workspaceList.$el.children,
      )[i].classList.add('current');
      const vm = this;
      setTimeout(() => {
        vm.transitionItemIndexList.splice(
          vm.transitionItemIndexList.indexOf(startIndex), endIndex - startIndex + 1,
        );
      }, 50);
      this.lastDragEnterIndex = i;
    },
    // 控制 文件拖拽进workspace Item
    async handleFileDragenter(e, i) {
      // 将enter事件触发点移到leave后
      setTimeout(() => {
        this.pointerIndex = i;
        console.info('dragEnter');
      }, 0);
    },
    async dropFile(e, workspace) {
      // workspace的拖拽不走dropFile事件
      if (e.dataTransfer.getData('text') === 'workspace') {
        e.dataTransfer.clearData();
        return;
      }
      if (!this.draggingData) {
        return;
      }
      const dragFile = { type: this.draggingData.type, data: this.draggingData.data, command: 'copy' };
      await updateWorkspaceRolesAndPermissions(workspace.groupAppId);
      this.pointerIndex = null;

      // 不允许拖拽到当前workspace
      if (workspace.groupAppId === this.relateId) return;
      // debugger;
      const id = await getCatalogId({
        entryAppType: 13,
        workspaceId: workspace.groupAppId,
      });
      try {
        await commands.paste.run({
          data: {
            entryAppType: 13,
            workspaceId: workspace.groupAppId,
            workspaceBusinessType: workspace.businessType,
            id,
            fileType: 0,
          },
        }, dragFile);
      } catch (err) {
        this.$errHandler(err);
      } finally {
        draggingData.clear();
      }
    },
    async handleDragenterTab(e, wsTab) {
      if (!this.draggingData) {
        return;
      }
      this.wsTab = wsTab;
    },
    async toggleworkspaceCurrentItem(workspaceID, ws) {
      await this.enterWorkspace(workspaceID, ws);
    },
    // 右点击修改名字
    async rightClickRenameWorkspace({ groupAppId, name }) {
      // await updateWorkspaceRolesAndPermissions(groupAppId);
      // const hasAuth = this.$can(groupAppId, 'OP0043');
      // console.info(hasAuth);
      this.$root.$emit('open-menu', [
        {
          name: i18n.t('workspace.rename'),
          available: true,
          icon: 'iconfont icon-pencil-o',
          command: async () => {
            await updateWorkspaceRolesAndPermissions(groupAppId);
            try {
              allow('OP0043', { workspaceId: groupAppId }, true);
            } catch (e) {
              this.$errHandler(e);
              return;
            }


            // const formData = await this.$Dialog.dialogify({
            //   is: DialogWorkspaceCreate,
            //   props: {
            //     name,
            //   },
            // }, {
            //   props: {
            //     title: i18n.t('workspace.workspace_name'),
            //     width: '400px',
            //     titleAlign: 'left',
            //     closeOnClickModal: false,
            //   },
            // });
            const newName = ((await prompt('', {
              title: i18n.t('public.dialog.rename'),
              inputPlaceholder: i18n.t('workspace.rename_placeholder'),
              confirmButtonText: i18n.t('public.dialog.confirm'),
              cancelButtonText: i18n.t('public.dialog.cancel'),
              inputValue: name,
              closeOnClickModal: false,
              inputValidator: (input) => nameInputValidator(input, { min: 1, max: 100 }),
            }))).value;

            renameWorkspace(groupAppId, newName);
          },
        },
      ]);
    },
  },
  async created() {
    await refreshWorkspaceWithoutMembers();
    this.$watch('collapseSideBar', async visiable => {
      if (visiable) {
        await refreshWorkspaceWithoutMembers();
      }
    });
    // 交换列表对象的sort，结合computed的orderBy的sort字段，达到变更顺序的效果
    this.changeSortDuringDrag = function changeSortDuringDrag(startIndex, endIndex, list) {
      if (startIndex === endIndex) return;
      // console.log('start', list.map(e => e.sort), startIndex, endIndex);
      const tmpSort = list[endIndex].sort;
      if (startIndex < endIndex) {
        for (let i = endIndex; i > startIndex; i--) {
          list[i].sort = list[i - 1].sort;
        }
      } else {
        for (let i = endIndex; i < startIndex; i++) {
          list[i].sort = list[i + 1].sort;
        }
      }
      list[startIndex].sort = tmpSort;
    };
  },
  watch: {

    draggingData: {
      handler(val) {
        if (!val) {
          this.pointerIndex = null;
          this.$refs.workspaceList.$el.classList.remove('dragging-hover');
        } else {
          this.$refs.workspaceList.$el.classList.add('dragging-hover');
        }
      },
    },
    workspaceType(val, oldVal) {
      if (val === oldVal) return;
      this.wsTab = val || oldVal;
    },
  },
};
</script>

<style lang="scss" scoped>
