<template>
  <div class="vue-drag-select" @mousedown="onMouseDown">
    <slot :selectedItems="selectedItems" />
    <div v-if="mouseDown" class="vue-drag-select-box"
      :style="selectionBoxStyling"></div>
  </div>
</template>

<script>
import difference from '_/difference';
// let selectedItemsDeduplicated = [];

export default {
  name: 'vue-drag-select',
  props: {
    selectorClass: {
      type: String,
      required: true,
    },
    color: {
      type: String,
      default: 'rgba(0, 162, 255, .4)',
    },
    mode: {
      type: String,
      default: 'contain',
      validator: val => ['contain', 'contact'].indexOf(val) !== -1,
    },
  },
  data() {
    return {
      mouseDown: false,
      startPoint: null,
      endPoint: null,
      selectedItems: [],
    };
  },
  computed: {
    selectionBox() {
      // Only set styling when necessary
      if (!this.mouseDown || !this.startPoint || !this.endPoint) return {};

      const clientRect = this.$el.getBoundingClientRect();
      const scroll = this.getScroll();

      // Calculate position and dimensions of the selection box
      const left = Math.min(this.startPoint.x, this.endPoint.x) - clientRect.left - scroll.x;
      const top = Math.min(this.startPoint.y, this.endPoint.y) - clientRect.top - scroll.y;
      const width = Math.abs(this.startPoint.x - this.endPoint.x);
      const height = Math.abs(this.startPoint.y - this.endPoint.y);
      const right = window.innerWidth - Math.max(this.startPoint.x, this.endPoint.x);
      const bottom = window.innerHeight - Math.max(this.startPoint.y, this.endPoint.y);

      // Return the styles to be applied
      return {
        left,
        top,
        width,
        height,
        right,
        bottom,
      };
    },
    selectionBoxStyling() {
      // Only set styling when necessary
      if (!this.mouseDown || !this.startPoint || !this.endPoint) {
        return { background: this.color };
      }
      const {
        left, top, width, height,
      } = this.selectionBox;

      // Return the styles to be applied
      return {
        background: this.color,
        left: `${left}px`,
        top: `${top}px`,
        width: `${width}px`,
        height: `${height}px`,
      };
    },
  },
  watch: {
    selectedItems(val) {
      // if (JSON.stringify(val) === JSON.stringify(selectedItemsDeduplicated)) {
      //   return;
      // }
      this.$emit('change', val);
      // selectedItemsDeduplicated = val;
    },
  },
  methods: {
    getScroll() {
      // If we're on the server, default to 0,0
      if (typeof document === 'undefined') {
        return {
          x: 0,
          y: 0,
        };
      }

      return {
        x: this.$el.scrollLeft || document.body.scrollLeft || document.documentElement.scrollLeft,
        y: this.$el.scrollTop || document.body.scrollTop || document.documentElement.scrollTop,
      };
    },
    onMouseDown(event) {
      // Ignore right clicks
      if (event.button === 2) return;

      // Register begin point

      this.startPoint = {
        x: event.pageX,
        y: event.pageY,
      };

      // eslint-disable-next-line prefer-const
      let unbind;
      const fn = (e) => {
        // 如果鼠标没动，则跳过
        if (e.pageX === this.startPoint.x && e.pageY === this.startPoint.y) {
          return;
        }
        // 两次绑定避免误触并有效导出click时间
        this.mouseDown = true;
        this.$emit('selectstart');
        this.selectedItems = [];
        window.removeEventListener('mousemove', fn);
        window.removeEventListener('mouseup', unbind);
        // Start listening for mouse move and up events
        window.addEventListener('mousemove', this.onMouseMove);
        window.addEventListener('mouseup', this.onMouseUp);
      };
      unbind = () => {
        this.$emit('click');
        window.removeEventListener('mousemove', fn);
        window.removeEventListener('mouseup', unbind);
      };
      window.addEventListener('mousemove', fn);
      window.addEventListener('mouseup', unbind);
    },
    onMouseMove(event) {
      // Update the end point position
      if (this.mouseDown) {
        this.endPoint = {
          x: event.pageX,
          y: event.pageY,
        };

        const children = Array.from((this.$children.length
          ? this.$children
          : this.$el.children) ?? []);


        if (children) {
          const selected = children.filter((item) => {
            return this.isItemSelected(item);
          });

          // eslint-disable-next-line
          // If shift was held during mousedown the new selection is added to the current. Otherwise the new selection
          // will be selected
          // eslint-disable-next-line
          const old = this.selectedItems.slice();

          this.selectedItems.splice(0, this.selectedItems.length, ...selected);

          const addedItems = difference(selected, old);
          const removedItems = difference(old, selected);
          const addedOrRemovedItems = children.filter(
            item => addedItems.includes(item) || removedItems.includes(item),
          );

          if (addedOrRemovedItems.length) {
            this.$emit(
              'select',
              selected,
              addedItems,
              removedItems,
              addedOrRemovedItems,
            );
          }
        }
      }
    },
    onMouseUp() {
      // Clean up event listeners
      window.removeEventListener('mousemove', this.onMouseMove);
      window.removeEventListener('mouseup', this.onMouseUp);

      // Reset state
      this.mouseDown = false;
      this.startPoint = null;
      this.endPoint = null;
      this.$emit('selectend');
    },
    isItemSelected(item) {
      const el = item.$el || item;
      // debugger;
      if (el.classList.contains(this.selectorClass)) {
        const boxA = this.selectionBox;
        const boxB = {
          top: el.offsetTop,
          left: el.offsetLeft,
          width: el.clientWidth,
          height: el.clientHeight,
        };

        if (this.mode === 'contain') {
          // const clientRect = this.$el.getBoundingClientRect();
          // const scroll = this.getScroll();
          // const boxBRect = el.getBoundingClientRect();
          return !!(
            boxB.left >= boxA.left
            && boxA.top <= boxB.top
            && boxA.left + boxA.width >= boxB.left + boxB.width
            && boxA.top + boxA.height >= boxB.top + boxB.height
          );
        }

        return !!(
          boxA.left <= boxB.left + boxB.width
          && boxA.left + boxA.width >= boxB.left
          && boxA.top <= boxB.top + boxB.height
          && boxA.top + boxA.height >= boxB.top
        );
      }

      return false;
    },
  },
  beforeDestroy() {
    // Remove event listeners
    window.removeEventListener('mousemove', this.onMouseMove);
    window.removeEventListener('mouseup', this.onMouseUp);

    this.$children.forEach((child) => {
      child.$off('click');
    });
  },
};
</script>

<style>
.vue-drag-select {
  position: relative;
  user-select: none;
}

.vue-drag-select-box {
  position: absolute;
  z-index: 99;
}
</style>
