import {
  ApiSingleWorkpaceTable as FetchWorkspaceMembersInfo, // 获取单个workspace的成员信息
} from '@/api/permission';
import {
  addMembersInRooms,
  deleteMembersInRooms,
  changeUserPrivilege,
} from '@/api/previlege';
import { WorkspaceMemberOrm } from '@/models';

export async function refreshByWorkspaceId(workspaceId: string) {
  // this.delete(each => each.groupAppId === workspaceId);
  const members = await FetchWorkspaceMembersInfo(workspaceId);
  // 增加groupAppId作为外键
  members.data.users.forEach(each => Object.assign(each, { groupAppId: workspaceId }));
  await WorkspaceMemberOrm.insertOrUpdate({
    data: members.data.users,
  });
  return WorkspaceMemberOrm.query().where(member => {
    return member.groupAppId === workspaceId;
  }).get();
}

export async function addMembers(groupAppId: string, memberIds: string) {
  await addMembersInRooms(groupAppId, memberIds);
  await refreshByWorkspaceId(groupAppId);
}

export async function delMember(groupAppId: string, memberId: string) {
  WorkspaceMemberOrm.delete(m => m.userId === memberId && m.groupAppId === groupAppId);
  await deleteMembersInRooms(groupAppId, memberId);
}

export async function changeMemberPrivilege(groupAppId: string, memberId: string, memberRoleId: string) {
  WorkspaceMemberOrm.update({
    where: m => m.groupAppId === groupAppId && m.userId === memberId,
    data: {
      roleId: memberRoleId,
    },
  });
  changeUserPrivilege(groupAppId, memberRoleId, memberId);
}

export function getMemberInfoByUserId(groupAppId: string, memberId: string) {
  const target = WorkspaceMemberOrm.query().where(user => {
    return user.userId === memberId && user.groupAppId === groupAppId;
  }).get();
  return target;
}
