
import {
  ref, computed, defineComponent, reactive, watch, toRefs, UnwrapRef,
} from '@vue/composition-api';
import toggleFrom from '_/toggle-from';

import useSetting from '@/utils/composables/use-setting';
import { sync } from '_/jsx-utils';
import CatalogModel, {
  CatalogFolder,
  CatalogOptions,
  SearchParams,
  CatalogModalTagMap,
} from '@/models/Catalog';
import axios from '@/utils/axios';
import draggingData from '@/utils/dragging-data';
import clipboard from '@/utils/clipboard';
import Paging from '@/models/Paging';
import hexToRgb from '_/hex-to-rgb';
import { i18n } from '@/i18n';
import { getContextWS, getContextUser } from '@/loaders/context';
import _remove from '@4dst-saas/public-utils/dist/remove';
import {
  ensureId, fetchChildrenWithPaging, getTagByIds,
} from '../../../../loaders/catalog';
import { SizeParams, useAutoFix } from './use-auto-fix';
import PanelList from './List.vue';
import NavigationBar from './NavigationBar.vue';
import NavigationTools from './NavigationTools.vue';
import FolderTree from './FolderTree.vue';
import NavigationSearch from './NavigationSearch.vue';

// @ts-ignore


async function getCatalogUrl(catalogId: string): Promise<CatalogModel[]> {
  return (await axios.get('/catalogue/yunpanCatalog/getCatalogUrl', {
    params: {
      catalogId,
    },
  })).data;
}


export async function getNavs(activeFolder: CatalogOptions) {
  const {
    workspaceId, id, entryAppType, workspaceBusinessType, workspaceType,
  } = activeFolder;

  let result = [] as CatalogModel[];
  if (id) {
    // @ts-ignore
    result = [...(await getCatalogUrl(id)).map(item => ({
      entryAppType,
      fileType: 0,
      workspaceId,
      workspaceBusinessType,
      workspaceType,
      ...item,
    }))];
    if (result && result[0]) {
      result[0].appType = activeFolder.entryAppType;
    }
  } else {
    // @ts-ignore
    result = [{ ...activeFolder, fileType: 0, appType: activeFolder.entryAppType }];
  }
  // navCache[cacheKey] = result;
  // }
  await ensureId(result[0]);

  result.forEach((item: CatalogModel) => {
    if (item.appType) {
      // @ts-ignore
      // eslint-disable-next-line no-unused-expressions
      item.appType = +item.appType;
    }
  });
  return result;
}


async function getCounts(ids: string[]) {
  return Object.fromEntries((await axios.post<{ catalogueId: string, count: number }[]>(
    '/anta-material/catalogue/material/count', Array.from(new Set(ids)),
  )).data.map(item => {
    return [item.catalogueId, item.count];
  }));
}

const Catalog = defineComponent({
  name: 'Catalog',
  props: {
    rootCatalogName: {
      type: String,
    },
    backgroundColor: {
      type: String,
      default: '#FAFAFA',
    },
    id: {
      type: String,
      required: true,
    },
    workspaceId: {
      type: String,
    },
    workspaceName: {
      type: String,
    },
    workspaceType: {
      type: Number,
    },
    workspaceBusinessType: {
      type: Number,
    },
    searchParams: {
      type: Object as unknown as () => SearchParams,
    },
    entryAppType: {
      type: Number,
      required: true,
    },
  },
  setup(props, { root: { $router } }) {
    const itemIncrement = useSetting('fileListItemIncrement', 0);
    const viewType = useSetting('fileListViewType', 'drape');
    const shownInfo = useSetting('fileListShownInfo', true);
    const sortOrderBy = useSetting('fileListSortOrderBy', 'gmt_modify_time');
    const sortDesc = useSetting('fileListSortDesc', true);
    const paths = ref<CatalogFolder[]>();
    const tagMap = ref<CatalogModalTagMap>();
    const workspace = computed(() => {
      return getContextWS();
    });
    const folderTree = ref<InstanceType<typeof FolderTree>>();
    const panelList = ref();
    const itemSize = ref<SizeParams>({
      folder: {
        defaultWidth: 220,
        scale: 1,
      },
      file: {
        defaultWidth: 220,
        scale: 1,
      },
      binaryFile: {
        defaultWidth: 220,
        scale: 1,
      },
      material: {
        defaultWidth: 220,
        scale: 1,
      },
    });

    // 启用容器内item等分自适应
    useAutoFix({
      target: '.panel-list',
      sizeParams: itemSize.value,
      itemIncrement,
      offset: 30,
      margin: 30,
      callBack: (sizeParams) => {
        itemSize.value = sizeParams;
      },
    });

    watch(() => {
      return {
        id: props.id,
        entryAppType: props.entryAppType,
        workspaceId: props.workspaceId,
        workspaceType: props.workspaceType,
        workspaceBusinessType: props.workspaceBusinessType,
        workspaceName: props.workspaceName,
      };
    }, async (p) => {
      paths.value = undefined;
      const ps = await getNavs(p);
      if (p.entryAppType === 13 && p.workspaceName) {
        ps[0].name = p.workspaceName;
      }
      paths.value = ps;
    }, {
      flush: 'sync',
      immediate: true,
    });
    const timestamp = ref(Date.now());
    const sortParams = computed({
      get: () => {
        return {
          orderBy: sortOrderBy.value,
          desc: sortDesc.value,
        };
      },
      set: (value) => {
        if (value.orderBy !== sortOrderBy.value) {
          sortOrderBy.value = value.orderBy;
        }
        if (value.desc !== sortDesc.value) {
          sortDesc.value = value.desc;
        }
      },
    });


    const activeFolder = computed(() => paths.value?.[paths.value.length - 1]);
    const activeModule = computed(() => paths.value?.[0]);

    const searchParams = ref<SearchParams>();
    watch(() => props.searchParams, (_searchParams) => {
      searchParams.value = _searchParams;
    }, {
      immediate: true,
      flush: 'pre',
    });

    const params = reactive({
      timestamp,
      sortParams,
      activeFolder,
      activeModule,
      searchParams,
    });
    const loadingPage = ref<number>();
    const catalogPaging = ref<Paging<CatalogModel>>();
    const catalogs = ref([] as CatalogModel[]);
    const pageSize = 30;
    const loadPage = async (p: typeof params, page: number) => {
      loadingPage.value = page;
      const catalogData = await fetchChildrenWithPaging(
        {
          ...p.activeFolder,
          relateType: p.searchParams?.relateType,
          root: p.activeModule,
          sortParams: p.sortParams,
          searchParams: p.searchParams,
        } as CatalogOptions,
        { cursor: page, limit: pageSize },
      );

      if (getContextUser()?.businessType === 1) {
        // 对于安踏空间的文件夹，查询是否为推荐文件夹
        const catalogRecords = catalogData.records.filter(c => c.fileType === 0);
        // console.log('loadPage=========', catalogRecords);
        if (catalogRecords && catalogRecords.length > 0) {
          const catalogIds = catalogRecords.map(c => c.id);
          const recommendStatus = (await axios({
            url: '/anta-material/recommend/folder/status',
            method: 'post',
            data: {
              ids: catalogIds,
            },
          })).data;
          // console.log('recommendStatus=========', recommendStatus);
          catalogRecords.forEach(catalog => {
            catalog.isRecommend = recommendStatus[catalog.id];
          });
        }
      }

      catalogPaging.value = catalogData;

      loadingPage.value = undefined;
    };

    // 获取标签，跟随翻页
    const loadTagMap = async (itemIds: string[]) => {
      const { data } = await getTagByIds(itemIds);
      tagMap.value = tagMap.value ? {
        ...tagMap.value,
        ...data,
      } : data;
    };

    watch(() => {
      return JSON.stringify(params);
    }, async (_p) => {
      const _params = JSON.parse(_p) as UnwrapRef<typeof params>;
      if (_params.searchParams && params.searchParams) {
        _params.searchParams.image = params.searchParams.image;
      }
      if (_params.activeFolder) {
        loadPage(_params, 1);
      }
    }, {
      immediate: true,
    });
    watch(catalogPaging, async (paging) => {
      if (paging) {
        const itemIds = paging.records?.map(item => item.id) as string[];
        loadTagMap(itemIds);
        if (paging.current === 1) {
          catalogs.value = paging.records;
        } else {
          catalogs.value.splice(catalogs.value.length, 0, ...paging.records);
        }
      }
    }, {
      immediate: true,
    });

    const selectedItems = ref<CatalogModel[]>([]);

    const selectedItemsMap = computed(() => {
      return selectedItems.value.reduce((memo, item) => {
        memo[item.id] = item;
        return memo;
      }, {} as {
        [x in CatalogModel['id']]: CatalogModel
      });
    });


    const dragingItemsMap = computed(() => {
      return draggingData.toObject();
    });

    const clipBoardItemsMap = computed(() => {
      return clipboard.toObject();
    });


    function isDragging(catalog: CatalogModel) {
      return !!dragingItemsMap.value[catalog.id];
    }

    function isCutting(catalog: CatalogModel) {
      return !!clipBoardItemsMap.value[catalog.id] && clipboard.command === 'cut';
    }

    function isCopying(catalog: CatalogModel) {
      return !!clipBoardItemsMap.value[catalog.id] && clipboard.command === 'copy';
    }

    function isSelected(catalog: CatalogModel) {
      return !!selectedItemsMap.value[catalog.id];
    }

    // 取消已选panel单元
    function cancelSelection() {
      selectedItems.value.splice(0, selectedItems.value.length);
    }

    watch(catalogPaging, () => {
      cancelSelection();
    }, {
      immediate: true,
    });

    // 切换选中方法
    function toggleSelection({ targets, toggle }: { targets: CatalogModel[], toggle?: boolean }) {
      const list = catalogs.value;
      targets.forEach(target => {
        if (
          !target.appType
          && list?.includes(target)
        ) {
          toggleFrom(selectedItems.value, target, toggle);
        }
      });
    }

    // 全选
    function selectAll() {
      const allCatalogs = catalogs.value ?? [] as CatalogModel[];
      cancelSelection();
      toggleSelection({ targets: allCatalogs, toggle: true });
    }


    async function remove(items: CatalogModel[]) {
      if (catalogPaging.value && catalogPaging.value.records) {
        const { records } = catalogPaging.value;
        items.forEach(item => {
          _remove(records, item);
        });
      }
    }

    async function refresh(_params: { timestamp: number, searchParams?: SearchParams } = { timestamp: Date.now() }) {
      if ('searchParams' in _params) {
        searchParams.value = _params.searchParams;
      }
      timestamp.value = _params.timestamp;
      if (folderTree.value) {
        folderTree.value.refresh();
      }
    }

    function loadMore() {
      if (!loadingPage.value && catalogPaging.value) {
        loadPage(params, catalogPaging.value.current + 1);
      }
    }

    const canLoadMore = computed(() => {
      if (!catalogPaging.value) {
        return false;
      }
      // if (catalogPaging.value.records.length < catalogPaging.value.size) {
      //   return false;
      // }
      return catalogPaging.value.pages > catalogPaging.value.current;
    });

    const folderTreeVisible = ref(false);

    return {
      searchParams_: searchParams,
      folderTree,
      folderTreeVisible,
      workspace,
      loadMore,
      itemSize,
      itemIncrement,
      viewType,
      shownInfo,
      sortParams,
      paths,
      catalogPaging,
      catalogs,
      isSelected,
      isDragging,
      isCutting,
      isCopying,
      selectedItems,
      activeModule,
      activeFolder,
      selectAll,
      toggleSelection,
      cancelSelection,
      refresh,
      remove,
      loadingPage,
      canLoadMore,
      panelList,
      params,
      tagMap,
    };
  },
  render() {
    const self = this as InstanceType<typeof Catalog>;
    const {
      backgroundColor,
      itemSize,
      viewType,
      shownInfo, rootCatalogName,
      $history, loadingPage,
      catalogPaging,
      paths,
      activeModule,
      activeFolder,
      searchParams_: searchParams,
      tagMap,
    } = self;
    const backgroundRgb = hexToRgb(backgroundColor)!;
    const loadingBgColor = `rgba(${backgroundRgb.r}, ${backgroundRgb.g}, ${backgroundRgb.b}, .6)`;
    const isSearch = catalogPaging?.isSearch;
    const emptyTips = isSearch ? i18n.t('workspace.no_search_result_tip') : i18n.t('workspace.empty');
    // default: `Your search filter found no matching files.
    // Please try another filter.\n(For best search results: Fill in all available fields)`,
    // default: 'No Search Results Found',
    return <main class="content-main-container" style={{ backgroundColor }}>
      <column flex="1">
        <div class="control-bar" style={{ backgroundColor }}>
          <div class="navigation">
            <NavigationBar
              onBack={() => $history.back()}
              onForward={() => $history.forward()}
              rootCatalogName={rootCatalogName}
              onEnterFolder={(item: CatalogModel) => self.$emit('enterFolder', item)}
              onToggleFolderTree={() => { self.folderTreeVisible = !self.folderTreeVisible; }}
              folderTreeOpened={self.folderTreeVisible}
              isSearch={isSearch}
              class="paths-container" />
            {
              // 搜索和筛选框
            }
            {activeFolder ? <NavigationSearch
              style="margin-left: 10px"
              onSearch={(v) => this.$emit('search', v, activeModule)}
              searchParams={searchParams}
              activeFolder={activeFolder} /> : null}

            <NavigationTools
              searchParams={searchParams}
              isSearch={isSearch}
              {...sync(self, ['viewType', 'itemIncrement', 'shownInfo', 'sortParams'])}
              style="margin-left: 10px"
              scopedSlots={{
                default: this.$scopedSlots.tools,
              }}
            />
          </div>
        </div>
        <row flex={1}>
          {self.workspaceBusinessType === 1 ? <FolderTree
            getCounts={getCounts}
            ref="folderTree"
            visible={self.folderTreeVisible}
            class="folder-tree"
            onSelect={(item: CatalogModel) => self.$emit('enterFolder', item)}
            paths={paths}
          /> : null}
          <PanelList
            ref="panelList"
            class="panel-list"
            backgroundColor={backgroundColor}
            viewType={viewType}
            itemSize={itemSize}
            shownInfo={shownInfo}
            vLoading={loadingPage === 1}
            element-loading-background={loadingBgColor}
            onEnterFolder={(item: CatalogModel) => self.$emit('enterFolder', item)}
            scopedSlots={{
              empty: () => (!catalogPaging ? null : <error error={{
                code: 404,
                message: emptyTips,
              }} style="font-size: 14px" img={isSearch ? require('@/assets/img/error/no-result.png') : undefined} />),
            }}
            tagMap={tagMap}
          />
        </row>

      </column>
    </main>;
  },
});
export default Catalog;

