<template>
  <section class="craete-workspace-pass-container">
    <!-- <div class="go-back" @click="$emit('changeComponent', 'MemberInvitationPermission')">
      <i class="iconfont icon-angle-left"></i>
      <span>&nbsp;Back</span>
    </div> -->
    <h4 class="invitation-header flex-center">
      <span>
        {{ $t('workspace.create_workspace_pass')}}
      </span>
      <span @click="$emit('showPermissionDetail')" class="iconfont icon-help"></span>
    </h4>

    <FormShareableLink mode='createAndInvite' :multiple="multiMode"
      ref="formShareableLink" />
  </section>
</template>

<script>
import FormShareableLink from '@/views/profile/account/components/FormShareableLink';
import {
  createVirtualAccount,
  ApiConnectAccount,
} from '@/api/account';
import { InviteMultipleMembers } from '@/api/email';

export default {
  props: {
    multiMode: Boolean,
    groupAppId: String,
  },
  components: {
    FormShareableLink,
  },
  mounted() {
    this.$closest('InvitationWrapper').okAvailable = false;
  },
  computed: {
    formShareableLink() {
      return this.$refs.formShareableLink;
    },
  },
  methods: {
    async handleSingleInvitation() {
      const res = await this.formShareableLink.validateForm();
      if (!res) return;
      const form = res;
      const { data: userId } = await createVirtualAccount(form);
      await ApiConnectAccount({
        list: [{
          roleId: '8',
          userId,
        }],
        isSendImg: this.formShareableLink.isSendImg,
      }, {
        groupAppId: this.groupAppId,
      });
    },
    async handleMultipleInvitation(emails) {
      const { startTime, endTime, remarks } = this.formShareableLink.form;
      const { groupAppId } = this;
      await InviteMultipleMembers({
        newAccount: true,
        emails,
        role: 8,
        remarks,
        groupAppId,
        startTime,
        endTime,
        isSendImg: this.formShareableLink.isSendImg,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.craete-workspace-pass-container {
  .go-back {
    width: 60px;
    margin-bottom: 30px;
    cursor: pointer;
  }
}
.icon-help {
  color: #ccc;
  font-weight: 100;
}
</style>
