<template>
  <div v-loading="members === null">

    <el-radio-group v-model="tabValue" class="selector-tabs flex-center">
      <el-radio-button label="公司成员">
        {{ tabLabels[0] }}
      </el-radio-button>
      <el-radio-button label="通讯录">
        {{ tabLabels[1] }}
      </el-radio-button>
      <el-radio-button label="虚拟ID"
        v-if="(!workspace || workspace.businessType !== 1) && user.businessType !== 1">
        {{ tabLabels[2] }}
      </el-radio-button>
    </el-radio-group>
    <div
      v-loading="true" class="loading" v-if="members === $loadingState"
      style="height: 400px"></div>
    <el-table :data="members"
      height="400" :border="false"
      style="width: 100%;margin-top:20px;"
      v-else
      class="custom-table custom-table-noborder"
      row-class-name="member-item">

      <!-- 虚拟账号邮箱接收者 -->
      <!-- <el-table-column
        width="25">
        <template slot-scope="{ row: member }">
          <el-checkbox
            :disabled="!member.enable || $store.state.account.user.id === member.id"
            :checked="!member.enable
             || $store.state.account.user.id === member.id
             || selected.includes(member)"
            @change="toggleSelect(member)" />
        </template>
      </el-table-column> -->
      <!-- 选择框 -->
      <member-selector-item
        width="25" @toggleSelect="e=>toggleSelect(e)" :selected="selected"
        scopeContent="recipientEmail" itemType="checkbox" :showTooltip="false" />

      <!-- 非虚拟账号的账号名/邮箱 -->
      <member-selector-item v-if="tabValue !== '虚拟ID'" :label="$t('workspace.email_or_id')"
        scopeContent="mail" itemType="user" align='left' />
      <!-- 虚拟账号的账号名+邮箱 -->
      <member-selector-item v-else :label="$t('workspace.email_or_id')"
        scopeContent="account" itemType="user"
        align='left' />
      <!-- 虚拟账号邮箱接收者 -->
      <member-selector-item v-if="tabValue === '虚拟ID'" :label="$t('workspace.recipient_email')"
        width="150" scopeContent="recipientEmail" />
      <!-- 用户名 -->
      <member-selector-item v-if="tabValue === '公司成员'" :label="$t('workspace.username')"
        width="120" scopeContent="name" />
      <member-selector-item v-if="tabValue === '通讯录'" :label="$t('workspace.username')"
        width="120" scopeContent="userName" />
      <!-- 所属部门 -->
      <member-selector-item v-if="tabValue === '公司成员'" :label="$t('workspace.department')"
        width="230" scopeContent="department" />
      <!-- 公司名称 -->
      <member-selector-item v-else-if="tabValue === '通讯录'" :label="$t('workspace.company_name')"
        width="230" scopeContent="companyName" />
      <!-- 公司名称 -->
      <member-selector-item v-if="tabValue === '虚拟ID'" :label="$t('workspace.workspace')"
        width="120" scopeContent="workspaceName" />
      <!-- 截止过期日期 -->
      <member-selector-item v-if="tabValue === '虚拟ID'" :label="$t('workspace.expires_on')"
        width="120" scopeContent="overtime" :extensionText="$t('workspace.days')"
        :showTooltip="false" />
    </el-table>

    <div slot="footer" class="button-group">
      <div></div>
      <div>
        <el-button
          round
          class="btn-cancel"
          @click="$emit('cancel')">
          {{ $t('public.dialog.cancel') }}
        </el-button>
        <el-button
          round
          :disabled="!selected.length"
          type="primary"
          @click="$act(handleOk)">
          {{ $t('public.dialog.confirm') }}</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import axios from '@/utils/axios';

import { WorkspaceOrm } from '@/models';
import { getContextUser } from '../../../../loaders/context';
import { callbackWhenLastElementShownNotCompositionVersion } from '../../../../utils/composables/use-callback-when-last-element-shown';
import MemberSelectorItem from './MemberSelectorItem';

const urls = [
  '/authcenter/user/list/page',
  '/authcenter/contacts/list/page',
  '/authcenter/virtual/list',
];
export default {
  name: 'MemberSelector',
  components: {
    MemberSelectorItem,
  },
  props: {
    groupAppId: String,
    selected: {
      default() { return []; },
    },
    membersHandler: {
      type: Function,
      async default(list) {
        if (!list.length) return list;
        if (this.tabValues[2] === this.tabValue) {
          const isVirtualAccountInMeeting = (await axios.post('/meeting/member/virtual/exists', {
            userIds: list.map(member => member.id),
          })).data;
          Object.entries(isVirtualAccountInMeeting).filter(([_, value]) => value).forEach(
            ([id]) => { list.find(i => i.id === id).enable = false; },
          );
        }
        return list;
      },
    },
    tabLabels: {
      type: Array,
      default() {
        return [
          i18n.t('workspace.internal_contact'),
          i18n.t('workspace.external_contack'),
          i18n.t('workspace.workspace_pass'),
        ];
      },
    },
  },
  computed: {
    workspace() {
      return WorkspaceOrm.find(this.groupAppId);
    },
    user() {
      return getContextUser();
    },
  },
  data() {
    return {
      tabValue: '公司成员',
      tabValues: ['公司成员', '通讯录', '虚拟ID'],
      members: [],

      pageNumber: 1,
      pageSize: 10,
      pages: 0,
    };
  },
  created() {
    this.$watch(() => {
      return [this.tabValue];
    }, () => {
      this.reloadMembers();
    }, {
      immediate: true,
    });

    callbackWhenLastElementShownNotCompositionVersion.call(this, 'members', async () => {
      // console.log(this.pages, this.pageNumber, 'callback invoked.');
      if (this.pages < this.pageNumber + 1) return;
      const index = this.tabValues.indexOf(this.tabValue);
      const url = urls[index];
      this.loadMore(url, ++this.pageNumber);
    }, '.member-item');
  },
  methods: {
    judgeMemberDisabled(member) {
      const currentUser = this.user;
      return (!member.enable || currentUser.id === member.id);
    },
    handleOk() {
      if (!this.selected.length) {
        throw new this.$Error(i18n.t('workspace.select_user_empty_tip'));
      }
      this.$emit('confirm', this.selected);
    },
    // 选择或取消选中该对象，并在选中的列表中增加后删除
    toggleSelect(target) {
      // eslint-disable-next-line
      this.selected.includes(target)
        ? this.selected.splice(this.selected.indexOf(target), 1)
        : this.selected.push(target);
    },
    clearSelected() {
      this.selected.splice(0, this.selected.length + 1);
    },
    isSameUser(a, b) {
      return a.id === b.id;
    },
    async reloadMembers() {
      this.clearSelected();
      this.members = null;
      const index = this.tabValues.indexOf(this.tabValue);
      const res = (await axios({
        url: urls[index],
        params: {
          pageNumber: 1,
          pageSize: this.pageSize,
          keyword: '',
        },
        headers: { groupAppId: this.groupAppId },
      })).data;
      this.pages = res.pages;
      this.pageNumber = 1;
      this.members = await this.membersHandler.call(this, res.records);
      this.selected.splice(0, this.selected.length + 1);
      // this.members.forEach((member) => {
      //   const i = this.selected.findIndex((item) => this.isSameUser(member, item));
      //   if (i !== -1) {
      //     this.selected.splice(i, 1, member);
      //   }
      // });
    },
    async loadMore(url, pageNumber, pageSize = this.pageSize) {
      const res = (await axios({
        url,
        params: {
          pageNumber,
          pageSize,
          keyword: '',
        },
        headers: { groupAppId: this.groupAppId },
      })).data?.records;
      this.members.push(...(await this.membersHandler.call(this, res)));
    },
  },
};
</script>


