<template>
  <div class="tree-wrapper" :class="{ invisible: !visible}">

    <scrollable x y class="scrollable">
      <tree
        v-if="data.children.length"
        :key="data.children[0].id"
        :expandable="isCatalogExpandable"
        :data="data"
        :load="getChildren"
        :opened="opened">
        <template v-slot="{ data }">
          <div class="tree-item"
            @click="handleClick(data)"
            :class="{
            selected: currentCatalog === data,
          }"
            tabindex="0">
            <span class="tree-title">
              {{ appType2Name[data.appType] || data.name }}
            </span>
            <span class="tree-count" v-if="counts[data.id]">{{
              counts[data.id]
            }}</span>
          </div>

        </template>
      </tree>
    </scrollable>
  </div>
</template>

<script>
import {
  defineComponent, ref, watch, set, reactive,
} from '@vue/composition-api';
import Tree from '@/components/Tree.vue';
import { appType2Name } from '@/views/file-list/components/catalog/app-type-to-name';
import axios from '@/utils/axios';
import Scrollable from '@/components/Scrollable.vue';
import sliceCaller from '_/slice-caller';
import Vue from 'vue';


const CatalogTree = defineComponent({
  name: 'CatalogTree',
  components: {
    Tree,
    Scrollable,
  },
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    paths: {
      type: Array,
      required: true,
      default: () => [],
    },
    getCounts: {
      type: Function,
      required: false,
    },
  },
  model: {
    event: 'select',
    prop: 'catalog',
  },
  setup(props, { emit }) {
    const _cacheCatalogs = {};
    const currentCatalog = ref();
    const counts = ref({});
    function cache(map, _item) {
      let _cache = map[_item.id];
      if (_cache) {
        Object.assign(_cache, _item);
      } else {
        map[_item.id] = _item;
        _cache = _item;
      }
      return _cache;
    }

    /**
 * @param { [string][] } argsList
 */
    const getCounts = async (argsList) => {
      const ids = argsList.map(args => args[0]);
      const cacheMap = props.getCounts ? await props.getCounts(ids)
        : Object.fromEntries(ids.map(id => ([id, null])));
      return ids.map(item => cacheMap[item]);
    };
    const _getCount = sliceCaller(getCounts, 500);
    const getCount = (item) => {
      _getCount(item.id).then(count => {
        counts.value = { ...counts.value, [item.id]: count };
      }, () => { });
    };
    async function selectCatalog(item) {
      emit('select', item);
    }

    function handleClick(item) {
      selectCatalog(item);
    }

    async function fetchChildCatalog(item) {
      let children = (await axios.get('bff-center/cloud-disk/list/getRenderMaterialChildCatalogBy', {
        params: {
          catalogId: item.id,
        },
        headers: {
          groupAppId: item.workspaceId ?? '',
        },
      })).data;
      if (children) {
        children = children.map((_item) => {
          _item.entryAppType = item.entryAppType;
          _item.parentFolderId = _item.parentFolderId || item.id;
          _item.workspaceId = item.workspaceId;
          _item = cache(_cacheCatalogs, _item);
          getCount(_item);

          return _item;
        });

        if (item.children) {
          item.children.splice(0, item.children.length, ...children);
          ({ children } = item);
        }
      }
      item.children = children;
      item.folderCount = children.length;
      return item.children;
    }

    function isCatalogExpandable(item) {
      return item.folderCount;
    }

    async function getChildren(item) {
      item.folderCount = Infinity;
      let { children } = item;
      if (!children) {
        if (isCatalogExpandable(item)) {
          children = await fetchChildCatalog(item);
        } else {
          item.children = [];
        }
      }
      return item.children;
    }


    const data = ref({ id: `${Date.now()}`, children: [] });
    const opened = ref([data.value]);

    watch(() => props.paths, (propsPaths) => {
      const paths = [];
      propsPaths.forEach((item, index) => {
        const isFirst = index === 0;
        const isLast = index === props.paths.length - 1;
        item = { ...item };
        item = cache(_cacheCatalogs, item);
        console.log(item);
        getCount(item);
        item.children = item.children ?? undefined;
        paths[index] = item;
        if (!isFirst) {
          const parent = paths[index - 1];
          parent.children = parent.children || [];
          if (!parent.children.includes(item)) {
            parent.children.push(item);
          }
        }
        if (!isLast) {
          getChildren(item);
        }
      });
      if (paths.length) {
        const first = paths[0];
        if (!data.value.children[0]) {
          data.value.children.push(first);
        } else if (first !== data.value.children[0]) {
          data.value.children.splice(0, 1, first);
        }
        currentCatalog.value = paths[paths.length - 1];
      } else {
        // data.value.children.splice(0, data.value.children.length);
      }
      paths.forEach((item) => {
        if (!opened.value.includes(item)) {
          opened.value.push(item);
        }
      });
    }, {
      immediate: true,
    });

    return {
      counts,
      refresh() {
        opened.value.forEach(item => {
          getCount(item);
          if (item !== data.value) {
            fetchChildCatalog(item);
          } else {
            item.children.forEach(getCount);
          }
        });
      },
      data,
      opened,
      isCatalogExpandable,
      getChildren,
      currentCatalog,
      selectCatalog,
      handleClick,
      appType2Name,
    };
  },
});

export default CatalogTree;
</script>

