import Setting, { Key2ValueType } from '@/models/Setting';


function getSetting
  <K extends keyof Key2ValueType>(key: K): NonNullable<Key2ValueType[K]> | undefined;
function getSetting
  <K extends keyof Key2ValueType,
  T extends Key2ValueType[K]>(key: K, defaults ?: T): T | Key2ValueType[K];
function getSetting
  <K extends keyof Key2ValueType, T extends Key2ValueType[K]>(key: K, defaults ?: T) {
  return Setting.get(key)?.value ?? defaults;
}

export { getSetting };

export function setSetting<K extends keyof Key2ValueType>(key: K, value ?: any) {
  Setting.insertOrUpdate({
    data: {
      key,
      value,
    },
  });
}
