import {
  watch, WatchStopHandle, Ref, getCurrentInstance, onUnmounted, onMounted, ref,
} from '@vue/composition-api';
import keys from '_/keys';
import ResizeObserver from 'resize-observer-polyfill';

/** 配置参数，支持多个类型 */
export interface SizeParams {
  [key: string]: {
    /** 默认宽度 */
    defaultWidth: number,
    /** 自适应后比例 */
    scale: number,
    /** 计算后宽度 */
    widthByCalc?: number
  }
}

/** 自适应参数 */
interface AutoFixParams {
  /** 监听目标dom */
  target: string | Element,
  /** 防抖时间长度 */
  awaitTime?: number
  /** 默认容器宽度 */
  defaultContentRect?: number
  /** 配置参数，支持多个类型 */
  sizeParams: SizeParams
  /** 单行增量 */
  itemIncrement?: Ref<number>
  /** 偏移量，目标容器实际宽度与理论宽度不一致时使用 */
  offset?: number
  /** 边距，指容器内item的间距 */
  margin?: number
  /** 计算后回调 */
  callBack?: (sizeParams: SizeParams, contentRect: number)=> any
}

/**
 * 指定容器，使容器内item自适应等分宽度，不会自动修改item配置
 *
 * @export Function
 * @param {AutoFixParams} 参数
 * @return {*} 返回计算后的item的配置参数
 */
export function useAutoFix(params: AutoFixParams) {
  const {
    target,
    callBack,
    defaultContentRect = 1220,
    awaitTime = 200,
    sizeParams,
    itemIncrement = ref(0),
    offset = 0,
    margin = 0,
  } = params;
  const cloneSizeParams: SizeParams = JSON.parse(JSON.stringify(sizeParams));
  const currentVm = getCurrentInstance();
  // 监听者
  let panelObserver: ResizeObserver | null = null;
  // 容器宽度
  let contentRect = defaultContentRect;
  // dom timer 防抖
  let timer: number | null = 0;
  // 监听单行增量变化
  let itemIncrementWatcher: WatchStopHandle | null = null;

  let targetEl!: Element | null | undefined;

  /** 计算itemSize */
  const itemSizeCalc = () => {
    const rectWidth = contentRect - offset;
    keys(cloneSizeParams).forEach(item => {
      const { defaultWidth } = cloneSizeParams[item];
      let num = Math.floor(rectWidth / defaultWidth) + itemIncrement.value;
      if (num <= 0) num = 1;
      const width = (rectWidth - num * margin) / num;
      const scale = width / defaultWidth;
      cloneSizeParams[item].scale = scale;
      cloneSizeParams[item].widthByCalc = width;
    });
    callBack && callBack(cloneSizeParams, contentRect);
  };

  onMounted(() => {
    if (typeof target === 'string') {
      targetEl = currentVm?.$el.querySelector(target);
    } else {
      targetEl = target;
    }
    // 监听单行增量变化
    targetEl
    && itemIncrement
    && (itemIncrementWatcher = watch(() => itemIncrement.value, () => {
      itemSizeCalc();
    }, {
      immediate: true,
    }));

    // 监听列表resize
    panelObserver = new ResizeObserver(entries => {
      timer && clearTimeout(timer);
      timer = setTimeout(() => {
        contentRect = entries[0].contentRect.width;
        itemSizeCalc();
      }, awaitTime);
    });
    targetEl && panelObserver.observe(targetEl);
  });


  onUnmounted(() => {
    itemIncrementWatcher && itemIncrementWatcher();
    panelObserver && panelObserver.disconnect();
    timer && clearTimeout(timer);
  });

  return cloneSizeParams;
}
