<template>
  <section class="member-add-container">
    <el-input :value="keyword" class='user-searcher' size="small"
      prefix-icon="el-icon-search" :placeholder="$t('workspace.search')"
      @input="val=>keyword=val"></el-input>

    <template v-if="keyword.trim() === ''">
      <MemberSelector @confirm="v => $emit('confirm', v)" v-bind="$attrs"
        @cancel="$emit('cancel')" :groupAppId='groupAppId' />
    </template>
    <template v-else>
      <MemberSearcher :keyword="keyword" :groupAppId="groupAppId" v-bind="$attrs"
        @confirm="v => $emit('confirm', v)" @cancel="$emit('cancel')" />
    </template>
  </section>
</template>

<script>
import MemberSelector from './MemberSelector';
import MemberSearcher from './MemberSearcher';

export default {
  props: {
    groupAppId: String,
  },
  components: {
    MemberSelector,
    MemberSearcher,
  },
  data() {
    return {
      keyword: '',
    };
  },
};
</script>

