

import {
  defineComponent, reactive, ref, watch,
} from '@vue/composition-api';
import DictSelectorNew from '@/components/dictionary/DictionarySelectorNew.vue';
import validateForm from '@/utils/validators/validate-form';
import { ElForm } from 'element-ui/types/form';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';

const INNER_VALUE = Symbol('inner value');

type PriceData = {
  startPrice: number | undefined,
  endPrice: number | undefined,
  unit: number | undefined,
  [INNER_VALUE]?: boolean
};

type InnerPriceData = {
  startPrice: string,
  endPrice: string,
  unit: number | undefined,
};

const rNumber = /^[\d.]+$/;
function isValidNumber(str: string) {
  if (!str) return;
  if (!rNumber.test(str) || str.split('').filter(item => item === '.').length > 1 || str.endsWith('.')) {
    throw new Error('请输入合法的数字');
  }
}

function data2InnerData(data: PriceData) {
  return {
    startPrice: `${data.startPrice ?? ''}`,
    endPrice: `${data.endPrice ?? ''}`,
    unit: data.unit,
  };
}
function innerData2Data(data: InnerPriceData) {
  const obj: any = {
    unit: data.unit,
    [INNER_VALUE]: true,
  };
  if (data.startPrice) {
    obj.startPrice = +data.startPrice;
  }
  if (data.endPrice) {
    obj.endPrice = +data.endPrice;
  }
  return obj;
}

const rules = {
  startPrice: validator2ElemRule(isValidNumber),
  endPrice: validator2ElemRule(isValidNumber),
};

const PriceForm = defineComponent({
  setup(props, { emit }) {
    const value_ = reactive<InnerPriceData>(data2InnerData(props.value));
    const form = ref<ElForm>();
    const submit = async () => {
      if (!form.value) return;
      if (!props.node.isAllRequired && !value_.startPrice && !value_.endPrice) throw new Error('价格区间不能为空');
      if (props.node.isAllRequired && (!value_.startPrice || !value_.endPrice)) throw new Error('价格区间不能为空');
      await validateForm(form.value);
      emit('change', {
        ...innerData2Data(value_),
        [INNER_VALUE]: true,
      });
    };
    watch(() => props.value, (_value: PriceData) => {
      if (!_value[INNER_VALUE]) {
        Object.assign(value_, data2InnerData(_value));
      }
    });
    return {
      submit,
      form,
      value_,
    };
  },
  props: {
    value: {
      type: Object as () => PriceData,
      default: () => ({
        startPrice: undefined,
        endPrice: undefined,
        unit: 1,
        [INNER_VALUE]: true,
      }),
    },
    node: {
      default: {},
      type: Object as () => {
        [key: string]: any
      },
    },
  },
  render() {
    const self = this as InstanceType<typeof PriceForm>;
    const { value_, submit } = self;
    return <div class="price-form host">
      <el-form label-width="2em" ref="form" props={{ model: value_, rules }}>
        <el-form-item label="从" size="small" placeholder="请输入价格" prop="startPrice">
          <el-input vModel={value_.startPrice} />
        </el-form-item>
        <el-form-item label="到" size="small" placeholder="请输入价格" prop="endPrice">
          <el-input vModel={value_.endPrice} />
        </el-form-item>
        <el-form-item size="small">
          <DictSelectorNew type="priceUnit" vModel={value_.unit} />
        </el-form-item>
        <el-form-item size="small">
          <el-button round onClick={submit}>确定</el-button>
        </el-form-item>
      </el-form>
    </div>;
  },
});

export default PriceForm;
