<template>
  <div v-if="usersList === null" class="loading-wrapper" v-loading="isLoading">
  </div>
  <div class="member-searcher-container" v-else-if="usersList && usersList.length !== 0"
    v-loading='isLoading'>
    <ul class="scrollable-y member-searcher">
      <li v-for="member of usersList" :key="member.userId">
        <el-checkbox :disabled="!member.enable" :value="selected.includes(member) || !member.enable"
          @change="val =>handleSelectMembers(selected, member, val)"
          class="flex flex-vertical-center"
          style="height: 60px;line-height: 60px;margin-left: 15px;">
          <!-- <div class="flex-center"> -->
          <!-- eslint-disable-next-line -->
          <!-- <img src="https://sdtc-test.oss-cn-shenzhen.aliyuncs.com/userImg/1183934685995208705-683a3b66900cb85bf2166823155de857.png?x-oss-process=image%2Fresize%2Cw_45%2Ch_45%7Cimage%2Fformat%2Cjpg&security-token=CAIS9wF1q6Ft5B2yfSjIr5b7Df%2FQuYp59ZqEQ1%2F2hWRke%2BZJuIPzsjz2IH1Oe3VrAe4esf02mmBX6vgdlqVvRppdAFfJdtBx6ZIPAMQkzWGH6aKP9rUhpMCPOwr6UmzWvqL7Z%2BH%2BU6muGJOEYEzFkSle2KbzcS7YMXWuLZyOj%2BwMDL1VJH7aCwBLH9BLPABvhdYHPH%2FKT5aXPwXtn3DbATgD2GM%2BqxsmtP7gmpbHu0WA3Ayhk7NLnemrfMj4NfsLFYxkTtK40NZxcqf8yyNK43BIjvwr1vMYom%2BZ5o7EWgcBvEnYbPCw6dFzJxN0fbQgHKpJvEpJf3vUSF%2B5GoABMbI%2FjHTdmbEwRNCVPQ4H13PF61iPaM2gzCeSi0ym9cB4yDSnrkdXe3Brb%2BQsOOwa74jwDz3ZfI8bN7R1Xz%2BNocu1qkUBLryXt3nRVRgzY%2BwPFa%2FVsb2RBDzqSUA7N2gUF7Bbkk6RvnppRr8w3tESXHmeMgojl%2BCobxWvxQQTvfo%3D&OSSAccessKeyId=STS.NUNFEdTUXBXoAyGad1wieWFXP&Expires=1581499844&Signature=3slVNRoXa14zEoWtvfIvihUW5ek%3D" alt="">
