          <BaseUser :user="member">
            {{ member.account }}{{ member.name ? `(${member.name})` : '' }}
          </BaseUser>
        </el-checkbox>
      </li>
    </ul>

    <div class="button-group">
      <div></div>
      <div>
        <el-button @click="$emit('cancel')">
          {{ $t('public.dialog.cancel') }}
        </el-button>
        <el-button
          :disabled="!selected.length"
          type="primary"
          @click="$act(handleOk, [selected])">
          {{
            $t('public.dialog.confirm')
          }}</el-button>
      </div>
    </div>

  </div>
  <div v-else class="flex-column flex-center" style="padding-bottom: 130px;">
    <img :src="require('@/assets/img/model/empty-hiddenlist.png')" alt="no-users" />
    <div>
      {{ $t('workspace.cannot_find_user_tip') }}
    </div>
    <el-button @click="$parent.$emit('confirm', 'invite')" class="turn-to-invite">
      {{ $t('workspace.invite_people') }}
    </el-button>
  </div>
</template>

<script>
import { debounce } from 'throttle-debounce';
import BaseUser from '@/components/base/User';
import {
  searchUsersToAdd,
} from '@/api/account';
import axios from '@/utils/axios';

export default {
  name: 'MemberSearcher',
  components: {
    BaseUser,
  },
  props: {
    membersHandler: {
      type: Function,
      async default(list) {
        if (!list.length) return list;
        const virtualAccountIds = list.filter(mem => +mem.roleId === 3).map(mem => mem.id);
        if (virtualAccountIds.length) {
          virtualAccountIds.forEach((id) => { list.find(i => i.id === id).enable = true; });
          const isVirtualAccountInMeeting = (await axios.post('/meeting/member/virtual/exists', {
            userIds: virtualAccountIds,
          })).data;
          Object.entries(isVirtualAccountInMeeting).filter(([_, value]) => value).forEach(
            ([id]) => { list.find(i => i.id === id).enable = false; },
          );
        }
        return list;
      },
    },
    groupAppId: String,
    keyword: String,
    selected: {
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      test: false,
      usersList: null,
      isLoading: false,
    };
  },
  methods: {
    async searchUsers(keyword, groupAppId) {
      this.isLoading = true;
      const res = await searchUsersToAdd(keyword, groupAppId);
      this.usersList = await this.membersHandler.call(this, res.data);
      this.isLoading = false;
    },
    async handleSelectMembers(selected, memberId, val) {
      // eslint-disable-next-line no-unused-expressions
      val
        ? selected.push(memberId)
        : selected.splice(selected.indexOf(memberId), 1);
    },
    handleOk(selected) {
      this.$emit('confirm', selected);
    },
  },
  created() {
    this.debounceSearchUsers = debounce(300, this.searchUsers);
  },
  mounted() {
    this.debounceSearchUsers(this.keyword, this.groupAppId);
  },
  watch: {
    keyword(newVal, oldVal) {
      if (oldVal !== newVal && newVal.trim() !== '') {
        this.debounceSearchUsers(newVal, this.groupAppId);
      }
    },
  },
};
</script>

