<template>
  <el-popover v-model="popoverVisible" :disabled="!workspaceMax" popper-class="workspace-unavailable-tip" width="208">
    <!-- 当workspace空间不足时，显示提示框 -->
    <div>
      <p class="text-align-left">
        {{ $t("profile.workspace.no_more_workspaces") }}
      </p>
      <div class="text-align-right">
        <el-button size="small" type="text" class="button-got-it" @click="hidePopover"
          >{{ $t("profile.workspace.got_it") }}!</el-button
        >
      </div>
    </div>

    <div slot="reference">
      <el-button
        class="button-create-workspace"
        type="text"
        ref="addButton"
        :disabled="workspaceAddDisable || workspaceMax"
        @click="handleClickAddWorkspace"
      >
        <div class="button-content-box">
          <i class="iconfont iconaddmembers" />
          <span>{{ $t("profile.workspace.new_workspace") }}</span>
          <span>{{ usedWorkspace }}/{{ totalWorkspace }}</span>
        </div>
      </el-button>
    </div>
  </el-popover>
  <!-- eslint-disable-next-line max-len -->
  <!-- @click="handleClickAddWorkspace" -->
  <!-- <div class="workspace-add">
    <el-popover :disabled="workspaceMax" placement="bottom"
      popper-class="workspace-max-tooltip" trigger="click"
      width="256" v-model="popoverVisible">
      <div>
        You have no more available workspaces.
        Please purchase more workspaces or delete an existing workspace.
        <el-button size="small" @click="popoverVisible = false"
          class="popover-class">Got it!</el-button>
      </div>
      <div slot="reference">
        <button class="add-button flex-space-between el-button el-button--default"
          :class="{ 'is-disabled': workspaceAddDisable || workspaceMax }"
          :disabled="workspaceAddDisable || workspaceMax" ref="addButton">
          <div class="flex-space-between" style="width: 100%;">
            <span class="iconfont iconaddmembers"></span>&nbsp;
            <span class="workspace-create-tag">New Workspace</span>&nbsp;
            <span>{{usedWorkspace}}/{{totalWorkspace}}</span>
          </div>
        </button>
      </div>
    </el-popover>
  </div> -->
</template>

<script>
import i18n from 'i18next';
import judgeInput from '@/utils/validators/checkers/valid-name';
import MemberManagement from '@/views/file-list/components/workspace/MemberManagement';
import wsMixin from '@/views/file-list/components/workspace/mixin';
import { WorkspaceOrm } from '@/models';
import { addWorkspace } from '@/loaders/workspace';
import { getContextUser, updateWorkspaceRolesAndPermissions } from '../../../loaders/context';
import { addUsedWorkspace } from '../../../loaders/user';

export default {
  name: 'ButtonCreateWorkspace',
  mixins: [wsMixin],
  data() {
    return {
      popoverVisible: false,
    };
  },
  computed: {
    isVirtualAccount() {
      return this.user?.roleId === '3';
    },
    user() {
      return getContextUser();
    },
    usedWorkspace() {
      return this.isVirtualAccount
        ? 0
        : this.user?.resourceInfo?.usedWorkspace;
    },
    totalWorkspace() {
      return this.isVirtualAccount
        ? 0
        : this.user?.resourceInfo?.totalWorkspace;
    },
    workspaceMax() {
      return this.usedWorkspace === this.totalWorkspace;
    },
    workspaceAddDisable() {
      return !this.$can('system', 'OP0144');
    },
  },
  mounted() {
    this.$root.$on('show-dialog-create-workspace', this.handleClickAddWorkspace);
  },
  methods: {
    hidePopover() {
      this.popoverVisible = false;
    },
    // 处理点击新增协同空间操作
    async handleClickAddWorkspace() {
      // 处理已满的情况
      if (this.workspaceMax) {
        this.popoverVisible = true;
        return;
      }
      try {
        // eslint-disable-next-line
        const workspaceName = (await this.$prompt('', i18n.t('profile.workspace.workspace_name'), {
          confirmButtonText: i18n.t('public.dialog.confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          inputValue: i18n.t('workspace.default_add_value'),
          inputPlaceholder: i18n.t('profile.workspace.placeholder'),
          closeOnClickModal: false,
          inputValidator: input => judgeInput(input, { min: 1, max: 64 }),
        })).value;
        const workspace = await addWorkspace(workspaceName);
        addUsedWorkspace(1);

        await updateWorkspaceRolesAndPermissions(workspace.groupAppId);

        try {
          await this.$Dialog.dialogify({
            is: MemberManagement,
            props: {
              groupAppId: workspace.groupAppId,
              title: workspaceName,
            },
          }, {
            props: {
              title: workspaceName,
              titleAlign: 'left',
              width: '700px',
            },
          });
        } catch (e) {
          console.warn(e);
        } finally {
          // this.$router.push({
          //   name: this.$route.name.startsWith('Qt') ? 'QtFileList' : 'FileList',
          //   query: {
          //     appType: 13,
          //     relateId: workspace.groupAppId,
          //   },
          // });
          this.enterWorkspace(workspace.groupAppId);
        }
        // eslint-disable-next-line no-empty
      } catch (e) { } finally {
        // this.$refs.addButton.blur();
      }
    },
  },
};
</script>

