<template>
  <nav class="flex-space-between workspace-top-bar-container">
    <!-- 左侧显示协同空间名称 -->
    <h3 style="font-size: 20px;">
      {{ currentWorkspaceInfo.name || '' }}
    </h3>

    <!-- 右边显示协同空间相关操作，如群聊、操作记录、群设置等 -->
    <div class="function-selector flex-center">
      <!-- 群聊 -->
      <el-popover
        class="show-im-popover"
        popper-class="im-introduce"
        placement="bottom-end"
        width="320"
        v-model="showImPoppver"
        trigger="manual">
        <p style="padding: 8px 8px 0 8px;text-align: left;">
          {{ $t('file-list.im.welcome_text', {
            currentWorkspaceInfo,
          }) }}
        </p>
        <div style="text-align: right">
          <el-button
            class="popover-button"
            type="primary"
            @click.native="closeImTips"
            size="mini">
            {{ $t('file-list.im.get_it') }}
            </el-button>
        </div>
        <span slot="reference" class="iconfont-wrapper flex-center" @click="$emit('openIm')">
          <i class="iconfont iconxiaoxi iconfont-target"
            :class="{ unread: unread }">
          </i>
        </span>
      </el-popover>
      <!-- <el-popover>
        <p>123464534</p>
        <span>123123123</span>
      </el-popover> -->
      <!-- 操作记录 -->
      <span
        :data-no-permission="!$can('workspace', 'OP0027')"
        class="iconfont-wrapper flex-center"
        @click="$can('workspace', 'OP0027') && showOperationRecord()">
        <i class="iconfont iconjilu iconfont-target"
          :class="{ unread: operationUnread }">
        </i>
      </span>
      <!-- 群设置 -->
      <span
        :data-no-permission="!$can('workspace', 'OP0027')"
        class="iconfont-wrapper flex-center"
        @click="$can('workspace', 'OP0027') && handleClickMore()">
        <i class="iconfont icongengduo iconfont-target"></i>
      </span>
    </div>
  </nav>
</template>

<script>
import i18n from 'i18next';
import SideSlider from '@/components/SideSlider';
import { getUnread as getWorkspaceUnread, removeUnread as removeWorkspaceUnread } from '@/loaders/workspace-msg';
import OperationRecord from '../operation-record/index';
import { getUnread } from '../../../../loaders/im';
import WorkspaceGroupSettings from './WorkspaceGroupSettings';
import wsMixin from './mixin';


export default {
  mixins: [wsMixin],
  props: {
    showImPoppver: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    /** @return { any } */
    currentWorkspaceInfo() {
      return this.currentWorkspace || {};
    },
    /** @return { boolean } */
    unread() {
      return getUnread(this.currentWorkspaceId);
    },
    /** @return { boolean } */
    operationUnread() {
      return getWorkspaceUnread(this.currentWorkspaceId);
    },
  },
  methods: {
    async handleClickMore() {
      await this.$Dialog.dialogify({
        is: WorkspaceGroupSettings,
        props: {
          currentWorkspace: this.currentWorkspace,
        },
      }, {
        is: SideSlider,
        props: {
          width: 438,
        },
      });
    },

    async showOperationRecord() {
      removeWorkspaceUnread(this.currentWorkspaceId);
      // this.operationRecordShown = true;
      await this.$Dialog.dialogify({
        is: OperationRecord,
        props: {
          workspaceId: this.currentWorkspaceId,
        },
      }, {
        is: SideSlider,
        props: {
          title: i18n.t('file-list.operation_record'),
          width: 438,
        },
      });
    },

    closeImTips() {
      this.$emit('closeImTips');
    },
  },
};
</script>

