<template>
  <div class="flex-center icon-wrapper" :style="`width:${fontSize}px;height:${fontSize}px;`">
    <i :class="[iconName, '']" :style="`font-size:${iconInnerSize};`"></i>
  </div>
</template>

<script>
export default {
  props: {
    fontSize: {
      default: 16,
      type: Number,
    },
    iconName: {
      default: 'iconfont iconbiaoqian-owner',
      type: String,
    },
  },
  computed: {
    iconInnerSize() {
      return `${this.fontSize - 2}px`;
    },
  },
};
</script>

<style lang="scss" scoped>
.icon-wrapper {
  background-color: #fff;
  border-radius: 50%;
}
</style>
