import i18n from 'i18next';
import { WorkspaceRoleOrm, WorkspaceOrm, WorkspaceUserRoleOrm } from '@/models';
import isQt from '@/utils/is-qt';


export default {
  props: {
    relateId: {
      type: String,
    },
  },
  computed: {
    currentWorkspaceId() {
      // eslint-disable-next-line
      return this.relateId || (this.$route.query.relateId
        ? this.$route.query.relateId
        : null);
    },
    currentWorkspace() {
      return this.currentWorkspaceId
        ? WorkspaceOrm.find(this.currentWorkspaceId)
        : null;
    },
  },
  methods: {
    async enterFirstWorkspace() {
      const firstWorkspace = WorkspaceOrm.query().get()[0];
      await this.enterWorkspaceByReplaceURL(firstWorkspace.groupAppId);
    },
    async enterWorkspaceByReplaceURL(groupAppId) {
      await this.$router.replace({
        query: {
          appType: 13,
          relateId: groupAppId,
        },
      });
    },
    async enterWorkspace(groupAppId, ws) {
      await this.$router.push({
        name: isQt() ? 'QtFileList' : 'FileList',
        query: {
          appType: 13,
          workspaceBusinessType: ws?.businessType,
          relateId: groupAppId,
        },
      });
    },
  },
};
export const userAbout = {
  data() {
    const roleList = [
      i18n.t('workspace.unknown_role'),
      i18n.t('workspace.owner'),
      i18n.t('workspace.admin'),
      i18n.t('workspace.member'),
      i18n.t('workspace.guest'),
      i18n.t('workspace.workspace_pass'),
    ];
    return {
      roleId2Role: {
        4: roleList[1],
        5: roleList[2],
        6: roleList[3],
        7: roleList[4],
        8: roleList[5],
      },
      OWNER: roleList[1],
      ADMIN: roleList[2],
      MEMBER: roleList[3],
      GUEST: roleList[4],
      WORKSPACEPASS: roleList[5],
    };
  },
  computed: {
    selfRoleId() {
      return WorkspaceUserRoleOrm.find(this.groupAppId).roleId;
    },
    roles() {
      return WorkspaceRoleOrm.all();
    },
    ownerUsableRole() {
      // eslint-disable-next-line
      const arr = this.roles.filter(item => item.roleId != 4 && item.roleId != 8);
      return arr;
    },
    adminUsableRole() {
      const list = this.roles.filter(item => +item.roleId === 6 || +item.roleId === 7);
      return list;
    },
  },
  methods: {
    roleTypeC2E(chinese) {
      if (chinese) {
        switch (chinese) {
          case '空间创建者':
            return 'Owner';
          case '下载者':
            return 'Admin';
          case '编辑者':
            return 'Member';
          case '查看者':
            return 'Guest';
          case '参与者':
            return 'Invited';
          default: return 'Undefined Role';
        }
      }
      return chinese;
    },
  },
};
