<template>
  <el-table-column
    :prop="prop" :label="label" :width="width" :show-overflow-tooltip="showTooltip"
    :align="align">
    <template slot-scope="{ row: member }">
      <!-- <el-tooltip effect="dark" :disabled="!showTooltip" :content="member[scopeContent]"> -->
      <span :disabled="judgeMemberDisabled(member)">
        <template v-if="itemType === 'text'">
          {{ member[scopeContent] }} {{ extensionText }}
        </template>
        <template v-else-if="itemType === 'user'">
          <BaseUser :user="member">{{ member[scopeContent] }}</BaseUser>
        </template>
        <template v-else-if="itemType === 'checkbox'">
          <el-checkbox
            :disabled="judgeMemberDisabled(member)"
            :checked="selected ? (
              selected.includes(member) ||
              judgeMemberDisabled(member)
            ) : false"
            @change="changeValue(member)" >
            <!-- {{ judgeMemberDisabled(member) }}
            {{ selected.includes(member) }}
            {{ judgeMemberDisabled(member) || selected.includes(member) }} -->
          </el-checkbox>
        </template>
      </span>
      <!-- </el-tooltip> -->
    </template>
  </el-table-column>
</template>

<script>
import BaseUser from '@/components/base/User';
import { getContextUser } from '../../../../loaders/context';

export default {
  components: {
    BaseUser,
  },
  props: {
    prop: String,
    label: {
      type: String,
      default: '',
    },
    extensionText: String,
    width: {
      type: [String, Number],
      default: 'auto',
    },
    scopeContent: [String, Array],
    disabled: Boolean,
    itemType: {
      default: 'text',
    },
    showTooltip: {
      type: Boolean,
      default: true,
    },
    selected: Array,
    align: {
      default: 'center',
      type: String,
    },
  },
  // computed: {
  //   content() {

  //   }
  // },
  methods: {
    judgeMemberDisabled(member) {
      const currentUser = getContextUser();
      // eslint-disable-next-line max-len
      // const a = !member.enable || currentUser.id === member.id;
      // const b = this.selected?.includes(member);
      // console.log(a || b, member.userName);
      return (!member.enable || currentUser.id === member.id);
    },
    changeValue(v) {
      this.$emit('toggleSelect', v);
    },
  },
};
</script>

<style>
span[disabled] {
  color: #bbb;
}
</style>
